import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { LoginPage } from './Login';
import { RegisterPage } from './Register';
import { LoginModel, loginStore, LoginType, RegisterType } from '../../stores/LoginStore';
import './LoginModal.scss';
import { ForgetPasswordPage } from './ForgetPassword';
import { LoginIndexPage } from './LoginIndex';

interface LoginModalProps {
    visible: boolean;
    cancle: Function;
}

export const LoginRegisterModal = observer((props: LoginModalProps) => {
    var title;
    if (loginStore.LoginModel === LoginModel.login) {
        title = <div className="login-tabs">
            <div className={loginStore.loginType == LoginType.phone ? "item active" : "item"} onClick={() => loginStore.loginType = LoginType.phone}>手机</div>
            <div className={loginStore.loginType == LoginType.email ? "item active" : "item"} onClick={() => loginStore.loginType = LoginType.email}>邮箱</div>
        </div>
        title = "登录";
    } else if (loginStore.LoginModel === LoginModel.register || loginStore.LoginModel === LoginModel.registerPop) {
        title = <div className="register-tabs">
            <div className={loginStore.registerType == RegisterType.phone ? "item active" : "item"} onClick={() => loginStore.registerType = RegisterType.phone}>手机</div>
            <div className={loginStore.registerType == RegisterType.email ? "item active" : "item"} onClick={() => loginStore.registerType = RegisterType.email}>邮箱</div>
        </div>
        title = "注册";
    } else {
        title = "忘记密码";
    }

    return (
            <Modal title={''} visible={props.visible}
                footer={null}
                centered={true}
                className="login-modal"
                onCancel={() => { props.cancle() }}
            >
                <LoginIndexPage visible={props.visible}  close={props.cancle}/>
            </Modal>
    );
})













