import { FindType, LoginModel, loginStore, LoginType, RegisterType } from '../../stores/LoginStore';
import './Login.scss';
import { Icon_logo } from '../../images/index/logo';
import { LoginPage } from './Login';
import { RegisterPage } from './Register';
import { ForgetPasswordPage } from './ForgetPassword';
import { observer } from 'mobx-react';
import { Headers } from '../Header/Header';
import { translate } from '../../components/Text';
import { Icon_logo_gold } from '../../images/index/logoGold';
import Login_Bg from "../../images/main/loginBg.png";
import Register_Bg from "../../images/main/registerBg.png";
import { Img } from '../../components/Image';

interface loginProps {
    close: Function;
    visible: boolean;
}

export const LoginIndexPage = observer((props: loginProps) => {

    var title;
    if (loginStore.LoginModel === LoginModel.login) {
        title = <div className="login-tabs">
            <div className={loginStore.loginType == LoginType.email ? "item active" : "item"} onClick={() => loginStore.loginType = LoginType.email}>
                {translate('login.a')}
                <div className="line"></div>
            </div>
            <div className={loginStore.loginType == LoginType.phone ? "item active" : "item"} onClick={() => loginStore.loginType = LoginType.phone}>{translate('login.b')}
                <div className="line"></div>
            </div>
        </div>
    } else if (loginStore.LoginModel === LoginModel.register) {
        title = <div className="register-tabs">
            <div className={loginStore.registerType == RegisterType.email ? "item active" : "item"} onClick={() => loginStore.registerType = RegisterType.email}>     {translate('login.a1')}
                <div className="line"></div></div>
            <div className={loginStore.registerType == RegisterType.phone ? "item active" : "item"} onClick={() => loginStore.registerType = RegisterType.phone}>     {translate('login.b')}
                <div className="line"></div></div>
        </div>
    } else {
        title = <div className="register-tabs">
            <div className={loginStore.findType == FindType.email ? "item active" : "item"} onClick={() => loginStore.findType = FindType.email}>{translate('login.a1')}
                <div className="line"></div></div>
            <div className={loginStore.findType == FindType.phone ? "item active" : "item"} onClick={() => loginStore.findType = FindType.phone}>{translate('login.b')}
                <div className="line"></div></div>
        </div>
    }


    return (
        <div className="loginPage">
            {loginStore.LoginModel === LoginModel.login && <Img className='loginBg' src={Login_Bg} alt="" />}
            <div className="loginWrapper">
                <div className="title">{
                    loginStore.LoginModel === LoginModel.login ? translate('login.g') :  loginStore.LoginModel === LoginModel.register ?  translate('login.h'):translate('login.i')
                }</div>
                <div className="loginContent">
                    <div className="tabs">{title}</div>
                    {loginStore.LoginModel === LoginModel.login ?
                        <LoginPage visible={props.visible} close={props.close} /> : loginStore.LoginModel === LoginModel.register ?
                            <RegisterPage close={props.close}/> : <ForgetPasswordPage />}
                </div>
            </div>
            {(loginStore.LoginModel ===LoginModel.register || loginStore.LoginModel ===LoginModel.forgetPassword) && <Img className='registerBg' src={Register_Bg} alt="" />}
        </div>)
})