import { Button, DatePicker, Form, Input, Spin } from "antd";
import { CreatorCollectionDetailService, CreatorVideoDeleteService } from "../../services/CreatorVideoService";
import { useCurrentUser, useRequest } from '../../utils/hooks';
import './MyCenter.scss';
import { useHistory } from "react-router";
import { CollectionDataService } from "../../services/CollectionListService";
import { IconEdit } from "../UserCenter/Icons";
import { CreateCollectService } from "../../services/PublishService";
import { translate } from "../../components/Text";
import moment from "moment";
import { observer } from "mobx-react";
import { ChangePassword } from "../Profile/ChangePassword";
import { settingStore } from "../../stores/SettingStore";
import { ChangeAccount } from "../Profile/ChangeAccount";


const saveVideoColletData = (data: any) => Promise.all([
    new CreateCollectService(data).send(),
]);


export const MyCenterPage = observer(() => {
    const user = useCurrentUser();
    const dateFormat = 'YYYY-MM-DD';
    var disabledDate = (current: any) => {
        return current > moment().add(0, 'day');
    };
    const { loading: loadingSave, load: loadSave } = useRequest(saveVideoColletData, { autoLoad: false });
    return (
        <div>
            <div className="myCenterPage">
                <Form
                    name="basic"
                    initialValues={{ remember: true }}
                    autoComplete="off"
                    onFinish={(v) => settingStore.editSetting(v)}
                >
                    <div className="content">
                        <div className="leftContent">
                            <div className="editTitle">
                                <div className="text">{translate('creator.apply.1')}:</div>
                            </div>
                            <div className="editInput">
                                <Form.Item
                                    name="username"
                                >
                                    <Input placeholder={user?.username} />
                                </Form.Item>
                            </div>
                            <div className="editTitle">
                                <div className="text">{translate('creator.apply.11')}:</div>
                            </div>
                            <div className="editInput ">
                                <Form.Item
                                    name="birthday">
                                    <DatePicker disabledDate={disabledDate} placeholder={user?.birthday} defaultValue={user?.birthday ? moment(user?.birthday, dateFormat) : undefined} />
                                </Form.Item>
                            </div>
                            <div className="editTitle">
                                <div className="text">{translate('creator.myCenter.13')}:</div>
                                {!user?.email && <div className="edit" onClick={() => {
                                    settingStore.emailModalVisible = true
                                }}>{translate("mine.k")} {IconEdit}</div>}
                            </div>
                            <div className="editInput">
                                <Form.Item
                                    name="email"
                                >
                                    <Input placeholder={user?.email} disabled={true} />
                                </Form.Item>
                            </div>
                            <div className="editTitle">
                                <div className="text">{translate('creator.myCenter.14')}:</div>
                                {!user?.phone && <div className="edit" onClick={() => {
                                    settingStore.phoneModalVisible = true
                                }}>{translate("mine.k")} {IconEdit}</div>}
                            </div>
                            <div className="editInput">
                                <Form.Item
                                    name="phone"
                                >
                                    <Input placeholder={user?.phone} disabled={true} />
                                </Form.Item>
                            </div>


                        </div>

                        <div className="rightContent">
                            <div className="editTitle">
                                <div className="text">{translate('creator.apply.4')}:</div>
                            </div>
                            <div className="editInput">
                                <Form.Item
                                    name="profile"
                                >
                                    <Input.TextArea placeholder={user?.profile} />
                                </Form.Item>
                            </div>
                        </div>

                    </div>

                    <Form.Item >
                        <Button className="submit" htmlType="submit">
                            {loadingSave && <Spin size="small" />}  <span>{translate('creator.playlist.12')}</span>
                        </Button>
                        <div className="passwordTitle" onClick={() => {
                            settingStore.passwordModalVisible = true;
                        }}>{translate('mine.g')}</div>
                    </Form.Item>

                </Form>

            </div>
            <ChangePassword visible={settingStore.passwordModalVisible} cancle={() => { settingStore.passwordModalVisible = false }} />
            <ChangeAccount visible={settingStore.emailModalVisible || settingStore.phoneModalVisible} isEmail={settingStore.emailModalVisible} cancle={() => {
                settingStore.emailModalVisible = false
                settingStore.phoneModalVisible = false
            }} />
        </div>
    )
})
