import { Ajax } from '../utils/http';

export class StockPatternService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<StockPattern> {
        var response: StockPattern;
        var data = {
            symbol: this.symbol,
        }
        try {
            response = await new Ajax<StockPattern>( '/oliview_market_patterns', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

/*Details*/
export interface Details {
    x: string;
    y: number;
  }
  
  /*Strategy_results*/
  export interface Strategy_results {
    details: Details[];
    type: string;
  }
  
  /*Patterns*/
  export interface Patterns {
    strategy_name: string;
    strategy_name_display: string;
    strategy_name_ch: string;
    begin_date: string;
    end_date: string;
    strategy_results: Strategy_results[];
  }
  
  /*tsModel3*/
  export interface StockPattern {
    support_resistance: number[];
    support_resistance_start_date: string;
    patterns: Patterns[];
  }
  
  