export interface VideoListProps {
  list: CreatorVideoItem[];
  sortMode?: boolean;
  delMode?: boolean;
  trycount?: number;
  price?: string;
  delData: string[];
  payProps?: Partial<StripePayAreaProps>;
  onClick: (item: CreatorVideoItem) => void;
  onEdit?: (item: CreatorVideoItem) => void;
  onDelete?: (item: CreatorVideoItem) => void;
  onSort?: (items: CreatorVideoItem[]) => void;
  onMulDelChange?: (id: string) => void;
}

const VideoListItem: FC<VideoListProps & { item: CreatorVideoItem; isTry: boolean }> = ({ item: it, onSort, delData, onClick, onMulDelChange, isTry, price, sortMode, delMode, onDelete, onEdit }) => {
  const [refresh, setRefresh] = useState(false);
  let videoItem = (
    <div className="editable-video-item"
      onMouseDown={(e) => e.preventDefault()}
      onClickCapture={(e) => {
        (onSort || onMulDelChange) && e.stopPropagation()
        if (onMulDelChange) {
          onMulDelChange && onMulDelChange(it.id)
          setRefresh(!refresh);
        }
      }}
    >
      {sortMode && <img className="reorder" src={reorderImg} alt="" />}
      {delMode && <Checkbox className="select" checked={delData.indexOf(it.id) > -1} ></Checkbox>}
      <Image className="thumb" src={it.thumb} alt={it.thumb} onClick={() => onClick(it)} />
      <div className="item-info">
        <div className="top">
          <div className="title">
            {it.title}
          </div>
          {/* <div className="free">{!isTry ? `$${price}` : 'FREE'}</div> */}
        </div>

        <div className="bottom">
          <div className="count">
            <span>{it.viewcount} {translate('creator.playlist.25')}</span>
          </div>
          <div className="date">
            Uploaded on {moment(it.createtime, 'YYYY-MM-DD HH:mm:ss').format('MM/DD/YYYY')}
          </div>
        </div>


      </div>
      {!sortMode && !delMode && <div className="tool-btns">
        {onEdit && <a onClick={() => onEdit(it)}>{IconEdit}</a>}
        {/* {onDelete && (
          <Popconfirm
            placement="topRight"
            title={translate('creator.playlist.13')}
            onConfirm={() => onDelete(it)}
          >
            <a>{IconDelete}</a>
          </Popconfirm>
        )} */}
      </div>}
    </div>
  );
  let SortItem = SortableHandle(() => videoItem);
  return (
    <div className="video-item-wrap">
      {sortMode ? <SortItem /> : videoItem}
    </div>
  );
}

const SortableVideoListItem = SortableElement(VideoListItem);

const SortableVideoList = SortableContainer((props: VideoListProps) => (
  <div className="editable-video-list">
    {props.list.map((it, i) => <SortableVideoListItem key={it.id} {...props} index={i} item={it} isTry={i < (props.trycount ?? 0)} />)}
  </div>
));

export const VideoList: FC<VideoListProps> = (props) => {
  const { list, onSort, onMulDelChange, trycount = 0 } = props;

  if (!list.length) {
    return <div className="video-list-no-data"> <></> </div>
  }

  if (onSort) {
    return (
      <SortableVideoList
        {...props}
        useDragHandle={true}
        onSortEnd={({ oldIndex, newIndex }) => {
          console.log(oldIndex, newIndex);
          onSort(arrayMoveImmutable(list, oldIndex, newIndex))
        }}
      />
    );
  }

  return (
    <div className="editable-video-list">
      {list.map((it, i) => (
        <VideoListItem key={it.id} {...props} item={it} isTry={i < trycount} />
      ))}
    </div>
  );
}