import { observer } from 'mobx-react';
import './Header.scss';
import {  Select } from 'antd';
import StatisticStore from '../../stores/StatisticStore';
import GlobalConfig from '../../config/GlobalConfig';
import { Icon_search_black } from '../../images/index/blackSearch';
import Translate, { translate } from '../../components/Text';
const { Option } = Select;


function handleChange(value: any) {
    var symbol = value.split(':')[0]
    window.scrollTo(0, 0);
    StatisticStore.symbol = symbol;
    GlobalConfig.history.push(`/main/stock/statistic/${symbol}`);
}

function onSearch(val: any) {
    console.log('search:', val);
}
export const StockSearchInput = observer((props: any) => {

    let children: any = [];
    StatisticStore.symbols.forEach((s, i) => {
        children.push(<Option key={i} value={`${s.symbol}:${s.name}`}>
            <div className="symbol"> {s.symbol.replaceAll('_','  ')}</div>
            <div className="name">
                {s.name}
            </div>
        </Option>);
    })
    return (
        <div className='searchWrapper'>
            <Select showSearch={true}
                style={{ width: '100%' ,fontSize:"16px"}}
                placeholder={translate('markets.n')}
                onChange={handleChange}
                optionFilterProp="children"
                onSearch={onSearch}
                showArrow={false}
                filterOption={(input, option: any) =>
                    option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }

            >
                {children}
            </Select>
            <div className='search'>
                <img className='icon' src={Icon_search_black} alt="" />
            </div>
        </div>

    );
})


