import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Loading_icon } from '../../../../images/Loading';
import { Icon_rate_profile } from '../../../../images/market/profile';
import { Icon_rate_star } from '../../../../images/market/star';
import { Icon_rate_star_off } from '../../../../images/market/starOff';
import StatisticStore from '../../../../stores/StatisticStore';
import NoData from '../../../../components/NoData';
import './Statistic.scss';
import Translate from '../../../../components/Text';
import moment from 'moment';
import GlobalConfig from '../../../../config/GlobalConfig';


export const RecommendPage = observer((props: any) => {
    let symbol = props.match.params.symbol;
    useEffect(() => {
        StatisticStore.getRecommend(symbol);
    }, []);
    var noData = !StatisticStore.rateData?.rating_summary.buy && StatisticStore.rateData?.rating_summary.hold && !StatisticStore.rateData?.rating_summary.sell
   var recommendations = StatisticStore.rateData?.recommendation ||[];
    return (
        <div className="recommendWrapper">
            {StatisticStore.rateLoading ?
                <div className="loading">
                    <img src={Loading_icon} alt="" />
                </div>
                : <div className='recommendContent'>
                    <div className="rateContent">
                        {StatisticStore.rateData?.recommendation.length ? <div>
                            <div className="titles">
                                <div className="item">
                                    <div className="text">
                                        <Translate text='markets.recommend.e' />
                                    </div>
                                    <div className="text">
                                        <Translate text='markets.recommend.f' />

                                    </div>
                                    <div className="text">
                                        <Translate text='markets.recommend.g' />

                                    </div>
                                    <div className="text">
                                        <Translate text='markets.recommend.h' />

                                    </div>
                                </div>
                            </div>
                            <div className="list">
                                {recommendations.length>0 &&recommendations.map((r, i) =>
                                    <div key={i} className='item' onClick={() => {
                                        var middle: any = document.getElementsByClassName('ant-layout-content')[0];
                                        middle.scrollTo(0, 0)
                                        StatisticStore.analyst = r.analyst;
                                        GlobalConfig.history.push("/main/analyst")

                                    }}>
                                        <div className="text info">
                                            <img className='profile' src={Icon_rate_profile} alt="" />
                                            <div className="infos">
                                                <div className="firm">{r.firm_name}</div>
                                                <div className="name">{r.analyst}</div>
                                                <div className="rate">
                                                    {[1, 2, 3, 4, 5].map((n, i) => <img className='icon' src={r.analyst_rating > i ? Icon_rate_star : Icon_rate_star_off} alt="" />)}
                                                </div>
                                            </div>
                                        </div>
                                        <div className="text">{r.rating}</div>
                                        <div className="text">{r.target_price}</div>
                                        <div className="text">{r.date}</div>
                                    </div>
                                )}
                            </div>
                        </div> : <NoData />}
                    </div>
                    <div className="leftContent">
                      <div className="top">
                            <div className="recommendChartWrapper">
                                <div id="recommendChart"></div>
                                <div className="tips">
                                    <div className="item">
                                        <div className="icon"></div>
                                        <div className="text">BUY: {StatisticStore.buyRate}%</div>
                                    </div>
                                    <div className="item">
                                        <div className="icon"></div>
                                        <div className="text">HOLD: {StatisticStore.holdRate}%</div>
                                    </div>
                                    <div className="item">
                                        <div className="icon"></div>
                                        <div className="text">SELL: {StatisticStore.sellRate}%</div>
                                    </div>
                                </div>
                            </div>
                            {!noData?  <div className="info">
                                <div className="text">
                                    <Translate text='markets.recommend.a' args={{
                                        year: moment().format("YYYY-MM-DD").replaceAll('-', '/'),
                                        symbol: StatisticStore.symbol,
                                        buy: StatisticStore.rateData?.rating_summary.buy,
                                        hold: StatisticStore.rateData?.rating_summary.hold,
                                        sell: StatisticStore.rateData?.rating_summary.sell
                                    }} />
                                </div>
                            </div>: <div />}
                        </div>
                        <div className="bottom">
                            <div className="pricelist">
                                <div className="item">
                                    <div className="text"> <Translate text='markets.recommend.b' /></div>
                                    <div className="value">{StatisticStore.rateData?.target_price.min_target_price?.toFixed(2) || '--'}</div>
                                </div>
                                <div className="item">
                                    <div className="text"> <Translate text='markets.recommend.c' /></div>
                                    <div className="value">{StatisticStore.rateData?.target_price.avg_target_price?.toFixed(2) || '--'}</div>
                                </div>
                                <div className="item">
                                    <div className="text"> <Translate text='markets.recommend.d' /></div>
                                    <div className="value">{StatisticStore.rateData?.target_price.max_target_price?.toFixed(2) || '--'}</div>
                                </div>
                            </div>
                            <div className="tips"> <Translate text='markets.recommend.i' /></div>
                        </div>
                    </div>

                </div>}
        </div>
    );
});
