import { ReactNode, useEffect, useState } from 'react';
import { useRem } from '../utils/globalFuncs';

interface GridViewProps {
    id: string
    space?: number
    itemCount: number
    isWidthConstant?: boolean
    getColCount?: Function
    itemBuilder: (i: number) => ReactNode
}

export const GridView = (props: GridViewProps) => {
    const [subArray, setSubArray] = useState([] as any)
    const [colCount, setColCount] = useState(0)
    useEffect(() => {
        setSubArray([]);
        let cols: any = document.getElementsByClassName(props.id + 0)[0];
        if (cols) {
            let row: any = document.getElementById(props.id)
            let _colCount = Math.floor(row.clientWidth / cols.clientWidth);
            let subCount = props.itemCount % _colCount;
            setColCount(_colCount);
            props.getColCount && props.getColCount(_colCount);
            if (subCount) {
                setSubArray(new Array(_colCount - subCount));
            }
        }

    }, [props]);

    var fontSize = parseFloat(document.documentElement.style.fontSize.replace('px', ''));
    var marginRight = (props.space || 0) * (fontSize / 16);
    return (
        <div id={props.id} style={{ display: 'flex',width:'100%', justifyContent: props.isWidthConstant ? 'space-between' : 'center', flexWrap: 'wrap' }}>
            {[...new Array(props.itemCount), ...subArray].map((it: any, idx: number) => {
                if (idx >= props.itemCount) {
                    return <div key={idx} style={{ visibility: 'hidden',marginRight: (idx + 1) % colCount == 0 ? '0' : `${marginRight}px`  }}>{props.itemBuilder(0)}</div>
                }
                return (
                    <div key={idx} className={props.id + idx} style={{ marginRight: (idx + 1) % colCount == 0 ? '0' : `${marginRight}px` }}>
                        {props.itemBuilder(idx)}
                    </div>
                );
            })}
        </div>
    )
}