import { observer } from 'mobx-react';
import { useEffect } from 'react';
import GlobalConfig from '../../../../config/GlobalConfig';
import { Loading_icon } from '../../../../images/Loading';
import { Icon_collect } from '../../../../images/market/collect';
import { Icon_collect_on } from '../../../../images/market/collectOn';
import { Icon_stock_info } from '../../../../images/market/info';
import StatisticStore from '../../../../stores/StatisticStore';
import NoData from '../../../../components/NoData';
import './Statistic.scss';
import Translate from '../../../../components/Text';
import moment from 'moment';
export const SalesPage = observer((props: any) => {
    let symbol = props.match.params.symbol;
    useEffect(() => {
        StatisticStore.symbol = symbol;
        StatisticStore.getSalesDetail();
        if (Object.keys(StatisticStore.ownerShip?.insider_transactions_summary || {}).length) {
            StatisticStore.paintOwnerShipChart();
        }
    }, [])
    return (
        <div className="salesWrapper">
            {StatisticStore.salesLoading ? <div className="loading" >
                <img src={Loading_icon} alt="" />
            </div> : <div className='contentWrapper'>
                <div className="leftContent">
                    <div className="transactionContent salesContent">
                        <div className="title"><Translate text='markets.sale.e' /></div>
                        {StatisticStore.sales?.length ? <div className="titles">
                            <div className="item">
                                <div className="text"><Translate text='markets.sale.a' /></div>
                                <div className="text"><Translate text='markets.sale.b' /></div>
                                <div className="text"><Translate text='markets.sale.c' /></div>
                                <div className="text"><Translate text='markets.sale.d' /></div>
                            </div>

                            <div className="list">
                                {StatisticStore.sales.map((t, i) =>
                                    <div key={i} className='item'>
                                        <div className="text">{moment(t.date).format('YYYY-MM-DD')}</div>
                                        <div className="text">{t.reported_gross_short_sales}</div>
                                        <div className="text">{t.issued_capital}</div>
                                        <div className="text">{t.percent_of_issued_capital_reported_as_short_sold}%</div>
                                    </div>
                                )}
                            </div>
                        </div> : <NoData />}
                    </div>
                </div>
                <div className="rightContent">
                {Object.keys(StatisticStore.ownerShip?.insider_transactions_summary || {}).length ? <div className="ownerShipChartContent">
                            <div id="ownerShipChart"></div>
                        </div> : <div></div>}
                    <div className="transactionContent">
                        <div className="title"><Translate text='markets.owner_ship.a' /></div>
                        {StatisticStore.ownerShip?.insider_transactions.length ? <div>
                            <div className="titles">
                                <div className="item">
                                    <div className="text"><Translate text='markets.owner_ship.d' /></div>
                                    <div className="text"><Translate text='markets.owner_ship.e' /></div>
                                    <div className="text"><Translate text='markets.owner_ship.f' /></div>
                                    <div className="text"><Translate text='markets.owner_ship.g' /></div>
                                </div>
                            </div>
                            <div className="list">
                                {StatisticStore.ownerShip?.insider_transactions.map((t, i) =>
                                    <div key={i} className='item' onClick={() => {
                                        var middle: any = document.getElementsByClassName('ant-layout-content')[0];
                                        middle.scrollTo(0, 0)
                                        StatisticStore.ownerShipName = t.name;
                                        GlobalConfig.history.push("/main/ownerShip")
                                    }}>
                                        <div className="text">{t.name}</div>
                                        <div className="text">{t.transactionDate}</div>
                                        <div className="text">{t.change}</div>
                                        <div className="text">{t.share}</div>
                                    </div>
                                )}
                            </div>
                        </div> : <NoData />}
                    </div>

                </div>
            </div>}
        </div>

    )
})

export const StockInfoTabs = () => {
    return <div className="stockTabs">
        <img src={Icon_stock_info} alt="" className="info" onClick={() => {
            GlobalConfig.history.push("/main/stockCompany")
        }} />
        <img src={StatisticStore.followedSymbols.indexOf(StatisticStore.symbol.toLowerCase()) == -1 ? Icon_collect : Icon_collect_on} alt="" className="watch" onClick={() => {
            StatisticStore.follow();
        }} />
    </div>
}