import { subscribeStore } from '../../stores/SubscribeStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './Subscribe.scss';
import { Modal } from 'antd';
import { Loading } from '../../components/Loading';
import { LocalStorageService } from '../../utils/storage';
import { userStore } from '../../stores/UserStore';
import { Icon_Checked } from '../../images/checked';
import { Headers } from '../Header/Header';
import { showError } from '../../components/Message';
import { translate } from '../../components/Text';
import GlobalConfig from '../../config/GlobalConfig';
import { NavHeader } from '../CreatorCenter/NavHeader';
import { mainStore } from '../../stores/MainStore';

export const Subscribe = observer(() => {
    useEffect(() => {
        subscribeStore.getData();
    }, []);
    const titles = ['mine.subscribe.b', 'mine.subscribe.c', 'mine.subscribe.d', 'mine.subscribe.e', 'mine.subscribe.f', 'mine.subscribe.g', 'mine.subscribe.g2'];
    return (
        <>
            <Headers />
            <div className="subscribeWrapper">
                <NavHeader title={translate('mine.subscribe.a1')} onBack={() => {
                    let path = LocalStorageService.get('path');
                    if (!path) {
                        GlobalConfig.history.goBack()
                    } else {
                        GlobalConfig.history.push(path)
                    }
                }} className="subscribe-header">
                </NavHeader>

                {subscribeStore.loading ? <Loading /> : <div className="list">
                    {subscribeStore.list.map((l, idx) => <div key={idx} className="item">
                        <div className="title">{translate('mine.subscribe.n', { 'value': l.duration })}{parseInt(l.duration) > 1 && mainStore.isEn ? 's' : ''}</div>
                        <div className="price">${l.price}</div>
                        {/* <div className="description">{l}</div> */}
                        <div className="infos">
                            {titles.map((t, i) => <div className="info">
                                <img src={Icon_Checked} />
                                <div className="text">{translate(t)}</div>
                            </div>)}
                        </div>

                        <div className="payWrapper">
                            {(l.isSubscribe === 1 && l.isRenew) ? <div className="tips">{translate("mine.subscribe.z")}</div> : <div></div>}
                            <div className="pay" onClick={() => {
                                let isLogin = LocalStorageService.get("_userInfo");
                                if (isLogin) {
                                    if (l.isSubscribe === 1) {
                                        if (l.isRenew === 1) {
                                            subscribeStore.showConfirm = true;
                                        } else {
                                            subscribeStore.activateSubscribe(l.priceid);
                                        }
                                    } else {
                                        subscribeStore.subscribe(l.priceid);
                                    }
                                } else {
                                    GlobalConfig.history.push('/login')
                                }
                            }}>
                                {subscribeStore.subscribeLoadingId === l.priceid ? <Spin indicator={<LoadingOutlined style={{ fontSize: 15 }} />} /> : translate((l.isSubscribe === 1 ? (l.isRenew ? 'mine.subscribe.l' : 'mine.subscribe.k') : 'mine.subscribe.j'))}
                            </div>
                        </div>


                    </div>)}

                </div>}

                <Modal
                    title={translate("common.s")}
                    visible={subscribeStore.showConfirm}
                    onOk={subscribeStore.cancelSubscribe.bind(subscribeStore)}
                    confirmLoading={subscribeStore.confirmLoading}
                    onCancel={() => subscribeStore.showConfirm = false}
                    okText={translate('common.f')}
                    cancelText={translate('common.g')}
                    centered={true}

                    width={300}
                >
                    <p>{translate('mine.subscribe.a2')}?</p>
                </Modal>
            </div>
        </>
    )
});