import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { KlinDetailResponse, KlineDetailService } from "../services/KlineDetailService";
import { KlineService, PatternItem } from "../services/KlineService";

class KlineStore {
  public list: PatternItem[] = [];
  public loading: boolean = false;
  public detailloading: boolean = false;
  public type: string = 'bull';
  public loadingLatest: boolean = false;
  public detail?:KlinDetailResponse;

  constructor() {
    makeAutoObservable(this);
  }

  public async getData() {
    if (this.loading) {
      return;
    }
    let res:PatternItem[];
    this.loading = true;
    try {
      res  = await new KlineService().send();
    } catch (e: any) {
      this.loading = false;
      showError(e);
      return;
    }
    this.list = res;
    this.loading = false;
  }

  public async getDetailData(strategy:string) {
    if (this.detailloading) {
      return;
    }
    let res:KlinDetailResponse;
    this.detailloading = true;
    try {
      res  = await new KlineDetailService(strategy).send();
    } catch (e: any) {
      this.detailloading = false;
      showError(e);
      return;
    }
      this.detailloading = false;
      this.detail = res;
  }



  getTimeAddDays(time: Date, days: any) {
    return new Date(time.getTime() + days * 24 * 60 * 60 * 1000);
  }

}

const klineStore = new KlineStore();

export { klineStore };