import { observer } from 'mobx-react';
import { FC, useEffect, useMemo, useState } from 'react';
import StatisticStore from '../../../../stores/StatisticStore';
import './Statistic.scss';
import { StatisInfomation } from './Infomation';
import { StatisticChart } from './StatisticChart';
import { NewsItem } from '../../../News/NewsItem';
import { Loading_icon } from '../../../../images/Loading';
import NoData from '../../../../components/NoData';
import Translate from '../../../../components/Text';
import ReactECharts from 'echarts-for-react';
import iconArr from '../../../../images/market/arr.png';
import { Ownership } from '../../../../services/StockOwnerShipService';
import classNames from 'classnames';
import GlobalConfig from '../../../../config/GlobalConfig';
import { newsStore } from '../../../../stores/NewsStore';
import eventBus from '../../../../utils/eventBus';
import { isMobile } from '../../../../utils/globalFuncs';

const pieChartsColors = [
    '#06ACEC', '#6ACDF4', '#B4E6F9', '#F69540', '#F9B57A', '#FAD4B2', '#F96262', '#FBA1A1', '#FCBFBF', '#FDDFDF', '#FCEDED'
];


function generateColor() {
    return '#' + Array(3).fill(0).map(() => Math.floor(255 * Math.random()).toString(16)).join('');
}

const PieChart: FC<{ data?: Ownership[] }> = ({ data = [] }) => {
    const [hideList, setHideList] = useState(true);
    const finalData = [...data, {
        name: 'Other',
        percent: Math.max(0, 1 - data.reduce((sum, it) => sum + it.percent, 0))
    }];
    const colors = useMemo(() => finalData.map((_, i) => pieChartsColors[i] ?? generateColor()), [finalData.length]);
    return (
        <div className="pie-data-chart">
            <ReactECharts option={{
                title: {
                    show: false,
                },
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    show: false
                },
                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        radius: '80%',
                        data: finalData.map((it, i) => ({
                            value: it.percent,
                            name: it.name,
                            tooltip: {
                                formatter: ({ name, value }: any) => `${name}: ${(value * 100).toFixed(2)}%`
                            },
                            itemStyle: {
                                color: colors[i]
                            }
                        })),
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        label: {
                            show: false
                        }
                    }
                ]
            }} />
            {!hideList && <div className="chart-data-list">
                {finalData.map((it, i) => (
                    <div className="item" key={it.name}>
                        <div className="color" style={{ backgroundColor: colors[i] }} />
                        <div className="name">{it.name}</div>
                        <div className="percent">{(it.percent * 100).toFixed(2)}%</div>
                    </div>
                ))}
            </div>}
            <a onClick={() => setHideList(!hideList)} className={classNames("clo-btn", { close: hideList })}>
                <img src={iconArr} />
            </a>
        </div>
    )
}


export const StatisticPage = observer((props: any) => {
    let symbol = props.match.params.symbol;
    useEffect(() => {
        if (isMobile()) {
            window.location.href = `https://mobile.oliview.com.au/#/stock?symbol=${symbol}`
            return;
        }
        if (symbol == ':symbol') {
            GlobalConfig.history.push('/main/market/index');
            return;
        }
        StatisticStore.getData(symbol);
        var middle: any = document.getElementsByClassName('ant-layout-content')[0];
        let scrollFuc = (event: any) => {
            if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - 100)) {
                StatisticStore.getStockNews(true);
            }
        }
        middle.addEventListener("scroll", scrollFuc)
        eventBus.addListener('translate', () => {
            StatisticStore.getStockNews();
        })
        return () => {
            middle.removeEventListener("scroll", scrollFuc);
        }

    }, [props])
    return (
        <div className='statisticPage'>
            {
                StatisticStore.loading ? <div className="loading" >
                    <img src={Loading_icon} alt="" />
                </div> : <div className="statisticWrapper">
                    <div className="leftContent">
                        <div className="chartContent">
                            <div className="title"><Translate text='markets.stock.a' /></div>
                            <StatisInfomation />
                            <StatisticChart />
                        </div>
                        {!StatisticStore.loadingNews && <div className="newsContent">
                            {(StatisticStore.stockNews || []).map((n, i) =>
                                <div key={i} className="item">
                                    <NewsItem title={n.headline} image={n.image} content={n.description} url={n.url} createtime={n.datetime} source={n.source} />
                                </div>
                            )}
                        </div>}
                    </div>
                    <div className="rightContent">

                        <div className="holderContent">
                            <div className="title"><Translate text='markets.owner_ship.b' /></div>
                            {Object.keys(StatisticStore.ownerShip?.ownership || []).length ? (
                                <PieChart data={StatisticStore.ownerShip?.ownership} />
                            )
                                : <NoData />}
                        </div>
                        <div className="fundContent">
                            <div className="title"><Translate text='markets.owner_ship.c' /></div>
                            {Object.keys(StatisticStore.ownerShip?.fund_ownership || []).length ? (
                                <PieChart data={StatisticStore.ownerShip?.fund_ownership} />
                            )
                                : <NoData />}


                        </div>
                    </div>
                </div>
            }
        </div>

    );
});
