import { Button, Form, Input, Popconfirm } from 'antd';
import { useState } from 'react';
import { Img } from '../../components/Image';
import { showError } from '../../components/Message';
import { translate } from '../../components/Text';
import { CommentItem, CommentReplyService } from '../../services/CommentService';
import { userStore } from '../../stores/UserStore';
import { IconDelete } from '../UserCenter/Icons';
import './Comment.scss';
import { deleteVideo } from './CommentItem';
import IconLike from '../../images/main/like.png';

export interface CommentItemProps {
    key: any
    isSelf?: boolean
    pid?: string,
    item: CommentItem,
    onLike: Function
    success: Function
}

export const CommentSubItemPage = (props: CommentItemProps) => {
    const [reply, setReply] = useState(false);
    const [replying, setReplying] = useState(false);
    var replyComment = async (replyText?: string) => {
        if (!replyText) {
            showError('发言不可为空！');
            return;
        }
        setReplying(true);
        let replyId: string;
        try {
            replyId = await new CommentReplyService(parseInt(props.item?.objectid || ''), props.item?.objecttype || '1', replyText, props.item.id || '', props.pid || '').send();
        } catch (e) {
            setReplying(false);
            showError(`${e}`);
            return;
        }

        var Dates = new Date();
        var Y = Dates.getFullYear();
        var M = Dates.getMonth() + 1;
        var D = Dates.getDate();
        var H = Dates.getHours();
        var Mu = Dates.getMinutes();
        var times = Y + (M < 10 ? "-0" : "-") + M + (D < 10 ? "-0" : "-") + D + ' ' + (H < 10 ? "0" : "") + H + (Mu < 10 ? ":0" : ":") + Mu;
        var input: any = document.getElementById(`commentInput${props.item.id}`);
        input.value = '';
        setReply(false);
        setReplying(false);
        props.success({
            id: replyId,
            username: userStore.userInfo?.username,
            userimg: userStore.userInfo?.avatar,
            content: replyText,
            createtime: times,
            likecount: '0',
            islike: 0
        });
        replyText = '';
    }
    let content = props.item.content || '';
    let replyIdx1 = content.indexOf('@');
    let replyIdx2 = content.indexOf(':');
    let isReply = props.item.beid != props.pid;
    let isCustomReply = (replyIdx1 > -1 && replyIdx2 > -1 && replyIdx2 > replyIdx1);
    return (

        <div key={props.key} className="ttp-comment-item">
            <a aria-hidden="true" href="/c/user/4177015844/?source=tuwen_detail" target="_blank" className="">
                <div className="ttp-avatar auth-0 auth">
                    <img src={props.item.userimg} alt="" />
                </div>
            </a>
            <div className="comment-info">
                <div className="user-name">
                    <div className="left-content">
                        <span className="name">{props.item.username}</span>
                        {(isReply || isCustomReply) && <>
                            <span className="reply">回复</span>
                            <span className="user">
                                {
                                    isReply ? props.item.beusername : content.slice(replyIdx1 + 1, replyIdx2)
                                }
                            </span>
                        </>}

                    </div>

                    {props.isSelf && <Popconfirm title={translate('creator.myVideos.4')} onConfirm={() => deleteVideo(props.item.id)}>
                        <a className="btn">{IconDelete}</a>
                    </Popconfirm>}
                </div>
                <div className="body">
                    <p className="content">{isReply ? content.slice(replyIdx2 + 1) : content}</p>
                </div>
                <div className="footer">
                    <div className="left-action">
                        <span className="time">{props.item?.createtime?.substring(5, 16)}</span>
                        <div
                            className="like"
                            onClick={() => {
                                props.onLike();
                            }}
                        >
                            <Img
                                className="left"
                                src={IconLike}
                                alt=""
                            />
                            <div className="likeCount left">{props.item.likecount}</div>
                        </div>

                        {userStore.userInfo?.username != props.item.username && <button className="reply-btn" onClick={() => {
                            userStore.signInCheck(() => setReply(!reply))
                        }}>{translate('home.video.g')}</button>}
                    </div>

                </div>
                {reply &&
                    <div className='subReply'>

                        <Form
                            name="normal_login"
                            className="login-form"
                            onFinish={(v) => {
                                replyComment(v.replyText);
                            }}>
                            <div className="ttp-comment-input reply-wrapper">
                                <Form.Item
                                    name="replyText"
                                >
                                    <Input.TextArea id={`commentInput${props.item.id}`} className="comment-textarea" placeholder={translate('home.video.c')} />
                                </Form.Item>
                            </div>
                            <div className="action">
                                <Button className="submit-btn disable" htmlType="submit" loading={replying} >{translate('home.video.d')}</Button>
                            </div>
                        </Form>



                    </div>
                }

            </div>
        </div>


    );
}