import { Form, Input, Button, Select, Spin } from 'antd';
import { observer } from 'mobx-react';
import { PhoneOutlined, LockOutlined, MailOutlined } from '@ant-design/icons';
import { FindType, LoginModel, loginStore } from '../../stores/LoginStore';
import './Login.scss';
import { translate } from '../../components/Text';
import { Icon_captcha } from '../../images/index/captcha';
import { areaCodes } from './AreaCodes';
import { useRequest } from '../../utils/hooks';
import { SendCaptchaService } from '../../services/LoginService';
const { Option } = Select;

interface ForgetPasswordProps {
    close?: Function;
}


export const ForgetPasswordPage = observer((props: ForgetPasswordProps) => {
    var areacode = "61"
    const form = Form.useForm()[0];
    const { loading, data, load } = useRequest(() =>
        new SendCaptchaService().send()
    );
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }} defaultValue="61" onChange={(e) => {
                areacode = e;
            }}>
                {areaCodes.map((e, i) => <Option key={i} value={e.areacode}>+{e.areacode}</Option>)}
            </Select>
        </Form.Item>
    );
    return (
        <div className="info">
            <Form
                name="normal_login"
                form={form}
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={(v) => {
                    if (loginStore.findType == FindType.phone) {
                        v.areacode = areacode;
                    }
                    loginStore.findPassword(v);
                }}
            >
                {loginStore.findType == FindType.email ? <div>
                    <Form.Item
                        name="email"
                        rules={[{
                            required: true,
                            pattern: /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/,
                            message: translate('mine.register.d')
                        }]}
                    >
                        <Input placeholder={translate('mine.register.c')} />
                    </Form.Item>
                    <div className="captcha">
                        <Form.Item
                            className="codeInput"
                            name="captcha"
                            rules={[{ required: true, message: translate('mine.login.k') }]}
                        >
                            <Input placeholder={translate('mine.login.k')} />
                        </Form.Item>
                        <Button htmlType="submit"  className={`forget-form-button ${((loginStore.countDwon < 60 && loginStore.countDwon > 0) ||loginStore.sending) ?'':'send-button'}`} onClick={() => { loginStore.sendEmial(form.getFieldsValue(["email"]).email, 1) }}>
                            {translate(loginStore.sendBtnText)}
                        </Button>
                    </div>
                </div> :
                    <div>
                        <div className="phone-type phone">
                            <Form.Item
                                name="phone"
                                rules={[{
                                    required: true,
                                    pattern: /^(4|04)\d{8}$/,
                                    message: translate('mine.register.j')
                                }]}
                            >
                                <Input addonBefore={prefixSelector} placeholder={translate('mine.login.i')} />
                            </Form.Item>
                        </div>

                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="captcha"
                                rules={[{ required: true, message: translate('mine.login.j') }]}
                            >

                                <Input placeholder={translate('mine.login.j')} />
                            </Form.Item>
                            <div className="captchaImg forget-form-button" onClick={() => {
                                load({});
                            }} >
                                {loading ? <Spin spinning={true} size='small'>
                                </Spin> : <img src={data?.content} className="forget-form-button" />}
                            </div>

                        </div>

                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="code"
                                rules={[{ required: true, message: translate('mine.login.l') }]}
                            >
                                <Input placeholder={translate('mine.login.l')} />
                            </Form.Item>
                            <Button  className={`forget-form-button ${((loginStore.countDwon < 60 && loginStore.countDwon > 0) ||loginStore.sending) ?'':'send-button'}`} onClick={() => {
                                loginStore.sendPhone(form.getFieldsValue(["phone"]).phone, 2, areacode, data?.id || '', form.getFieldsValue(["captcha"]).captcha)
                            }}>
                                {translate(loginStore.sendBtnText)}
                            </Button>
                        </div>
                    </div>}

                <Form.Item
                    name="password"
                    rules={[{
                        required: true,
                        pattern: /^(?:(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])).{8,15}$/,
                        message: translate('mine.register.f')
                    }]}
                >
                    <Input
                        type="password"
                        placeholder={translate('mine.register.e')}
                    />
                </Form.Item>
                <Button htmlType="submit" className="login-form-button">
                    {translate('mine.l')}
                </Button>
            </Form>
            <div className="to-login">
                <a href="javascript:void(0);" onClick={() => {
                    if (loginStore.registerLoading) return;
                    loginStore.setLoginModel(LoginModel.login)
                }}>
                    {translate('login.m')}
                </a>
            </div>
        </div>
    );
});













