import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { Form, Input, Select, Button } from 'antd';
import Translate, { translate } from '../../components/Text';
import countryCode from './Country';
import { goldStore } from '../../stores/GoldStore';
import { stripeStore } from '../../stores/StripeStore';
import { loadStripe } from '@stripe/stripe-js';
import eventBus from '../../utils/eventBus';
import {
    Elements,
    useStripe,
    useElements,
    CardNumberElement,
    CardExpiryElement,
    CardCvcElement,
} from '@stripe/react-stripe-js';

// import './Stripe.scss';
import { showError, showLoading, showSuccess } from '../../components/Message';
import { useEffect, useState } from 'react';
import { mainStore } from '../../stores/MainStore';
import GlobalConfig from '../../config/GlobalConfig';

const { Option } = Select;
const PriceRange = [
    {
        value: '20000-50000',
        text: '20k-50k'
    },
    {
        value: '50000-100000',
        text: '50k-100k'
    },
    {
        value: 'above 100000',
        text: 'above 100k'
    },
];

const stripePromise = loadStripe(GlobalConfig.stripeKey);

interface PayGoladModalProps {
    visible: boolean;
    cancle: Function;
}

export const PayGoldModal = observer((props: PayGoladModalProps) => {
    const [refresh, useRefresh] = useState('');
    let priceChildren: any = [];
    PriceRange.forEach((s, i) => {
        priceChildren.push(<Option key={i} value={`${s.value}`}>
            {s.text}
        </Option>);
    })
    let countryChildren: any = [];
    countryCode.forEach((s, i) => {
        countryChildren.push(<Option key={i} value={`${s.en}`}>
            {s.en}
        </Option>);
    })
    return (
        <Modal title={translate('gold.b')} visible={props.visible}
            footer={null}
            centered={true}
            className='editUserModal buyGoldModal'
            onCancel={() => { props.cancle() }}
        >
            <div className="top">
                <div className="row">
                    <div className="text"><Translate text='gold.form.a' /></div>
                    <Select
                        className='range'
                        placeholder=''
                        onChange={(v) => {
                            goldStore.purchaseData.pricerange = v;
                        }}
                        optionFilterProp="children"
                        filterOption={(input, option: any) =>
                            option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
                        }

                    >
                        {priceChildren}
                    </Select>
                    <div className="text"><Translate text='gold.form.b' />：2000 AUD</div>
                </div>
                <div className="tips">
                    <span className='required'>*</span>  {translate('gold.form.c')}
                </div>
            </div>

            <div className="title"><Translate text='gold.form.address.a' /></div>
            <Form
                name="normal_login"
                layout="vertical"
                className="login-form edit-form"
                initialValues={{ remember: true }}
                autoComplete={'off'}
                onFinish={(v) => {
                    stripeStore.amount = goldStore.purchaseData.advance_deposit;
                    goldStore.purchaseData = Object.assign(goldStore.purchaseData, v);
                    eventBus.emit('stripeSubmit');
                }}
            >
                <div className='buy-gold-form'>
                    <div className="leftContent">
                        <div className="row">
                            <Form.Item
                                className='leftInput'
                                name="firstname"
                                label={<Translate text='gold.form.address.b' />}
                                rules={[{ required: true }]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item
                                name="lastname"
                                label={<Translate text='gold.form.address.c' />}
                                rules={[{ required: true }]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                        </div>

                        <Form.Item
                            name="company"
                            label={<Translate text='gold.form.address.d' />}
                        >
                            <Input placeholder="" />
                        </Form.Item>

                        <Form.Item
                            name="phone"
                            label={<Translate text='gold.form.address.e' />}
                            rules={[{ required: true }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>

                        <div className="row">
                            <Form.Item
                                className='leftInput'
                                name="state_province"
                                label={<Translate text='gold.form.address.f' />}
                                rules={[{ required: true }]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item
                                name="postalcode"
                                label={<Translate text='gold.form.address.g' />}
                                rules={[{ required: true }]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                        </div>
                    </div>
                    <div className="rightContent">
                        <Form.Item
                            name="addressline1"
                            label={<Translate text='gold.form.address.h' />}
                            rules={[{ required: true }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                        <Form.Item
                            name="addressline2"
                            label={<Translate text='gold.form.address.i' />}
                        >
                            <Input placeholder="" />
                        </Form.Item>

                        <div className="country">
                            <div className="text"><Translate text='gold.form.address.j' /></div>
                            <Select
                                showSearch={true}
                                style={{ width: '100%' }}
                                placeholder=''
                                onChange={(v) => {
                                    goldStore.purchaseData.country = v;
                                }}
                                optionFilterProp="children"
                                filterOption={(input, option: any) =>
                                    option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {countryChildren}
                            </Select>
                        </div>

                    </div>

                </div>
                <div className="stripe">
                    <div className="title">{translate('gold.form.i')}</div>
                    <Elements stripe={stripePromise} options={{ locale: 'en'  }}>
                        <CheckoutForm success={() => {
                            goldStore.showStripe = false;
                            goldStore.payModalVisible = false;
                            goldStore.paySuccess = true;
                        }} getSercet={goldStore.purchase.bind(goldStore)} />
                    </Elements>
                </div>

                <Form.Item>
                    <Button htmlType="submit" className="login-form-button modalSubmit" >
                        <Translate text='gold.form.address.k' />
                    </Button>
                </Form.Item>
            </Form>


        </Modal>
    );
})

interface CheckFormProps {
    success: Function;
    getSercet: Function;
}

const CheckoutForm = (props: CheckFormProps) => {
    const stripe = useStripe();
    const elements = useElements();
    eventBus.addListener('stripeSubmit', () => {
        handleSubmit();
    });
    let handleSubmit = async () => {
        if (elements == null || stripeStore.submitting) {
            return;
        }
        stripeStore.submitting = true;
        let element = elements.getElement(CardNumberElement);
        if (stripe && element) {
            let loading = showLoading(translate('gold.form.d'));
            try {
                const { error, paymentMethod } = await stripe.createPaymentMethod({
                    type: 'card',
                    card: element,
                });
                if (error) {
                    throw error.message;
                }
                let secret = await props.getSercet();
                console.log("secretsecret", secret)
                stripe.confirmCardPayment(secret, { payment_method: { card: element } }).then(function (result: any) {
                    stripeStore.submitting = false;
                    console.log("result", result)
                    //此处data.client_secret变量为生成订单后 后端返回给前端的秘钥  将这个秘钥返回给stripe去做校验
                    if (result.paymentIntent && result.paymentIntent.status == "succeeded") {
                        //支付成功的逻辑操作
                        loading();
                        props.success && props.success();
                    } else {
                        showError(translate('gold.form.e', { value: result.error.message }));
                    }
                })
            } catch (e) {
                stripeStore.submitting = false;
                showError(translate('gold.form.e', { value: `${e}` }));
            }
        } else {
            stripeStore.submitting = false;

        }
    };
    return (
        <div className="cardInfo">
            <div className="card">
                <CardNumberElement options={{ showIcon: true }} />
            </div>
            <div className="expiry">
                <CardExpiryElement />
            </div>
            <div className="cvc">
                <CardCvcElement />
            </div>
        </div>
    );
};

