import { observer } from 'mobx-react';
import { StockLogo } from '../../../../components/StockLogo';
import Translate from '../../../../components/Text';
import ChartStore from '../../../../stores/ChartStore';
import { mainStore } from '../../../../stores/MainStore';
import StatisticStore from '../../../../stores/StatisticStore';
import './Statistic.scss';
export const StatisInfomation = observer(() => {
    try {
        var symbolList: Array<string> = StatisticStore.summary?.symbol.split('_') || [''];
        var yearsList: Array<string> = StatisticStore.incomes?.financial_years || ['', ''];
        var revenueList: Array<number> = StatisticStore.incomes?.total_revenues || [];
        var incomeList: Array<number> = StatisticStore.incomes?.net_incomes || [];
        var assetList: Array<number> = StatisticStore.assets?.total_assets || [];
        var liabsList: Array<number> = StatisticStore.assets?.total_liabs || [];
        var year: string = yearsList[Math.min(yearsList.length - 1, 1)].split("-").reverse().join('/');
        var income: number = (((revenueList[0] - revenueList[1]) / revenueList[1]) * 100);
        var netIncome: number = (((incomeList[0] - incomeList[1]) / incomeList[1]) * 100);

        var netIncomeText =  `${netIncome > 0 ? (mainStore.isEn ? 'increased' : '上升') : (mainStore.isEn ? 'decreased' : '下降')} ${Math.abs(netIncome).toFixed(2)}`;
        if (incomeList[1] < 0 && incomeList[0] > 0) {
            netIncomeText = mainStore.isEn ? ' turned from negative to positive' : '由负转正';
        } else if (incomeList[1] > 0 && incomeList[0] < 0) {
            netIncomeText = mainStore.isEn ? ' turned from positive to negative' : '由正转负';
        }

        var curAsset = assetList[0] - liabsList[0];
        var lastAsset = assetList[1] - liabsList[1];

        var netAssets: number = (((curAsset - lastAsset) / lastAsset) * 100);
        var incomeTip: string = income === Infinity ? '迅速' : `${Math.abs(income).toFixed(2)}%`;
        var incomeText =  `${income > 0 ? (mainStore.isEn ? 'increased' : '上升') : (mainStore.isEn ? 'decreased' : '下降')} ${incomeTip}`;
        if (revenueList[1] < 0 && revenueList[0] > 0) {
            incomeText = mainStore.isEn ? ' turned from negative to positive' : '由负转正';
        } else if (revenueList[1] > 0 && revenueList[0] < 0) {
            incomeText = mainStore.isEn ? ' turned from positive to negative' : '由正转负';
        }
         var assetText =  `${netAssets > 0 ? (mainStore.isEn ? 'increased' : '上升') : (mainStore.isEn ? 'decreased' : '下降')} ${Math.abs(netAssets).toFixed(2)}%`;
         if (lastAsset < 0 && curAsset > 0) {
            assetText = mainStore.isEn ? ' turned from negative to positive' : '由负转正';
        } else if (lastAsset > 0 && curAsset < 0) {
            assetText = mainStore.isEn ? ' turned from positive to negative' : '由正转负';
        }

        var symbol: string = StatisticStore.summary?.symbol || '';
        var curClose = ChartStore.stockData[0]?.close;
        var preClose = ChartStore.stockData[1]?.close;
        if (!symbol) {
            return <div></div>
        }
        console.log("StatisticStore.summary" ,JSON.parse(JSON.stringify(StatisticStore.summary)));
        return (
            <div className="topWrapper">

                <div className="information">
                    <div className="leftInfo">
                        <StockLogo symbol={symbol} />
                        <div className="title-info">
                            <div className="title"><span>{symbol.split('_')[0]}</span><span>{symbol.split('_')[1]}</span></div>
                            <div className="subtitle">{StatisticStore.summary?.name}</div>
                        </div>
                    </div>
                    <div className='bottom-info'>
                        {ChartStore.stockData && <div className="data" style={{color:(StatisticStore.marketTitle?.latest_return || 0)>=0 ? '#4CB94D':'#EC4D3D'}}>
                            {StatisticStore.marketTitle?.latest_price}&nbsp;&nbsp;
                            <span>
                                ({StatisticStore.marketTitle?.latest_return??0 * 100}%)
                            </span>
                        </div>}
                    </div>
                </div>

                <div className="introduce">
                    <Translate text='markets.stock.t' args={
                        {
                            "symbol": symbolList[0],
                            "year": year,
                            "incomeTip":
                            incomeText,
                            "netIncome":
                            netIncomeText,
                            "netAssets":
                            assetText,
                        }
                    } />
                </div>
            </div>
        )
    } catch (e) {
        console.log("build error $e");
    }
    return <div></div>
});