// rem等比适配配置文件

import eventBus from "./eventBus";

// 基准大小
const baseSize = 16
// 设置 rem 函数
function setRem(delay: boolean) {
  // 当前页面宽度相对于 1920宽的缩放比例，可根据自己需要修改。
  const scale = document.documentElement.clientWidth / 1440
  // 设置页面根节点字体大小（“Math.min(scale, 2)” 指最高放大比例为2，可根据实际业务需求调整）
  const fontSize = baseSize * Math.min(scale, 1.1) + 'px';
  if (!delay) {
    document.documentElement.style.fontSize = fontSize;
  } else {
    setTimeout(() => {
      document.documentElement.style.fontSize = fontSize;
    }, 300);
  }

}
// 初始化
setRem(false);
// 改变窗口大小时重新设置 rem
window.onresize = function () {
  setRem(true);
  eventBus.emit('resize');
}

/**
 * 将开发过程中使用的 px 数值换算为实际运行环境中的 px
 * @param num 
 */
export function px(num: number) {
  const scale = Math.min(document.documentElement.clientWidth / 1440, 1.1);
  return num * scale;
}