import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { ArticleCategory, ResearchCategoryService, ResearchCollectService, ResearchData, ResearchHotService, ResearchItem, ResearchService } from "../services/ResearchService";

class ResearchStore {
  public categories: ArticleCategory[] = [];
  public list: ResearchItem[] = [];
  public hotList: ResearchItem[] = [];
  public loading: boolean = false;
  public type: string = '';
  public keyword: string = '';
  private _page = 1;
  public loadingMore: boolean = false;
  public loadingHot: boolean = false;
  private noMore: boolean = false;

  constructor() {
    makeAutoObservable(this);
  }

  public async getHot() {
    if (this.loadingHot) {
      return;
    }
    let response: ResearchData;

    this.loadingHot = true;

    try {
      response = await new ResearchHotService().send();
    } catch (e: any) {
      this.loadingHot = false;
      return;
    }
    this.loadingHot = false;
    this.hotList = response.list;
  }



  public async getData(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: ResearchData;
    console.log('loadmore',loadmore);
    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
    console.log('innnn');
    this.loading = true;
      this._page = 1;
    }
    try {
      if (this.type === 'collect') {
        response = await new ResearchCollectService(this._page).send();
      } else {
        response = await new ResearchService('', '', this.keyword, this.type, this._page).send();
      }
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.list.length === 0;
      this.list.push(...response.list);
    } else {
      this.list = response.list;
    }
  }

  public async getCategory() {
    try {
      this.categories = await new ResearchCategoryService().send();
    } catch (e: any) {
      // showError(e);
      return;
    }
    this.type = this.categories.map((c) => c.id).join(",");
  }

}

const researchStore = new ResearchStore();

export { researchStore };