import { makeAutoObservable, } from "mobx";
import { showError, showLoading, showSuccess } from "../components/Message";
import { CollectionItem, CollectionListService } from "../services/CollectionListService";
import { CreatorCollectCountService, CreatorCollectionDetailService, CreatorVideoCountService, CreatorVideoDeleteService, CreatorVideoItem, CreatorVideoService, CreatorVideoSortService, VideoCount } from "../services/CreatorVideoService";
import {
  swap
} from "react-grid-drag";
import { CreatorSubscribeListService, CreatorSubscribeService, CreatorUser, CreatorUserService } from "../services/CreatorService";
import { CreateCollectService, CreateDelCollectService, PublishVideoService } from "../services/PublishService";
import { userStore } from "./UserStore";


class CreatorVideoStore {
  private allCategories: Array<any> = [{ text: '个人介绍', key: 'introduce' }, { text: '视频', key: 'video' }, { text: '播放列表', key: 'collect' }, { text: '订阅列表', key: 'subscribe' }];
  public categories: Array<any> = [];
  public type: string = 'introduce';
  public priceType: string = 'charge';
  public list: CreatorVideoItem[] = [];
  public detailList: CreatorVideoItem[] = [];
  public detailListOrder: CreatorVideoItem[] = [];
  public followList: CreatorUser[] = [];
  public followIds: string[] = [];
  public loading: boolean = false;
  public user?: CreatorUser;
  public loadingUser: boolean = false;
  public loadingCollection: boolean = false;
  public loadingDetail: boolean = false;
  private _page = 1;
  public loadingMore: boolean = false;
  private noMore: boolean = false;
  public selectedItem?: CreatorVideoItem;
  public collections: CollectionItem[] = [];
  public collectionItem?: CollectionItem;
  public isEditing: boolean = false;
  public isSorting: boolean = false;
  public isSelf: boolean = false;
  public isfree: boolean = true;
  public editVideoId?: string;
  public editCollectionId?: string;

  public selectedVideoItem?: CreatorVideoItem;
  public isSubscribed: boolean = false;
  public showStripe: boolean = false;
  public searchkey: string = '';

  public userId: string = '0';
  public videoCount?: VideoCount;
  public collectCount?: VideoCount;
  

  constructor() {
    makeAutoObservable(this);
  }
  public async getUser() {
    if (this.loadingUser) {
      return;
    }
    this.loadingUser = true;
    let response: CreatorUser;
    this.isSelf = this.userId == userStore.userInfo?.id;
    if (!this.isSelf) {
      this.categories = this.allCategories.slice(0, this.allCategories.length - 1);
    } else {
      this.categories = this.allCategories;
    }
    try {
      response = await new CreatorUserService(this.userId).send();
    } catch (e: any) {
      this.loadingUser = false;
      showError(e);
      return;
    }
    this.loadingUser = false;
    this.user = response;
  }

  public async getCollections() {
    if (this.loadingCollection) {
      return;
    }
    this.loadingCollection = true;
    let response: CollectionItem[];

    try {
      response = await new CollectionListService({ searchkey: this.searchkey, userid: this.userId, psize: 20, isfree: this.isfree ? 0 : 1 }).send();
    } catch (e: any) {
      this.loadingCollection = false;
      showError(e);
      return;
    }
    this.loadingCollection = false;
    this.collections = response;
  }

  public async getCollectionDetail(id: string) {
    if (this.loadingDetail) {
      return;
    }
    this.loadingDetail = true;
    let response: CreatorVideoItem[];

    try {
      response = await new CreatorCollectionDetailService(id).send();
    } catch (e: any) {
      this.loadingDetail = false;
      showError(e);
      return;
    }
    this.loadingDetail = false;
    this.detailList = response;
    this.detailListOrder = JSON.parse(JSON.stringify(response));
  }

  public sortCollectionOrder(sourceId: string, sourceIndex: number, targetIndex: number, targetId?: string) {
    const nextState = swap<CreatorVideoItem>(this.detailList, sourceIndex, targetIndex);
    this.detailList = nextState;
  }

  public async sortVideo(id: string) {
    showLoading('提交中...');
    let videocreateid: string = this.detailList.map((l) => l.id).join(',');
    try {
      await new CreatorVideoSortService({
        collectid: id,
        videocreateid: videocreateid
      }).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    this.isSorting = false;
    showSuccess('修改成功');
  }

  public cancelOrder() {
    this.detailList = JSON.parse(JSON.stringify(this.detailListOrder));
  }

  public async getData(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CreatorVideoItem[];

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
      response = await new CreatorVideoService({ searchkey: this.searchkey, userid: this.userId, psize: 20, isfree: this.isfree ? 0 : 1 }).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length === 0;
      this.list.push(...response);
    } else {
      this.list = response;
    }
  }


  public async getSubscribeList(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CreatorUser[];

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
      response = await new CreatorSubscribeListService({ userid: this.userId, psize: 20 }).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      // showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length === 0;
      this.followList.push(...response);
    } else {
      this.followList = response;
    }
    this.followIds = this.followList.map((e) => e.id);
  }

  async editVideo(data: any, success: Function) {
    showLoading('提交中...');
    try {
      await new PublishVideoService(data).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    showSuccess('修改成功');
    success && success()
    window.scrollTo(0, 0);
  }

  async editCollection(data: any, success: Function) {
    showLoading('提交中...');
    try {
      await new CreateCollectService(data).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    showSuccess('修改成功');
    success && success()
    window.scrollTo(0, 0);
  }

  async deleteVideo(data: any, success: Function) {
    showLoading('删除中...');
    try {
      await new CreatorVideoDeleteService(data).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    showSuccess('删除成功');
    success && success()
  }
  async deleteCollection(data: any, success: Function) {
    showLoading('删除中...');
    try {
      await new CreateDelCollectService(data).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    showSuccess('删除成功');
    success && success()
  }

  async subscribe(id: any, success?: Function) {
    let isFollowed: boolean = this.followIds.indexOf(id) > -1;
    showLoading(`${isFollowed ? '取消' : ''}订阅中...`);
    try {
      await new CreatorSubscribeService({
        objectid: id,
        objecttype: 1,
        status: isFollowed ? 0 : 1,
      }).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    showSuccess(`${isFollowed ? '取消' : ''}订阅成功`);
    console.log("isFollowed", isFollowed)
    if (isFollowed) {
      let list: string[] = [];
      console.log("id", id)
      this.followIds.forEach((f) => {
        if (f != id) {
          list.push(f);
        }
      });

      console.log("list", list)
      this.followIds = list;
    } else {
      this.followIds.push(id);
    }
    success && success()
  }

  resetData() {
    this.isEditing = false;
    this.isSorting = false;
  }

  async getVideoCount() {
    let response: VideoCount;
    try {
      response = await new CreatorVideoCountService({userid:this.userId}).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    this.videoCount = response;
  }

  async getCollectCount() {
    let response: VideoCount;
    try {
      response = await new CreatorCollectCountService({userid:this.userId}).send();
    } catch (e) {
      showError(`${e}`);
      return;
    }
    this.collectCount = response;
  }
}

const creatorVideoStore = new CreatorVideoStore();

export { creatorVideoStore };