import { Icon_No_Data } from '../images/NoData';
import Translate from './Text';

export interface NoDataProps{
  text?:string
}

function NoData(props: NoDataProps) {
  return (
    <div className="no_data">
    <img src={Icon_No_Data} alt='' ></img>
    <div className="text">{props.text ?? <Translate text='common.c' />}</div>
</div>
  );
}

export default NoData;
