
import { Ajax } from '../utils/http';
import moment from 'moment';

export class StockNewsService {
    private symbol: string;
    private start: string;
    private end: string;
    constructor(_symbol: string,_start:string,_end:string) {
        this.symbol = _symbol;
        this.start = _start;
        this.end = _end;
    }

    public async send(): Promise<StockNewsItem[]> {
        window.moment=moment;
        var response: StockNewsData;
        var data = {
            symbol: this.symbol,
            start:this.start,
            end:this.end,
        }
        try {
            response = await new Ajax<StockNewsData>('/oliview_news', data, true).get();
        } catch (e) {
            throw e;
        }
        var list: StockNewsItem[] = [];
        var press_releases = Object.keys(response.press_releases).length?response.press_releases:[];
        var company_news = Object.keys(response.company_news).length?response.company_news:[];
        press_releases.forEach((p) => {
            list.push({
                headline: p.headline,
                source: p.symbol,
                image: p.image,
                datetime: p.datetime,
                url: p.url,
                description: p.description,
            })
        });
        company_news.forEach((p) => {
            list.push({
                headline: p.headline,
                source: p.source,
                image: p.image, 
                datetime: moment(p.datetime * 1000).format("YYYY-MM-DD HH:MM:SS"),
                url: p.url,
                description: p.summary,
            })
        });
        list.sort((a,b) => Date.parse(b.datetime) - Date.parse(a.datetime))
        return list;
    }
}


export interface StockNewsItem {
    headline: string;
    image: string;
    source: string;
    url: string;
    description: string;
    datetime: string;

}

/*Press_releases*/
export interface Press_releases {
    symbol: string;
    datetime: string;
    headline: string;
    description: string;
    url: string;
    image: string;
}

/*Company_news*/
export interface Company_news {
    category: string;
    datetime: number;
    headline: string;
    id: number;
    image: string;
    related: string;
    source: string;
    summary: string;
    url: string;
}

/*tsModel2*/
export interface StockNewsData {
    press_releases: Press_releases[];
    company_news: Company_news[];
}

