import { Modal, Select, Spin } from 'antd';
import { observer } from 'mobx-react';
import { LockOutlined } from '@ant-design/icons';
import { Form, Input, Button } from 'antd';
import { settingStore } from '../../stores/SettingStore';
import Icons from '../../images/images';
import { Icon_password } from '../../images/index/password';
import { translate } from '../../components/Text';
import './ChangeModal.scss';
import { useRequest } from '../../utils/hooks';
import { SendCaptchaService } from '../../services/LoginService';
import { areaCodes } from '../Login/AreaCodes';
import { loginStore } from '../../stores/LoginStore';
const { Option } = Select;

interface ChangeAccountModalProps {
    visible: boolean;
    cancle: Function;
    isEmail: boolean;
}

export const ChangeAccount = observer((props: ChangeAccountModalProps) => {
    var areacode = "61"
    const form = Form.useForm()[0];
    const { loading, data, load } = useRequest(() =>
        new SendCaptchaService().send()
    );
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 50 }} defaultValue="61" onChange={(e) => {
                areacode = e;
            }}>
                {areaCodes.map((e, i) => <Option key={i} value={e.areacode}>+{e.areacode}</Option>)}
            </Select>
        </Form.Item>
    );
    return (
        <Modal className='changeAccountModal' title={translate(`mine.${props.isEmail ? 'p' : 'q'}`)} visible={props.visible}
            footer={null}
            centered={true}
            onCancel={() => { props.cancle() }}
        >
            {props.visible && <Form
                name="normal_login"
                form={form}
                className="login-form"
                initialValues={{ remember: false }}
                onFinish={(v) => {
                    if (v.captchaCode) {
                        delete v.captchaCode;
                    }
                    v.phone = v.phone.indexOf('0') == 0 ? v.phone : `0${v.phone}`;
                    settingStore.changeAccount(v);
                }}
            >
                <div>
                    {props.isEmail ? <Form.Item
                        name="email"
                        rules={[{
                            required: true,
                            pattern: /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/,
                            message: translate('mine.register.d')
                        }]}
                    >
                        <Input placeholder={translate('mine.register.c')} />
                    </Form.Item> :
                        <div className="phone-type phone">
                            <Form.Item
                                name="phone"
                                rules={[{
                                    required: true,
                                    pattern: /^(4|04)\d{8}$/,
                                    message: translate('mine.register.j')
                                }]}
                            >
                                <Input addonBefore={prefixSelector} placeholder={translate('mine.login.i')} />
                            </Form.Item>
                        </div>}
                    {!props.isEmail && <div className="captcha phone-type">
                        <Form.Item
                            className="codeInput"
                            name="captchaCode"
                            rules={[{ required: true, message: translate('mine.login.j') }]}
                        >
                            <Input placeholder={translate('mine.login.j')} />
                        </Form.Item>
                        <div className="captchaImg forget-form-button" onClick={() => {
                            load({});
                        }} >
                            {loading ? <Spin spinning={true} size='small'>
                            </Spin> : <img src={data?.content} className="forget-form-button" />}
                        </div>
                    </div>}
                    <div className="captcha phone-type">
                        <Form.Item
                            className="codeInput"
                            name="captcha"
                            rules={[{ required: true, message: translate(props.isEmail ? 'mine.login.k' : 'mine.login.l') }]}
                        >
                            <Input placeholder={translate(props.isEmail ? 'mine.login.k' : 'mine.login.l')} />
                        </Form.Item>
                        <Button htmlType="submit" className="forget-form-button" onClick={(e) => {
                            e.preventDefault();
                            e.stopPropagation();
                            if (props.isEmail) {
                                settingStore.sendEmial(form.getFieldsValue(["email"]).email, 5)
                            } else {
                                settingStore.sendPhone(form.getFieldsValue(["phone"]).phone, 4, areacode, data?.id || '', form.getFieldsValue(["captchaCode"]).captchaCode)
                            }
                        }}>
                            {translate(settingStore.sendBtnText)}
                        </Button>
                    </div>
                </div>

                <Form.Item>
                    <Button htmlType="submit" className="modalSubmit">
                        {translate('mine.l')}
                    </Button>
                </Form.Item>
            </Form>}
        </Modal>
    );
})













