import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { Sub_Bg_Img } from '../../../images/SubBg';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-calendar/dist/Calendar.css';
import './Patterns.scss';
import NoData from '../../../components/NoData';
import { klineStore } from '../../../stores/KlineStore';
import { StrategiesMap } from '../../../services/StatisticService';
import { verifyRule } from '../../../utils/globalFuncs';
import { Loading_icon } from '../../../images/Loading';
import Translate from '../../../components/Text';
import { mainStore } from '../../../stores/MainStore';

export const PatternsPage = observer(() => {
  useEffect(() => {
    klineStore.getData();
  }, []);
  const tabs = [{ text: "markets.m", id: 0, type: 'bull' },
  { text: "markets.l", id: 1, type: "neutral" },
  { text: "markets.k", id: 2, type: "bear" }]
  return (
    <div className="klineWrapper">
      {klineStore.loading ? <div className="loading">
        <img src={Loading_icon} alt="" />
      </div> : <div className="klineContent">
        {/* <div className="topNav">
          <div className="menu">
            {
              tabs.map((r) => {
                return <div className="title" style={{ color: klineStore.type === r.type ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                  klineStore.type = r.type;
                }}>
                  {r.text}
                </div>
              })
            }

          </div>
        </div> */}
        {
          tabs.map((r) => {
            return <div className="patternContent">
              <div className="title">

                <Translate text={r.text} />
              </div>
              <div className="klineList">
                {(klineStore.list.filter((l: any) => l.trend == r.type)).map((n, i) => {
                  var strategy = StrategiesMap[n.pattern_name.en] || {};
                  return <div key={i} className="klineItem" onClick={() => {
                    verifyRule('technicalForm', `/main/market/patternDetail/${n.pattern_name.en}`)
                  }}>
                    <div className="tit-pic ">
                      {strategy && <img src={`https://aolinews.oss-accelerate.aliyuncs.com/patterns/${n.pattern_name.en?? ''}.png`} />}
                    </div>
                    <div className="content" >
                      <div className="title">{mainStore.isEn ? n.pattern_name.en_display : n.pattern_name.ch}</div>
                      <div className="info"> {mainStore.isEn ? n.description.en : n.description.ch}</div>
                    </div>
                  </div>
                })}
              </div>
            </div>
          })
        }
      </div>}
    </div>
  )
});