import { observer } from 'mobx-react';
import Translate, { translate } from '../../components/Text';
import { Form, Modal, Input, message, Button } from "antd";
import './Report.scss';
import { useRequest } from '../../utils/hooks';
import { ReportService } from '../../services/ReportService';
import { showError, showSuccess } from '../../components/Message';

interface VideoReportModalProps {
    videoid: string;
    visible: boolean;
    cancle: Function;
}


export const VideoReportModal = observer((props: VideoReportModalProps) => {
    const submitReportData = (data: any) => Promise.all([
        new ReportService(data).send().then(() => {
            props.cancle();
            showSuccess(translate("report.5"))
        }, (err) => {
            console.log("ewrrrr", err)
            showError(err)

        }),
    ]);
    const { loading: submitting, load: submitReport } = useRequest(submitReportData, { autoLoad: false });

    return (
        <Modal title={translate('report.1')} visible={props.visible}
            footer={null}
            centered={true}
            className='editUserModal editReportModal'
            onCancel={() => { props.cancle() }}
        >
            <Form labelCol={{ span: 4 }} className="video-edit-modal"
                onFinish={(data) => {
                    submitReport({ videoid: props.videoid, content: data.content });
                }}>
                <Form.Item name="content">
                    <Input.TextArea placeholder={translate('report.2')} maxLength={200} />
                </Form.Item>
                <div className="btns">
                    <div className="cancel" onClick={() => props.cancle()}>{translate('report.4')}</div>
                    <Button htmlType="submit" className="submit-btn" size="large" loading={submitting}>{translate('report.3')}</Button>
                </div>
            </Form>


        </Modal>
    );
})













