import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import {  IpoCalendarItem, IpoCalendarService} from "../services/StockCalendar";

class IpoCalendarStore {
  public list: IpoCalendarItem[] = [];
  public loading: boolean = false;
  public type: number = 1;
  public loadingLatest: boolean = false;
  private _start: string = '';
  private _end: string = ''

  constructor() {
    makeAutoObservable(this);
  }

  public async getData() {
    if (this.loading) {
      return;
    }
    this.loading = true;
    try {
      this.list = await new IpoCalendarService(this._start, this._end).send();
    } catch (e: any) {
      this.loading = false;
      showError(e);
      return;
    }
    this.loading = false;
  }


  getTimeAddDays(time: Date, days: any) {
    return new Date(time.getTime() + days * 24 * 60 * 60 * 1000);
  }


  public async changeType(_type: number) {
    this.type = _type;

    if (_type < 3) {
      var day = this.getTimeAddDays(new Date(), _type - 1);
      var date = this.getDate(day);
      this._start = date;

      this._end = date;
    } else if (_type == 3) {
      var now = this.getTimeAddDays(new Date(), -7);
      var next = this.getTimeAddDays(new Date(), 0);
      this._start = this.getDate(now);
      this._end = this.getDate(next);

    } else if (_type == 4) {
      var now = this.getTimeAddDays(new Date(), 0);
      var next = this.getTimeAddDays(new Date(), 7);
      this._start = this.getDate(now);
      this._end = this.getDate(next);
    }


    this.getData();
  }


  public setRange(range: any) {
    this._start = range[0].format('YYYY-MM-DD');
    this._end = range[1].format('YYYY-MM-DD');
    this.getData();
  }


  getDate(date: Date) {

    return date.getFullYear().toString() + '-' + (date.getMonth() + 1).toString() + '-' + date.getDate().toString();
  }

}

const ipoCalendarStore = new IpoCalendarStore();

export { ipoCalendarStore };