import { useEffect, useState } from "react"
import { VideoAuthService } from "../services/VideoAuth";
import { mainStore } from "../stores/MainStore";
import { LocalStorageService } from "../utils/storage";
import './Aliplayer.scss';
import IconCry from '../images/creator/cry.png';
import { translate } from "./Text";
import { LoginModel, loginStore } from "../stores/LoginStore";
import { userStore } from "../stores/UserStore";
import GlobalConfig from "../config/GlobalConfig";
import { StripeModal } from "../pages/Stripe/Stripe";
import { showSuccess } from "./Message";
import { stripeStore } from "../stores/StripeStore";
import { Icon_Full_Screen } from "../images/base64/FullScreen";
import { Icon_Volume } from "../images/base64/Volume";
import { Icon_Volume_Off } from "../images/base64/VolumeOff";
import { Icon_Play } from "../images/base64/Play";
import { Icon_Pause } from "../images/base64/Pause";
import { Icon_Select } from "../images/base64/Select";
import { Icon_Retry } from "../images/base64/Retry";

export interface AliplayerProps {
    vid: string,
    source: any,
    tryminutes: number,
    isMember: boolean,
    price: string,
    onComplete: Function,
    targetInfo: any,
    objectid: string,
    ordertype: number,
    onEnd: Function,
}

export const Aliplayer = (props: AliplayerProps) => {
    const { vid, source, tryminutes, isMember, price, onEnd, onComplete } = props;
    const [visible, setVisible] = useState(false);
    useEffect(() => {
        var player: any;
        let ready = false;
        let isLogin = LocalStorageService.get("_userInfo");
        let script = document.createElement('script');
        script.id = "endPreviewTemplate";
        script.type = "text/template";
        let isCharge = parseFloat(price) > 0;
        let topText = isLogin ? translate(`creator.myVideos.${isCharge ? (isMember ? 53 : 47) : 54}`) : translate(`creator.myVideos.55`);
        let btn1 = isMember && isLogin ? `<span class="price vip">${translate('mine.subscribe.j')}</span>` : `<span></span>`;
        let btn2 = isLogin ? `<span></span>` : `<span class="price login">${translate('mine.login.h')}</span>`;
        let btn3 = price == '0' || !isLogin ? `<span></span>` : `<span class="price buy">$${price}</span>`
        let icon = `<img src=".${IconCry}" />`
        let tryBtn = '';
        let prefix = `<span> ${translate(tryminutes == 1 ? 'payment.9' : 'payment.15', { value: tryminutes })}</span>`;
        let or = mainStore.isEn ? 'or' : '或'
        if (isLogin) {
            if (isMember) {
                if (isCharge) {
                    tryBtn = `${mainStore.isEn ? prefix.replace('Please', 'Become') : prefix} <a class="btn subscribe">${mainStore.isEn ? 'A VIP Member' : '订阅'}</a> ${or} <span class="btn purchase">${translate('payment.10')}</span> ${translate('home.video.p')}  `
                } else {
                    tryBtn = `${mainStore.isEn ? prefix.replace('Please', 'Become') : prefix} <a class="btn subscribe">${mainStore.isEn ? 'A VIP Member' : '订阅'}</a> ${translate('home.video.p')}`;
                }
            } else {
                tryBtn = `${prefix} <span class="btn purchase">${translate('payment.10')}</span> ${translate('home.video.p')}   `;
            }
        } else {
            if (isMember) {
                if (mainStore.isEn) {
                    tryBtn = `${prefix.replace('Please', '')} Become <span class="btn subscribe"> A VIP Member</span> ${translate('home.video.p')} If you are a member, please <span class="btn retry-login">${translate('mine.login.h')}</span>`;
                } else {
                    tryBtn = `${prefix.replace('请', '')} <span class="btn retry-login">VIP用户登录</span> ${translate('home.video.p')} `;
                }
            } else if (isCharge) {
                tryBtn = `${prefix.replace('请', '')} <span class="btn retry-login">${translate('mine.login.h')}</span> ${mainStore.isEn ? 'and' : '后'} <span>${translate('payment.10')}</span> ${translate('home.video.p').replace('后', '')} `;
            } else {
                tryBtn = `${prefix} <span class="btn retry-login">${translate('mine.login.h')}</span> ${translate('home.video.p')} `;
            }
        }

        script.innerHTML = `
            <div class="video-pay" >
                  ${icon}
            <div> ${tryBtn}</div>
              <div class="vip_limit_close"><img src=${Icon_Retry} /></div>
        </div >`;
        document.body.appendChild(script);


        let components: Array<any> = [{
            name: 'QualityComponent',
            type: (window as any).AliPlayerComponent.QualityComponent,
        },
        {
            name: 'MemoryPlayComponent',
            type: (window as any).AliPlayerComponent.MemoryPlayComponent,
        }, {
            name: 'PreviewVodComponent',
            type: (window as any).AliPlayerComponent.PreviewVodComponent,
            args: [tryminutes == -1 ? 0 : tryminutes * 60, '#endPreviewTemplate', tryBtn]
        }];
        let autoKey = mainStore.isEn ? 'Auto' : '自动';
        let sourceJsonString = JSON.stringify(source).replace("AUTO", autoKey);
        player = new ((window as any).Aliplayer)({
            //创建dom元素的id:
            id: 'videoContainer',
            //视频播放器的宽度
            width: '100%',
            //是否自动播放
            autoplay: true,
            rePlay: false,
            source: sourceJsonString,
            // source:'https://video.olicreater.com/customerTrans/ba72c38e2012aef6fe131611f84630a1/643e5a2-181b9c320a7-0014-56de-9fa-dbbb8.mp4',
            qualitySort: 'asc',
            language: mainStore.isEn ? "en-us" : "zh-cn",
            components: components,
            skinLayout: [
                { name: "bigPlayButton", align: "blabs", x: 30, y: 80 },
                {
                    name: "H5Loading", align: "cc"
                },
                { name: "errorDisplay", align: "tlabs", x: 0, y: 0 },
                { name: "infoDisplay" },
                { name: "tooltip", align: "blabs", x: 0, y: 56 },
                { name: "thumbnail" },
                {
                    name: "controlBar", align: "blabs", x: 0, y: 0,
                    children: [
                        { name: "progress", align: "blabs", x: 0, y: 44 },
                        { name: "playButton", align: "tl", x: 15, y: 12 },
                        { name: "volume", align: "tl", x: 10, y: 10 },
                        { name: "timeDisplay", align: "tl", x: 20, y: 7 },
                        { name: "fullScreenButton", align: "tr", x: 10, y: 12 },
                    ]
                }
            ]
        }, function (player: any) {
            player.on('sourceloaded', function (params: any) {
                var paramData = params.paramData
                var desc = paramData.desc
                var definition = paramData.definition
                if (source['AUTO']) {
                    player.getComponent('QualityComponent').setCurrentQuality(autoKey, autoKey)
                } else {
                    player.getComponent('QualityComponent').setCurrentQuality(desc, definition)
                }
                let fullScreen: any = document.querySelector('.prism-fullscreen-btn')
                let pauseIcon: any = document.querySelector('.prism-play-btn')
                let volumeIcon: any = document.querySelector('.volume-icon')
                let canPreview: any = document.querySelector('.can-preview')
                let previewTime: any = document.querySelector('.preview-time')
                let tip: any = document.querySelector('.preview-component-tip')
                previewTime.remove()
                canPreview.remove()
                tip.innerHTML.replace("， ", '')
                let tipText = tip.innerHTML.replace("， ", '')
                tip.innerHTML = tipText;
                fullScreen.style.background = `url(${Icon_Full_Screen}) no-repeat`;
                volumeIcon.style.background = `url(${Icon_Volume}) no-repeat`;
                pauseIcon.style.background = `url(${Icon_Pause}) no-repeat`;
                let volume = document.querySelector('.prism-volume')
                let time = document.querySelector('.prism-time-display')
                let controller = document.querySelector('.prism-controlbar')
                let slider = document.createElement('input');
                slider.setAttribute('type', 'range')
                slider.setAttribute('class', 'volume-range')
                slider.setAttribute('value', '100')
                slider.style.display = 'none';

                volumeIcon.addEventListener('click', () => {
                    let val = slider.getAttribute('value');
                    if (val == '0') {
                        player.setVolume(1);
                        slider.setAttribute('value', '100');
                        volumeIcon.style.background = `url(${Icon_Volume}) no-repeat`;
                    } else {
                        volumeIcon.style.background = `url(${Icon_Volume_Off}) no-repeat`;
                        player.setVolume(0);
                        slider.setAttribute('value', '0');
                    }

                })

                volume?.addEventListener('mouseover', () => {
                    slider.style.display = 'block';
                })
                volume?.addEventListener('mouseout', () => {
                    slider.style.display = 'none';
                })
                slider.addEventListener("input", (e: any) => {
                    let pecent = e.target.value / 100;
                    player.setVolume(pecent);
                    let background = volumeIcon.style.background;
                    let icon = `url("${Icon_Volume}") no-repeat`;
                    if (pecent == 0) {
                        volumeIcon.style.background = `url(${Icon_Volume_Off}) no-repeat`;
                    } else if (background != icon) {
                        volumeIcon.style.background = icon;
                    }
                })
                volume?.appendChild(slider);
            });
            player.on('play', function () {
                let playingIcon: any = document.querySelector('.prism-play-btn.playing')
                playingIcon.style.background = `url(${Icon_Play}) no-repeat`;
                var qualityModal: any = document.querySelector('.quality-modal');
                if (qualityModal && qualityModal.style.display == 'block')
                    qualityModal.style.display = 'none';
            })
            player.on('pause', function () {
                let pauseIcon: any = document.querySelector('.prism-play-btn')
                pauseIcon.style.background = `url(${Icon_Pause}) no-repeat`;
            })
            player.on('ready', function (params: any) {
                if (!ready) {
                    ready = true
                    var parentNode: any = document.querySelector('.quality-list');
                    var childList: any = [].slice.apply(parentNode.children).reverse();
                    console.log("childList", childList)
                    var list = childList.filter((c: any) => {
                        return c.innerText != autoKey;
                    })
                    var auto = childList.find((c: any) => {
                        return c.innerText == autoKey;
                    })
                    list.reduce((x: any, y: any) => parentNode.appendChild(y));
                    parentNode.appendChild(auto);
                    let playingIcon: any = document.querySelector('.prism-play-btn.playing')
                    playingIcon.style.background = `url(${Icon_Play}) no-repeat`;
                    let subscribe: any = document.querySelector('.subscribe');
                    let closeBtn: any = document.querySelector('.preview-vod-close');
                    let preview: any = document.querySelector('.preview-component-tip');
                    let subscribeList: any = document.getElementsByClassName('subscribe');
                    let purchaseList: any = document.getElementsByClassName('purchase');
                    let loginBtnList: any = document.getElementsByClassName('retry-login')
                    if (closeBtn)
                        closeBtn?.addEventListener('click', () => {
                            preview.style.display = 'none'
                        })
                    subscribeList.forEach((s: any) => {
                        s?.addEventListener('click', () => {
                            GlobalConfig.history.push('/subscribe')
                        })
                    });
                    purchaseList.forEach((s: any) => {
                        s?.addEventListener('click', () => {
                            stripeStore.objectid = props.objectid;
                            stripeStore.ordertype = props.ordertype;
                            stripeStore.pay(props.price);
                            setVisible(true);
                        })
                    });
                    loginBtnList.forEach((s: any) => {
                        s?.addEventListener('click', () => {
                            loginStore.LoginModel = LoginModel.login;
                            userStore.loginModalVisible = true;
                        })
                    });
                }

            });
            player.on('ended', function (params: any) {
                if (tryminutes == -1) {
                    onEnd();
                }

                let tip: any = document.querySelector('.preview-component-tip')
                let close: any = document.querySelector('.vip_limit_close');

                close?.addEventListener('click', () => {
                    player.seek(0);
                    player.replay();
                    player.getComponent('PreviewVodComponent').closePreviewLayer()
                })
                tip.style.display = 'none';
            });

            var vip_btn: any = document.querySelector('.vip')
            if (vip_btn)
                vip_btn.addEventListener('click', function () {
                    GlobalConfig.history.push('/subscribe');
                })

            var buy_btn: any = document.querySelector('.buy')
            if (buy_btn)
                buy_btn.addEventListener('click', function () {
                    stripeStore.objectid = props.objectid;
                    stripeStore.ordertype = props.ordertype;
                    stripeStore.pay(props.price);
                    setVisible(true);
                })


            var login_btn: any = document.querySelector('.login')
            if (login_btn)
                login_btn.addEventListener('click', function () {
                    loginStore.LoginModel = LoginModel.login;
                    userStore.loginModalVisible = true;
                })


        });
        return () => {
            document.body.removeChild(script);
            player.dispose();
        }
    }, []);
    return (
        <div id="videoContainer">
            <StripeModal
                visible={visible}
                success={() => {
                    setVisible(false);
                    showSuccess(mainStore.isEn ? 'Payment success' : '支付完成');
                    if (onComplete) {
                        onComplete();
                    }
                }}
                targetInfo={props.targetInfo}
                cancle={() => {
                    setVisible(false);
                }}
            />
        </div>
    )
}