import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { Form, Input, Select, DatePicker, Button } from 'antd';
import { userStore } from '../../stores/UserStore';
import moment from 'moment';
import { settingStore } from '../../stores/SettingStore';
import { translate } from '../../components/Text';

const { Option } = Select;

interface LoginModalProps {
    visible: boolean;
    cancle: Function;
}

export const EditBasicModal = observer((props: LoginModalProps) => {
    const dateFormat = 'YYYY-MM-DD';
    var disabledDate = (current:any) => {
        return current >  moment().add(0, 'day') ;
       };
    return (
        <Modal title={translate('mine.k')} visible={props.visible}
            width={400}
            footer={null}
            centered={true}
            className='editUserModal'
            onCancel={() => { props.cancle() }}
        >
            <Form
                name="normal_login"
                className="login-form edit-form"
                initialValues={{ remember: true }}
                onFinish={(v) => settingStore.editSetting(v)}
            >

                <Form.Item
                    name="birthday"
                    label={translate('mine.b')}>
                    <DatePicker disabledDate={disabledDate}  placeholder={userStore.userInfo?.birthday} defaultValue={userStore.userInfo?.birthday ? moment(userStore.userInfo?.birthday, dateFormat) : undefined} />
                </Form.Item>

                <Form.Item name='profile' label={translate('mine.c')} initialValue={userStore.userInfo?.profile}>
                    <Input.TextArea />
                </Form.Item>

                <Form.Item>
                    <Button htmlType="submit" className="login-form-button modalSubmit" >
                    {translate('mine.l')}
                    </Button>
                </Form.Item>
            </Form>
        </Modal>
    );
})













