import { observer } from 'mobx-react';
import { useEffect, useRef, useState } from 'react';
import StatisticStore from '../../../stores/StatisticStore';
import './MarketIndex.scss';
import classNames from 'classnames';
import { InvestOpportunity, MarketMovements, Movements } from '../../../services/WatchlistService';
import { useHistory } from 'react-router';
import { StockLogo } from '../../../components/StockLogo';
import { Loading_icon } from '../../../images/Loading';
import { indexStore } from '../../../stores/IndexStore';
import Translate from '../../../components/Text';
import { useRequest } from '../../../utils/hooks';
import { Ajax } from '../../../utils/http';
import { Spin } from 'antd';
import { Arrow_Left } from '../../../images/ArrowLeft';
import { Arrow_Right } from '../../../images/ArrowRight';
import GlobalConfig from '../../../config/GlobalConfig';
import { GridView } from '../../../components/GridView';

function StockChart({ symbol = '', name = '' }) {
    const { data, loading } = useRequest(() => new Ajax<any[]>('/prices', { symbol }, true).get());
    const containElRef = useRef<HTMLDivElement>(null);
    useEffect(() => {
        const Highcharts = (window as any).Highcharts;
        if (data) {
            const yearAgo = Date.now() - 365 * 24 * 3600 * 1000;
            const list = Object.values(data).filter(it => it.time >= yearAgo);
            const point = Math.round((Math.max(...list.map(it => it.close)) + Math.min(...list.map(it => it.close))) / 2);
            Highcharts.chart(containElRef.current, {
                chart: {
                    type: 'area',
                    backgroundColor: "#f6f6f6",

                },
                title: false,
                xAxis: {
                    visible: false,
                    type: 'datetime',
                    // crosshair: true
                },
                yAxis: {
                    visible: false,
                },
                credits: {
                    enabled: false
                },
                legend: {
                    enabled: false // 关闭图例
                },
                tooltip: {
                    enabled: false
                },
                series: [{
                    name,
                    color: '#00ff00',
                    fillColor: '#00ff00',
                    negativeFillColor: '#ff0000',
                    negativeColor: '#ff0000',
                    data: list.map(it => [it.time, it.close - point])
                }]
            })
        }
    }, [data]);

    return (
        <Spin spinning={loading}>
            <div className="stockChart" ref={containElRef}></div>
        </Spin>
    );
}

function MarketStatus(props: { data: MarketMovements[] }) {
    const list = props.data ?? [];
    return (
        <div className="contentWrapper">
            <div className="topInfo">
                <div className="title"><Translate text="markets.o" /></div>
                <div className="dealDate"></div>
            </div>
            <div className="stockContent">
                {list.map((it) => (
                    <div className="stockItem" key={it.symbol}>
                        <div className="stockName">{it.name}</div>
                        <div className="stockNum">
                            <div className="value">{it.value}</div>
                            <div className={classNames('rate', { raise: it.rate >= 0 })}>{(it.rate * 100).toFixed(2)}%</div>
                        </div>
                        <StockChart symbol={it.symbol} name={it.name} />
                    </div>
                ))}
            </div>
        </div>
    );
}

export const MarketPage = observer(() => {
    let history = useHistory();
    useEffect(() => {
        StatisticStore.getWatchMarketlist('AU');
        StatisticStore.getWatchMarketlist('US', true);
    }, []);
    const tabs = ['markets.b', 'markets.c'];
    return (
        <div className="marketCenterPage">
            {StatisticStore.marketLoading ? <div className="loading">
                <img src={Loading_icon} alt="" />
            </div> : <div className="content">
                <MarketStatus data={StatisticStore.marketsAu?.market_movements ?? []} />
                {tabs.map((t, index) => {
                    return <MarketItem itemId={'movements' + index} title={t} isTop={index == 0} />
                }
                )}
            </div>}
        </div >
    )
});

interface MarketItemProps {
    itemId: string
    title: string,
    isTop: boolean
}

export const MarketItem = observer((props: MarketItemProps) => {

    const [isAu, setAu] = useState(true);
    let markets = isAu ? StatisticStore.marketsAu : StatisticStore.marketsUs;
    let _list: Array<Movements> = (props.isTop ? markets?.top_movements?.top_10 : markets?.top_movements?.last_10) || [];
    return <div className="contentWrapper">
        <div className="topInfo">
            <div className="title">
                <div><Translate text={props.title} /></div>
                <div className="dealDate"><Translate text={'markets.d'} args={{ "date": markets?.top_movements.date }} /> </div>
            </div>
            <div className="switch">
                <div className="left" onClick={() => setAu(true)}> <img style={{ opacity: isAu ? 0.2 : 1 }} src={Arrow_Left} alt="" /></div>
                <div className="right" onClick={() => setAu(false)}><img style={{ opacity: !isAu ? 0.2 : 1 }} src={Arrow_Right} alt="" />  </div>
            </div>

        </div>
        <ul className="marketCenterContent">
            {isAu ? <MovementList itemId={'listTop' + props.itemId} list={_list} isAu={true} isTop={props.isTop} /> :
                <MovementList itemId={'listLast' + props.itemId} list={_list} isAu={false} isTop={props.isTop} />}
        </ul>
    </div>
});

interface MovementListProps {
    list: Array<Movements>
    itemId: string
    isAu: boolean
    isTop: boolean
}


export const MovementList = observer((props: MovementListProps) => {
    return <GridView id={props.itemId} isWidthConstant={true} itemCount={props.list.length} itemBuilder={(idx) => {
        let item = props.list[idx];
        return <div className="item" key={idx} onClick={() => {
            window.scrollTo(0, 0);
            StatisticStore.symbol = item.symbol_id;
            indexStore.link = '/stock/statistic';
            GlobalConfig.history.push(`/main/stock/statistic/${item.symbol_id}`);
        }}>
            <div className="info">
                <StockLogo symbol={item.symbol_id} />
                <div className="text">
                    <div className="symbol"><span>{item.symbol_id.split('_')[0]}</span><span>{item.symbol_id.split('_')[1]}</span></div>
                    <div className="name">{item.symbol_name}</div>
                </div>
            </div>
            <div className="price">
                <div className="left">{item.price}</div>
                <div className="right" style={{ color: props.isTop ? '#4CB94D' : '#EC4D3D' }}>{(item.daily_return * 100).toFixed(2)}%</div>
            </div>
        </div>
    }}></GridView>;
})