import { observer } from 'mobx-react';
import zh from '../language/zh.json';
import en from '../language/en.json';
import { mainStore } from '../stores/MainStore';

export interface TextProps {
    text: string,
    args?: any
}

const Translate = observer((props: TextProps) => {
    let value = translate(props.text, props.args);
    return (
        <span>{value}</span>
    );
})

export function getTranslator(isEn: boolean) {
    return (text: string, args?: any): string => {
        let textSplit: string[] = text.split(".");
        if(textSplit.length<=1){
                  return text;
        }
        let language = isEn ? en : zh;
        let count = -1;
        let getText = (value: any): any => {
            let result;
            count++;
            if (!value) {
                return '';
            }
            if (count < textSplit.length) {
                result = getText(value[textSplit[count]]);
             
            } else {
                if (args) {
                    for (var key in args) {
                        value = value.replace(`{${key}}`, args[key])
                    }
                }
                result = value;
            }
        return result;
        }
        let value = getText(language);
        return value;
    };
}

export const translate = (text: string, args?: any): string => {
    return getTranslator(mainStore.isEn)(text, args);
}

export default Translate;
