import './Header.scss';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import '../Research/Research.scss';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { StockSearchInput } from './SearchStock';
import GlobalConfig from '../../config/GlobalConfig';
import Translate, { translate } from '../../components/Text';
import { ReserchSearchInput } from './SearchResearch';
import { Popover, Select, Avatar } from 'antd';
import eventBus from '../../utils/eventBus';
import { VideoSearchInput } from './SearchVideo';
import { Link } from 'react-router-dom';
import { LocalStorageService } from '../../utils/storage';
import { px } from '../../utils/rem';
import imgMenu from '../../images/main/menu.png';
import imgNotification from '../../images/main/notification.png';
import { LoginRegisterModal } from '../Login/LoginModal';
import { TabsConfig } from '../../config/TabsConfig';
import { mainStore } from '../../stores/MainStore';
import { indexStore, SearchType } from '../../stores/IndexStore';
import { IconEdit, IconEditImg } from '../UserCenter/Icons';
import { Img } from '../../components/Image';
import Icon_logo from '../../images/main/sideLogo.png';
import { UnreadMessage } from '../../services/LoginService';

const { Option } = Select;

export interface HeadersProps {
    isIndex?: boolean
}

export const Headers = observer((props: HeadersProps) => {
    let links = TabsConfig.sideTabs.map((t) => t.link);
    let hash = window.location.hash;
    let hashIdx = links.indexOf(hash.slice(1));
    if (hashIdx > -1) {
        mainStore.menuKey = hashIdx;
    } else if (hash.indexOf("myCenter") > -1) {
        mainStore.menuKey = 5;
    }
    let unreadMessageCount = 0;
    if (userStore.userInfo) {
        let _message: UnreadMessage = userStore.userInfo.unreadMessage;
        if(_message)
        unreadMessageCount = _message.myAttentionMessageCount + _message.myMessageCount + _message.replyToMyMessageCount + _message.systemMessageCount
    }
    return (
        <div className="header">
            <div className="leftContent">
                {hash.indexOf('login') == -1 && hash.indexOf("subscribe") == -1 && <img className='foldIcon' src={imgMenu} alt="" onClick={() => {
                    if (mainStore.menuCollapsed) {
                        mainStore.tabWidth = document.body.clientWidth - px(236);
                    } else {
                        mainStore.tabWidth = document.body.clientWidth - 78;
                    }
                    mainStore.menuCollapsed = !mainStore.menuCollapsed;
                }} />}
                <Img className='logo' src={Icon_logo} alt="" onClick={() => {
                    GlobalConfig.history.push('/main/index/recommend')
                }} />
            </div>
            <div className="rightContent">
                {indexStore.searchType == SearchType.stock && <StockSearchInput />}
                {indexStore.searchType == SearchType.research && <ReserchSearchInput />}
                {indexStore.searchType == SearchType.creator && <VideoSearchInput />}
                <div className='profileContent'>
                    {!props.isIndex && <div className="subscribed" onClick={() => {
                        GlobalConfig.history.push('/subscribe');
                    }} >
                        <Translate text='mine.subscribe.a' />
                    </div>}
                    {!props.isIndex && userStore.isVip && (
                        <Popover placement="topLeft" content={(
                            <div className="user-float">
                                <div className="user-info-up">
                                    <img className="avatar" src={userStore.userInfo?.avatar} />
                                    <div className="info">

                                        <h1> <span className='hi'>Hi</span> , {userStore.userInfo?.username} <div className="vip-sign">VIP</div></h1>
                                        <div className="desc">
                                            {translate('home.e')} <span className="strong">{userStore.userInfo?.expiretime.slice(0, 11)}</span><br />
                                            <Link to="/subscribe">{translate('home.f')} &gt;&gt;</Link>
                                        </div>
                                    </div>

                                </div>
                                <Link to="/subscribe" className="renew-btn">{translate('home.g')}</Link>
                            </div>
                        )} trigger="hover">
                            <div className="vip-sign" onClick={() => {
                                GlobalConfig.history.push('/subscribe');
                            }}>VIP</div>
                        </Popover>
                    )}
                    <div className="language">
                        <Select value={mainStore.isEn ? "EN" : "ZH"} style={{ width: '100%' }} bordered={false} onChange={(v) => {
                            mainStore.isEn = v == 'EN';
                            LocalStorageService.set('_lang', v);
                            eventBus.emit('translate');
                        }}>
                            <Option value="ZH">ZH</Option>
                            <Option value="EN">EN</Option>
                        </Select>
                    </div>
                    <div className="notification" onClick={() => {
                        mainStore.menuKey = 6;
                        GlobalConfig.history.push('/main/myCenter/notification');
                    }}>
                        <img className="icon" src={imgNotification} alt="" />
                        {unreadMessageCount > 0 && <div className="tips">{unreadMessageCount}</div>}
                    </div>
                    <div >
                        {userStore.userInfo ?
                            <Popover placement="topLeft" content={(
                                <div className="login-float ">
                                    <div className="user-info">
                                        <div className="avatar-wrapper" onClick={() => {
                                            mainStore.menuKey = 6;
                                            GlobalConfig.history.push('/main/myCenter/center');
                                        }}>
                                            <img className='avatar' src={userStore.userInfo?.avatar} alt="" />
                                            <div className="edit">
                                                {IconEditImg}
                                            </div>
                                        </div>
                                        <div className='name'>{userStore.userInfo?.username}</div>
                                        <div className="contact">
                                            {userStore.userInfo?.email || userStore.userInfo?.phone}
                                        </div>
                                    </div>
                                    <div className="line"></div>
                                    <div className="logout-btn" onClick={() => {
                                        mainStore.menuKey = 0;
                                        userStore.logout()
                                        GlobalConfig.history.push("/main/index/recommend");
                                        window.location.reload();
                                    }}>{translate('mine.i')}</div>
                                </div>
                            )} trigger="hover">
                                <Avatar src={userStore.userInfo?.avatar} />
                            </Popover>
                            : <div>
                                <button type="button" className='login-btn' onClick={() => {
                                    loginStore.LoginModel = LoginModel.login;
                                    userStore.loginModalVisible = true;
                                }} >
                                    {translate("mine.login.h")}
                                </button>
                            </div>
                        }
                    </div>
                </div>
            </div>

            <LoginRegisterModal visible={userStore.loginModalVisible} cancle={() => { userStore.loginModalVisible = false }} />
        </div>)
})