import { goto, verifyRule } from '../utils/globalFuncs';
import './Lock.scss';

export const LockView = () => {
    return (
        <div className="lock">
            <div className="content">
                <div className="tips">研究内容仅对订阅会员开放</div>
                <div className="subscribe" onClick={() => {
                    verifyRule('researchReport');
                }}>点击订阅</div>
            </div>
        </div>
    )
}