import axios, { AxiosResponse } from 'axios';
import GlobalConfig from '../config/GlobalConfig';
import eventBus from './eventBus';
import { LocalStorageService } from './storage';
import qs from 'qs';
import { mainStore } from '../stores/MainStore';
import { userStore } from '../stores/UserStore';

enum RequestType {
    get,
    post,
    put,
    delete
}


export class Ajax<T>{
    private url: string;
    private data?: object;
    private useSubHost: boolean = false;
    private fullPath: boolean = false;
    constructor(_url: string, _data?: object, _useSubHost: boolean = false, _fullPath:boolean = false) {
        this.url = _url;
        this.data = _data;
        this.useSubHost = _useSubHost;
        this.fullPath = _fullPath;
    }

    public async get(): Promise<T> {
        let response: T;
        response = await this.request(RequestType.get)
        return response;
    }

    public async post(): Promise<T> {
        let response: T;
        response = await this.request(RequestType.post)
        return response;
    }

    public async put(): Promise<T> {
        let response: T;
        response = await this.request(RequestType.put)
        return response;
    }

    public async delete(): Promise<T> {
        let response: T;
        response = await this.request(RequestType.delete)
        return response;
    }

    private async request(type: RequestType): Promise<T> {
        let response: AxiosResponse<any>;
        let token: string = LocalStorageService.get('token');
        let requestConfig = {
            headers: {
                'content-type': "application/x-www-form-urlencoded; charset=UTF-8",
                'Accept-Language':mainStore.isEn?'en':'cn'
            }
        };
        let data = this.data || {};
        if (token) {
            data['token'] = token;
        }
        let httpUrl: string = (this.fullPath? '':this.useSubHost ? GlobalConfig.subHost : GlobalConfig.host) + this.url;
        try {
            switch (type) {
                case RequestType.get:
                    response = await axios.get(`${httpUrl}?${qs.stringify(data)}`, requestConfig);
                    break;
                case RequestType.post:
                    response = await axios.post(httpUrl, qs.stringify(data), requestConfig);
                    break;
                case RequestType.put:
                    response = await axios.put(httpUrl, qs.stringify(data), requestConfig);
                    break;
                case RequestType.delete:
                    response = await axios.delete(`${httpUrl}?${qs.stringify(data)}`, requestConfig);
                    break;
            }
        } catch (e) {
            console.log("e", e);
            throw e;
        }
        let result = response.data;
        if(this.fullPath){
          return result;
        }
        if (!this.useSubHost) {
            let status: number = result.status;
            if (status !== 1) {
                let error: string = result.error;
                if (error.indexOf("登录失效") > -1) {
                    userStore.logout();
                    GlobalConfig.history.push("/main/index/recommend");
                    window.location.reload();
                }
            throw error;
            }
            return result.data;
        } else {
            return result;
        }
    }

    private paramsPostBody(obj: any) {
        var result = '';//接受最后结果
        var item;
        for (item in obj) {
            result += '&' + item + '=' + encodeURIComponent(obj[item])
        }
        if (result) {
            result = result.slice(1)//去掉第一个&
        }
        return result
    }
}