import { observer } from 'mobx-react';
import './Header.scss';
import {  Input } from 'antd';
import StatisticStore from '../../stores/StatisticStore';
import GlobalConfig from '../../config/GlobalConfig';
import { Icon_search_black } from '../../images/index/blackSearch';
import Translate, { translate } from '../../components/Text';
import { researchStore } from '../../stores/ResearchStore';


function handleChange(value: any) {
    var symbol = value.split(':')[0]
    window.scrollTo(0, 0);
    StatisticStore.symbol = symbol;
    GlobalConfig.history.push(`/main/stock/statistic/${symbol}`);
}

function onSearch(val: any) {
    console.log('search:', val);
}
export const ReserchSearchInput = observer((props: any) => {
    return (
        <div className='searchResearchWrapper'>

            {/* <Select showSearch={true}
                style={{ width: '100%' }}
                placeholder={translate('common.o')}
                onChange={handleChange}
                optionFilterProp="children"
                onSearch={onSearch}
                showArrow={false}
            >
            </Select> */}
            <Input placeholder={translate('common.o')} onChange={(v)=>{
                researchStore.keyword = v.target.value;
            }} onPressEnter={()=>{
                var researchContent: any = document.getElementsByClassName('researchContent')[0];
                var _top: number = researchContent.offsetTop;
                window.scrollTo(0, _top - 150);
                researchStore.getData();
            }} />
            <div className='search' onClick={()=>{
                var researchContent: any = document.getElementsByClassName('researchContent')[0];
                var _top: number = researchContent.offsetTop;
                window.scrollTo(0, _top - 150);
                researchStore.getData();
            }}>
                <img className='icon' src={Icon_search_black} alt="" />
            </div>
        </div>

    );
})


