    public static setStatisticChart = (data1: Array<Array<number>>, data2: Array<Array<number>>) => {
        return {
            chart: {
                events: {
                    load: function () {
                    }
                },
                backgroundColor: "#FAFAFA",
                zoomType: 'x',
                plotBackgroundColor: 0,
                spacing: [5, 0, 5, 5]

            },
            scrollbar: {
                enabled: false
            },
            navigator: {
                enabled: false
            },
            rangeSelector: {
                enabled: false,
            },
            tooltip: {
                enabled: false
            },
            yAxis: [{
                labels: {
                    align: 'right',
                    style: { "fontSize": "12px" },
                    formatter: function (): any {
                        let value: number = (this as any).value;
                        return numFormatter(value);
                    }
                },
                gridLineWidth: 0.5,
                endOnTick: true,
                gridLineColor: "#EEEEEE",
                crosshair: {
                    label: {
                        enabled: true,
                        formatter: function (value: number): any {
                            return numFormatter(value, 2);
                        },
                        style: { "fontSize": "12px" },
                    }
                },
                tickColor: '#8E8E8E',
                lineColor: "#8E8E8E",
                opposite: false,
                resize: {
                    enabled: true
                }
            }],
            xAxis: [{
                labels: {
                    style: { "fontSize": "12px" },
                },
                crosshair: {
                    label: {
                        enabled: true,
                        style: { "fontSize": "12px" },
                    }
                },
                gridLineColor: "#E1E1E1",
                startOnTick: true,
                tickColor: '#8E8E8E',
                lineColor: "#8E8E8E",
            }],
            stockTools: {
                gui: {
                    enabled: true
                }
            },

            series: [{
                type: 'line',
                name: '总资产',
                data: data1,
                color: "#F1CD49",
                tooltip: {
                    valueDecimals: 2
                }
            }, {
                type: 'line',
                name: '总负债',
                color: "#4887F6",
                data: data2,
                tooltip: {
                    valueDecimals: 2
                }
            }],
        };
    }


