    public static setRecommendChart = (data: Array<any>) => {
        return {
            chart: {
                type: 'pie'
            },
            xAxis: {
                categories: ['BUY', 'HOLD', 'SELL']
            },
            scrollbar: {
                enabled: false
            },
            navigator: {
                enabled: false
            },
            rangeSelector: {
                enabled: false,
            },
            tooltip: {
                enabled: false
            },
            stockTools: {
                gui: {
                    enabled: false
                }
            },
            plotOptions: {
                pie: {
                    innerSize: '50%'
                },
                series: {
                    allowPointSelect: false
                }
            },
            series: [{
                type: 'pie',
                colors: ['#5B8FF9', '#61DDAA', '#65789B'],
                data: data
            }]
        }
    }




