
import { Ajax } from '../utils/http';

export class StockRateService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<RatedData> {
        var response: RatedData;
        var data = {
            symbol: this.symbol,
        }
        response = await new Ajax<RatedData>('/oliview_market_rating', data, true).get();
        return response;
    }
}

export class StockAnalystService {
    private analyst: string;
    constructor(_analyst: string) {
        this.analyst = _analyst;
    }

    public async send(): Promise<Array<AnalystData>> {
        var response: Array<AnalystData>;
        var data = {
            analyst: this.analyst,
        }
        response = await new Ajax<Array<AnalystData>>('/analyst_reviews', data, true).get();
        return response;
    }
}



/*tsModel0*/
export interface AnalystData {
    symbol: string;
    firm_name: string;
    analyst: string;
    rating: number;
    target_price: number;
    date: number;
  }
  
/*Rating_summary*/
export interface Rating_summary {
    buy: number;
    hold: number;
    sell: number;
}

/*Target_price*/
export interface Target_price {
    min_target_price: number;
    avg_target_price: number;
    max_target_price: number;
}

/*Recommendation*/
export interface Recommendation {
    date: string;
    firm_name: string;
    analyst: string;
    rating: number;
    target_price: number;
    analyst_rating: number;
}

/*tsModel3*/
export interface RatedData {
    rating_summary: Rating_summary;
    target_price: Target_price;
    recommendation: Recommendation[];
}

