import { FC, useEffect, useState } from "react";
import { RouteComponentProps, useHistory } from "react-router";
import Translate from "../../../components/Text";
import { Arrow_Right } from "../../../images/ArrowRight";
import { useRequest } from "../../../utils/hooks";
import { Ajax } from "../../../utils/http";
import { keyBy } from "lodash";
import "./OpportunitiesPage.scss";
import { Loading } from "../../../components/Loading";
import { StockLogo } from "../../../components/StockLogo";
import { observer } from "mobx-react";
import { mainStore } from "../../../stores/MainStore";
import { Arrow_Left } from "../../../images/ArrowLeft";
import { verifyRule } from "../../../utils/globalFuncs";
import GlobalConfig from "../../../config/GlobalConfig";
import { LockView } from "../../../components/Lock";
import { GridView } from "../../../components/GridView";

export interface OpportunityDataItem {
  symbol: string;
  name: string;
}

export interface OpportunityData {
  sector: string;
  sector_cn: string;
  itemsGroups: OpportunityDataItem[][];
}

function getInvestOpportunity(): Promise<OpportunityData[]> {
  return new Ajax("/invest_opportunity", {}, true)
    .get()
    .then(({ AU = [], US = [] }: any) => {
      const usMap = keyBy(US, "sector");
      return AU.map(
        ({ sector, sector_cn, symbols }: any) =>
        ({
          sector,
          sector_cn,
          itemsGroups: [symbols, usMap[sector].symbols],
        } as OpportunityData)
      );
    });
}

const OpportunitiesArea: FC<{ item: OpportunityData }> = observer(({ item }) => {
  const isEn = mainStore.isEn;
  const [tab, setTab] = useState(0);
  const { sector, sector_cn, itemsGroups } = item;
  const groups = itemsGroups[tab];
  const his = useHistory();
  const canRight = tab + 1 < itemsGroups.length;
  const canLeft = tab > 0;
  return (
    <div className="opport-area">
      <div className="topInfo">
        <div className="title">
          {isEn ? sector : sector_cn}
        </div>
        <div className="switch">
          <div className="left" onClick={() => canLeft && setTab(tab - 1)}>
            <img style={{ opacity: canLeft ? 1 : 0.2 }} src={Arrow_Left} alt="" />
          </div>
          <div className="right" onClick={() => canRight && setTab(tab + 1)}>
            <img
              style={{ opacity: canRight ? 1 : 0.2 }}
              src={Arrow_Right}
              alt=""
            />
          </div>
        </div>
      </div>
      <ul className="marketCenterContent opportunityContent">
        <GridView id="opportunityContent" space={10} isWidthConstant={true} itemCount={groups.length} itemBuilder={(idx) => {
          let symbol = groups[idx].symbol;
          let name = groups[idx].name;
          return <div
            className="item"
            onClick={() => his.push(`/main/stock/statistic/${symbol}`)}
          >
            <div className="info">
              <StockLogo symbol={symbol} />
              <div className="text">
                <div className="symbol">
                  <span>{symbol.split("_")[0]}</span>
                  <span>{symbol.split("_")[1]}</span>
                </div>
                <div className="name">{name}</div>
              </div>
            </div>
          </div>
        }} />
        
      </ul>
    </div>
  );
});

export const OpportunitiesPage: FC<RouteComponentProps> = ({ }) => {
  const { data = [], loading } = useRequest(getInvestOpportunity);
  if (loading) {
    return (
      <div className="opportunities-page">
        <Loading size="mini" />
      </div>
    );
  }
  return (

    <div className="opportunities-page" >
      <div style={{ filter: verifyRule('researchReport', undefined, true) ? 'none' : 'blur(15px)' }}>
        {data.map(it => <OpportunitiesArea key={it.sector} item={it} />)}
      </div>
      {!verifyRule('researchReport', undefined, true) && <LockView />}
    </div>
  );
};
