import 'moment-timezone';
import moment from "moment";
import { FC, useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import classNames from "classnames";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { CollectionListService } from "../../services/CollectionListService";
import { CreatorUserListService } from "../../services/CreatorService";
import { useCurrentUser, useQuery, useRequest, useRequestLoadMore, useScrollToBottomEvent, useVideoSearch } from "../../utils/hooks";
import "./CreatorCenter.scss";
import { CreatorList } from "./CreatorList";
import { VideoCollectionList } from "./VideoCollectionList";
import { LabelItem } from "./VideoEditorModal";
import { VideoData, VideoService } from "../../services/VideoService";
import { Image } from "../../components/Image";
import { StripePayArea } from "../Stripe/Stripe";
import { Carousel } from 'antd';
import { GridView } from "../../components/GridView";
import GlobalConfig from "../../config/GlobalConfig";
import { translate } from "../../components/Text";
import { BannerLoader, LableLoader, VideoLoader } from "../Skelleton";
import { observer } from "mobx-react";
import { mainStore } from "../../stores/MainStore";
import { IconArrowLeft, IconArrowRight } from "../UserCenter/Icons";
import Routes from "../../components/Routes";
import { NavLink } from "react-router-dom";
import VideoAuthLabel from '../../components/VideoAuthLabel';
export { IconSubCount } from "./CreatorList";

export function videoLink(it: any, isCollect?: boolean) {
  let link = isCollect ? `/main/videoPlay/c${it.id}/0` : `/main/videoPlay/${it.type || 'videocreator'}/${it.id}`;
  GlobalConfig.history.push(link)
}

export const ViewBlock: FC<{
  title: string;
  loading?: boolean;
  noData?: boolean;
  noDataTxt?: string;
  onMore?: () => void;
}> = (props) => {
  return (
    <div className="block-view">
      <h1 className="block-header">
        <span>{props.title}</span>
        {props.onMore && (
          <a className="more-btn" onClick={props.onMore}>
            更多
          </a>
        )}
      </h1>
      {props.loading ? <Loading size="mini" /> : props.noData ? <NoData text={props.noDataTxt} /> : props.children}
    </div>
  );
};

const HotCollectionsArea = () => {
  const his = useHistory();
  const [page, setPage] = useState(0)
  const [count, setCount] = useState(5)
  const { loading, data = [] } = useRequest(() =>
    new CollectionListService({ ishot: 1, psize: 999 }).send()
  );
  let list = data.slice(page * count, count * (page + 1));
  return loading ? <GridView id="videoList" space={10} isWidthConstant={true} getColCount={(c: any) => setCount(c)} itemCount={count} itemBuilder={(idx) => <VideoLoader />} /> : (
    <div className="vidoe-coll-area hot-collections">
      <VideoCollectionList list={list}
        onClick={(it) => videoLink(it, true)}
      />

      {data.length > count && <span className="leftArrow " onClick={() => {
        if (page - 1 < 0) {
          return;
        }
        setPage(page - 1);
      }}>{IconArrowLeft}</span>}
      {data.length > count && <span className="rightArrow" onClick={() => {
        if ((page + 1) * count >= data.length) {
          return;
        }
        setPage(page + 1);
      }}>{IconArrowRight}</span>}
    </div>
  );
};

export const AllCollectionsArea = () => {
  const his = useHistory();
  const { reloading, data = [], reload, moreLoad } = useRequestLoadMore(({ pageNum, pageSize }) =>
    new CollectionListService({ p: pageNum, psize: pageSize }).send()
  );
  useEffect(() => reload({}), []);
  useScrollToBottomEvent(moreLoad);
  return reloading ? <GridView id="videoListLoader" space={10} isWidthConstant={true} itemCount={20} itemBuilder={(idx) => <VideoLoader />} /> : (
    <div className="vidoe-coll-area">
      <VideoCollectionList list={data}
        onClick={(it) => videoLink(it, true)}
      />
    </div>
  );
};

export function getAgo(time: string) {
  return moment(time, 'YYYY-MM-DD HH:mm:ss').format('YYYY-MM-DD');
}


const commonNavItems: LabelItem[] = [
  { name: "推荐", id: "recommend", enname: "Recommend", link: '/main/index/recommend' },
  { name: "合集", id: "collections", enname: "Playlist", link: '/main/index/collection' },
];

const CommonVideoList = (props: { list: VideoData[]; loading: boolean; moreLoading: boolean, onClick: (it: VideoData) => void }) => {
  const his = useHistory();
  const user = useCurrentUser();
  return (
    <div className="label-video-list">
      {props.loading ? <GridView id="videoList" space={10} isWidthConstant={true} itemCount={20} itemBuilder={(idx) => <VideoLoader />} /> : props.list.length === 0 ? <NoData text={translate("common.c")} /> :
        <GridView id="videoList" space={10} isWidthConstant={true} itemCount={props.list.length} itemBuilder={(idx) => {
          let it: any = props.list[idx];
          var dec = moment.tz(it.createtime, "Asia/Shanghai");
          return (
            <div className='video-item' key={it.id}>
              <StripePayArea
                price={'0'}
                duration={it.duration}
                objectid={it.id}
                ordertype={3}
                targetInfo={{ title: it.title, avatar: it.avatar, subTitle: it.username }}
                onClick={() => props.onClick(it)}
                onComplete={() => props.onClick(it)}
              >
                <Image className="thumb" src={it.thumb.replace("http://", "https://")} onClick={() => props.onClick(it)} />
                <VideoAuthLabel item={it} />
              </StripePayArea>
              <div className="info" >
                <img className="avatar" src={it.avatar.replace("http://", "https://")} onClick={() => {
                  if (it.type == 'video') {
                    return;
                  }
                  his.push(`/main/userCenter/${it.userid}/videoList`)
                }} />
                <div>
                  <div className="title">{it.title}</div>
                  <div className="author">{it.username}</div>
                  <div className="subInfo">{it.viewcount} {translate('home.video.a')} · {dec.clone().tz("Australia/Sydney").format('YYYY-MM-DD HH:mm')}</div>
                </div>
              </div>

            </div>
          );
        }} />
      }
      {props.moreLoading && <div className="loadingMore"><Loading size="mini" /></div>}
    </div>
  );
};

export const LabelVideoList = () => {
  const { id } = useParams<any>();
  const his = useHistory();
  const { reload, moreLoad, reloading, moreLoading, data = [] } = useRequestLoadMore(
    ({ pageNum, pageSize, labelid = '' }) => new VideoService(pageNum, pageSize, { labelid }).send(),
    { autoLoad: false }
  );
  useEffect(() => {
    reload({ labelid: id }, true);
  }, [id]);
  useScrollToBottomEvent(moreLoad);
  return (
    <CommonVideoList
      list={data}
      loading={reloading}
      moreLoading={moreLoading}
      onClick={videoLink}
    />
  );
};



const AllVideoList = () => {
  const his = useHistory();
  const { data = [], reloading, reload, moreLoad, moreLoading } = useRequestLoadMore(({ pageNum, pageSize }) => new VideoService(pageNum, pageSize).send());
  useEffect(() => {
    reload({});
  }, []);
  useScrollToBottomEvent(moreLoad);
  return (
    <CommonVideoList
      list={data as any[]}
      loading={reloading}
      moreLoading={moreLoading}
      onClick={(it) => videoLink(it)}
    />
  );
};

export const CreatorCenterPage = observer((props: any) => {
  const his = useHistory();
  useVideoSearch((value: any) => his.push(`/main/videoSearch?searchKey=${encodeURIComponent(value)}`));
  return (
    <>
      <div className="nav-area indexTabs" style={{ width: mainStore.tabWidth }}>
        {mainStore.labels.length == 0 && <GridView id="lable" space={10} itemCount={5} itemBuilder={() => <LableLoader />} />}
        {mainStore.labels.length > 0 && commonNavItems.concat(mainStore.labels).map((label, i) => <NavLink key={i} className="nav-item" activeClassName="active" to={label.link || `/main/index/views/${label.id}`}>{mainStore.isEn ? label.enname : label.name}</NavLink>
        )}
      </div>

      <div className="creator-center-page">
        <div className="banner-area">
          {mainStore.banners.length == 0 ? <BannerLoader /> :
            <Carousel autoplay>
              {
                mainStore.banners.map((b, i) => <img key={i} src={b.url.replace("http://", "https://")} alt="" />)
              }
            </Carousel>}
        </div>
        <div className="v-box">
          <Routes routes={props.routes} />
        </div>
      </div>

    </>

  );
});

export const CreatorRecommendPage = () => {
  return (
    <div className="recommend-area">
      <h1>{translate('creator.common.3')}</h1>
      <HotCollectionsArea />
      <h1>{translate('creator.common.4')}</h1>
      <AllVideoList />
    </div>
  );
}

export const CreatorVideoSearchPage = observer(() => {
  const his = useHistory();
  const { searchKey = '' } = useQuery();
  const [tab, setTab] = useState('creator.common.1');
  const { data: videoResult = [], reload: videoLoad, reloading: videoLoading, moreLoading, moreLoad } = useRequestLoadMore((params) => {
    return new VideoService(params['pageNum'], params['pageSize'], { searchkey: searchKey }).send()
  }, { autoLoad: false });
  const { data: collResult = [], load: collLoad, loading: collLoading } = useRequest((searchkey) => new CollectionListService({ searchkey, psize: 999 }).send(), { autoLoad: false });
  const { data: userResult = [], load: userLoad, loading: userLoading } = useRequest((searchkey) => new CreatorUserListService({ searchkey }).send().then(res => res.list));

  useVideoSearch((value: any) => his.push(`/main/videoSearch?searchKey=${encodeURIComponent(value)}`))

  useEffect(() => {
    const search = decodeURIComponent(searchKey);
    if (tab === 'creator.common.1') {
      videoLoad(search);
    } else if (tab === 'creator.common.2') {
      collLoad(search);
    } else {
      userLoad(search);
    }
  }, [searchKey, tab]);
  useScrollToBottomEvent(() => {
    if (tab === 'creator.common.1') {
      moreLoad();
    } else if (tab === 'creator.common.2') {
    } else {
    }
  });

  return (
    <div className="video-search-page" style={{ width: mainStore.tabWidth }}>
      <div className="nav-area">
        {['creator.common.1', 'creator.common.2', 'creator.common.6'].map(k => (
          <a className={classNames("nav-item", { active: k === tab })} onClick={() => setTab(k)}>{translate(k)}</a>
        ))}
      </div>
      <div className="v-box">
        {tab === 'creator.common.1' && (
          videoLoading ? <Loading size="mini" /> : videoResult.length === 0 ? <NoData text={translate("common.c")} /> : <CommonVideoList list={videoResult as any} loading={false} moreLoading={moreLoading} onClick={videoLink} />
        )}
        {tab === 'creator.common.2' && (
          collLoading ? <Loading size="mini" /> : collResult.length === 0 ? <NoData text={translate("common.c")} /> : <VideoCollectionList list={collResult}
            onClick={(it) => videoLink(it, true)}
          />
        )}
        {tab === 'creator.common.6' && (
          userLoading ? <Loading size="mini" /> : userResult.length === 0 ? <NoData text={translate("common.c")} /> : <CreatorList list={userResult} />
        )}
      </div>

    </div>
  );
});