import { FC } from 'react';
import { useHistory } from 'react-router';
import { translate } from '../../components/Text';
import './NavHeader.scss';

export interface NavHeaderProps {
  className?: string;
  onBack?: () => void;
  title: string;
}

export const NavHeader: FC<NavHeaderProps> = ({ children, onBack, title, className = ''}) => {
  const router = useHistory();
  return (
    <div className={`nav-header ${className}`}>
      <a className="back-btn" onClick={onBack ?? (() => router.goBack())}>
        <svg width="20" height="16" viewBox="0 0 20 16" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M5.02774 9.29055H18.727C19.4066 9.29055 19.9574 8.74212 19.9574 8.06712V7.99915C19.9574 7.32415 19.4066 6.77571 18.727 6.77571H4.99727L9.10118 2.65774C9.57461 2.18196 9.57461 1.41555 9.10118 0.939773L9.0918 0.930398C8.61836 0.454617 7.84727 0.449929 7.3668 0.918679L7.35508 0.930398L0.682426 7.62649C0.568718 7.74074 0.504883 7.89538 0.504883 8.05657C0.504883 8.21776 0.568718 8.3724 0.682426 8.48665L1.98789 9.7968L7.24961 15.0726C7.72774 15.5531 8.50821 15.5554 8.99102 15.082L9.00039 15.0726L9.04024 15.0327C9.51602 14.557 9.51602 13.7882 9.04024 13.3124L5.02774 9.29055Z" fill="black"/>
        </svg>
        <span>{translate('creator.playlist.23')}</span>
      </a>
      <div className="header-title">{title}</div>
      {children}
    </div>
  )
}

