import { Ajax } from '../utils/http';

export class CreatorVideoService {
    private data: any;
    constructor(_data: any) {
        this.data = _data;
    }

    public async send(): Promise<CreatorVideoItem[]> {
        let response: CreatorVideoItem[];
        try {
            response = await new Ajax<CreatorVideoItem[]>('/creator/getCreatorVideoList', this.data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export class CreatorCollectionDetailService {
    private collectid?: string;
    constructor(_collectid?: string) {
        this.collectid = _collectid;
    }

    public async send(): Promise<CreatorVideoItem[]> {
        let response: CreatorVideoItem[];
        let data = {
            collectid: this.collectid || '',
            psize: 999,
        }
        try {
            response = await new Ajax<CreatorVideoItem[]>('/creator/getVideoCollectList', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


