
import { Ajax } from '../utils/http';

export class StockSaleService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<SalesData[]> {
        var response: SalesData[];
        var data = {
            symbol: this.symbol,
        }
        response = await new Ajax<SalesData[]>('/short_interest', data, true).get();
        return response;
    }
}
/*tsModel0*/
export interface SalesData {
    date: string;
    symbol: string;
    company_name: string;
    product_class: string;
    reported_gross_short_sales: number;
    issued_capital: number;
    percent_of_issued_capital_reported_as_short_sold: number;
  }
  
  