import ContentLoader from "react-content-loader"
import { SkelletonLoader } from ".";
import './Skelleton.scss';

export const CreatorLoader = (props: any) => (
    <div className="creatorLoader">
        <div>
            <SkelletonLoader
                className='background'
            >
                <rect />
            </SkelletonLoader>
        </div>

        <div className="creatorWrapper">
            <SkelletonLoader className='creator'>
                <circle className="avatar" />
                <rect className="name" />
                <rect className="desc" />
                <rect className="tabs" />
                <rect className="follow" />
                <rect className="search" />
            </SkelletonLoader>
        </div>
    </div>

)

export const CreatorVideoLoader = (props: any) => (
    <div className="creatorLoader">
        <SkelletonLoader
            className="video"
        >
            <rect className="cover" />
            <rect className="title" x="0" y="188" rx="2" ry="2" width="140" height="10" />
            <rect className="name" x="0" y="154" rx="2" ry="2" width="140" height="10" />
        </SkelletonLoader>
    </div>
)


