import { Form, Input, Button, Select, Spin, Checkbox } from 'antd';
import { LoginModel, loginStore, LoginType } from '../../stores/LoginStore';
import './Login.scss';
import { translate } from '../../components/Text';
import { observer } from 'mobx-react';
import { areaCodes } from './AreaCodes';
import { useRequest } from '../../utils/hooks';
import { SendCaptchaService } from '../../services/LoginService';
import { mainStore } from '../../stores/MainStore';
import type { CheckboxChangeEvent } from 'antd/es/checkbox';
import { useEffect, useState } from 'react';
import { LocalStorageService } from '../../utils/storage';
import { userStore } from '../../stores/UserStore';
const { Option } = Select;

interface loginProps {
    close: Function;
    visible?: boolean;
}


export const LoginPage = observer((props: loginProps) => {
    var areacode = "61"
    const [defaultChecked, setDefaultChecked] = useState('');
    const [account, setDefaultAccount] = useState('');
    const form = Form.useForm()[0];
    const { loading, data, load } = useRequest(() =>
        new SendCaptchaService().send()
    );
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 50 }} defaultValue="61" onChange={(e) => {
                areacode = e;
            }}>
                {areaCodes.map((e, i) => <Option key={i} value={e.areacode}>+{e.areacode}</Option>)}
            </Select>
        </Form.Item>
    );
    const onCheckChange = (e: CheckboxChangeEvent) => {
        if (e.target.checked) {
            LocalStorageService.set('_remember', '1')
        } else {
            LocalStorageService.set('_remember', '0');
        }
    };
    useEffect(() => {
        let _remember = LocalStorageService.get("_remember") || '0';
        let _account = LocalStorageService.get("_account") || '';
        setDefaultAccount(_account);
        setDefaultChecked(_remember);
    }, [userStore.loginModalVisible]);
    return (
        <div className="info">
            {defaultChecked && <Form
                name="normal_login"
                form={form}
                className="login-form"
                initialValues={{ email: account }}
                onFinish={(v) => {
                    if (loginStore.loginType == LoginType.phone) {
                        v.areacode = areacode;
                    }
                    loginStore.login(v, () => {
                        props.close();
                        let _remember = LocalStorageService.get("_remember") || '0';
                        if (_remember == '1') {
                            LocalStorageService.set('_account', v.email);
                        } else {
                            LocalStorageService.remove("_account");
                        }
                        if (loginStore.fromIndex) {
                            loginStore.fromIndex = false;
                            mainStore.menuKey = 0;
                            window.location.href = "main.html#/main/index/recommend"
                        } else {
                            window.location.reload();

                        }
                    })
                }}
            >
                {loginStore.loginType == LoginType.email ? <div>
                    <Form.Item
                        name="email"
                        rules={[{ required: true, message: translate('mine.login.c') }]}
                    >
                        <Input placeholder={translate('mine.login.c')}
                        />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        requiredMark={false}
                        rules={[{
                            required: true,
                            message: translate('mine.login.e')
                        }]}
                    >
                        <Input
                            type="password"
                            autoComplete="new-password"
                            placeholder={translate('mine.login.e')}
                        />
                    </Form.Item>
                    <Form.Item
                        name="temp"
                        style={{ position: 'absolute', zIndex: -1 }}
                    >
                        <Input
                            type="password"
                        />
                    </Form.Item>
                </div> :
                    <div>
                        <div className="phone-type phone">
                            <Form.Item
                                name="phone"
                                rules={[{
                                    required: true,
                                    pattern: /^(4|04)\d{8}$/,
                                    message: translate('mine.register.j')
                                }]}
                            >
                                <Input addonBefore={prefixSelector} placeholder={translate('mine.login.i')} />
                            </Form.Item>
                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="captcha"
                                rules={[{ required: true, message: translate('mine.login.j') }]}
                            >

                                <Input placeholder={translate('mine.login.j')} />
                            </Form.Item>
                            <div className="captchaImg forget-form-button" onClick={() => {
                                load({});
                            }} >
                                {loading ? <Spin spinning={true} size='small'>
                                </Spin> : <img src={data?.content} className="forget-form-button" />}
                            </div>

                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="code"
                                rules={[{ required: true, message: translate('mine.login.l') }]}
                            >
                                <Input placeholder={translate('mine.login.l')} />
                            </Form.Item>
                            <Button htmlType="submit" className={`forget-form-button ${((loginStore.countDwon < 60 && loginStore.countDwon > 0) ||loginStore.sending) ?'':'send-button'}`} onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                loginStore.sendPhone(form.getFieldsValue(["phone"]).phone, 3, areacode, data?.id || '', form.getFieldsValue(["captcha"]).captcha)
                            }}>
                                {translate(loginStore.sendBtnText)}
                            </Button>
                        </div>
                    </div>}
                <div className="form-wrapper">
                    <Checkbox defaultChecked={defaultChecked == '1'} onChange={onCheckChange}>{translate('login.j')}</Checkbox>
                    <a className="login-form-forgot" href="javascript:void(0);" onClick={() => {
                        if (loginStore.loginLoading) return;
                        loginStore.setLoginModel(LoginModel.forgetPassword)
                    }}>
                        {translate('mine.login.f')}
                    </a>

                </div>
                <Form.Item>
                    <Button htmlType="submit" className="login-form-button">
                        {translate('mine.login.h')}
                    </Button>
                </Form.Item>
            </Form>}
            <div className="sign-up">
                <span>{translate('login.k')}</span>
                <a href="javascript:void(0);" onClick={() => {
                    if (loginStore.loginLoading) return;
                    loginStore.setLoginModel(LoginModel.register)
                }}>
                    {translate('login.l')}
                </a>
            </div>
        </div>
    );
})













