import { Form, FormItemProps, Input, message, Select, Radio, Tooltip } from "antd";
import { Modal, Button, formatSize, formatCount } from '../UserCenter/Common';
import { useForm } from "antd/lib/form/Form";
import { FieldData } from "rc-field-form/es/interface";
import { FC, useEffect, useState } from "react";
import { ImageUploader } from "../../components/ImageUploader";
import { showError, showSuccess } from "../../components/Message";
import { CollectionItem } from "../../services/CollectionListService";
import { CreatorVideoItem } from "../../services/CreatorVideoService";
import { CreateCollectService, PublishVideoService } from "../../services/PublishService";
import { useRequest, useTranslator } from "../../utils/hooks";
import { Ajax } from "../../utils/http";
import './VideoEditorModal.scss';
import { IconDelete, IconEdit, IconTips } from "../UserCenter/Icons";
import { px } from "../../utils/rem";
import { mainStore } from "../../stores/MainStore";
import imgTips from '../../images/main/tips.png';

// const FormItem = Form.Item;

function submitForm(val: any) {
  return new PublishVideoService(val)
    .send()
    .then(() => {
      showSuccess(mainStore.isEn ? 'Submission done!' : '视频发布完成');
    })
    .catch((err) => {
      message.error(err);
      return Promise.reject(err);
    });
}

export function getLabels() {
  return new Ajax<LabelItem[]>('/creator/videoLabelList', {}).post();
}

export interface LabelItem {
  id: string;
  name: string;
  enname: string;
  link?: string;
}

export interface VidoeEditModalProps {
  mode?: 'edit' | 'add';
  visible: boolean;
  currentItem?: CreatorVideoItem;
  collections?: CollectionItem[];
  onClose: () => void;
  onComplete?: () => void;
}

const keys: Array<keyof CreatorVideoItem> = ['title', 'price', 'thumb', 'abstract', 'collectid', 'labelid'];

const FormItem: FC<FormItemProps & { other?: any }> = ({ label, other, ...rest }) => {
  return (
    <>
      <div className="editTitle">
        <div className="text">{label ? `${label}:` : ''}</div>
        <div className="other">
          {other}
        </div>
      </div>
      <div className="editInput">
        <Form.Item  {...rest} />
      </div>
    </>
  )
}

/**
 * 组件: 视频编辑弹窗
 */
export function VidoeEditModal({ mode = 'edit', visible, currentItem, onClose, onComplete }: VidoeEditModalProps) {
  const [form] = useForm<CreatorVideoItem>();
  const { translate } = useTranslator();
  const { data: labels } = useRequest(getLabels);
  const [free, setFree] = useState(false);
  const [showPrice, setShowPrice] = useState(false);
  const [auth, setAuth] = useState<string>('');

  const { loading, loadWithPromise: submit } = useRequest(submitForm, { autoLoad: false });
  const doSubmit = (data: any) => {
    const params = mode === 'add' ? { ...currentItem, ...data } : { id: currentItem?.id, ...data };
    let values = JSON.parse(JSON.stringify(params));
    if (auth == 'price') {
      values.isfree = 0;
    } else if (auth == 'free') {
      values.isfree = 0;
      values.price = '0';
    } else {
      values.isfree = 1;
    }
    console.log("values.tryminute",values.tryminute)
    if (values.tryminute) {
      let duration = Math.floor((parseFloat(currentItem?.duration || '0')) / 60);
      if (parseInt(values.tryminute) > duration) {
        showError(translate('creator.myVideos.59'));
        return;
      }
    }
    if (values.thumb.indexOf("?") > -1) {
      delete values.thumb;
    }
    submit({
      ...values,
      labelid: (values.labelid ?? []).join(','),
      price: values.price
    }).then(() => {
      onComplete && onComplete();
      onClose();
    });
  };
  const onValueChange = (fields: FieldData[]) => {
    const f = fields.find(it => JSON.stringify(it.name).indexOf('collectid') > -1);
    if (f) {
      setShowPrice(!f.value || parseInt(f.value ?? '0', 10) <= 0);
    }
  }
  useEffect(() => {
    setAuth('');
    if (visible && currentItem) {
      form.setFields(keys.map(k => ({
        name: k,
        value: k === 'labelid' ? (currentItem[k] ?? '').split(',').filter(c => !!c) : currentItem[k]
      })));
      setShowPrice(parseInt(currentItem.collectid ?? '0', 10) <= 0);
      setFree(parseFloat(currentItem.price) <= 0);
      console.log("parseFloat(currentItem.price)", parseFloat(currentItem.price))
      if (parseFloat(currentItem.price) > 0) {
        let _auth = currentItem.isfree == '0' ? 'price' : 'member';
        setAuth(_auth);
      } else {
        let _auth = currentItem.isfree == '0' ? 'free' : 'member';
        setAuth(_auth);
      }
    }
  }, [visible]);
  console.log("authttt ", auth);
  return (
    <Modal width={px(510)} title={translate('creator.myVideos.5')} visible={visible} onCancel={onClose} footer={false}>
      <Form form={form} labelCol={{ span: 4 }} className="video-edit-modal"
        onFinish={doSubmit}
        onFieldsChange={onValueChange}>
        <FormItem label={translate('creator.myVideos.6')} name="title">
          <Input />
        </FormItem>
        <FormItem label={translate('creator.myVideos.7')} name="abstract">
          <Input.TextArea maxLength={140} />
        </FormItem>
        <FormItem label={translate('creator.myVideos.8')} name="labelid" initialValue={[]}>
          <Select mode="multiple">
            {(labels ?? []).map(l => <Select.Option key={l.id} value={l.id}>{l.name}</Select.Option>)}
          </Select>
        </FormItem>
        {showPrice && auth && (
          <div className="authContent">
            <div className="info">
              <div className="text">{translate('creator.myVideos.9')}:</div>
              <Radio.Group defaultValue={auth} onChange={(v) => {
                setAuth(v.target.value);
              }} buttonStyle="solid">
                <Radio.Button value="price">{translate('creator.myVideos.11')}</Radio.Button>
                <Radio.Button value="free">{translate('creator.myVideos.10')}</Radio.Button>
                <Radio.Button value="member">{translate('creator.myVideos.52')}</Radio.Button>
              </Radio.Group>
            </div>
            {auth != 'free' && <div className="auth">
              <div className="setPrice">
                <div className="leftText">
                  {translate('creator.myVideos.44')}
                </div>
                <Form.Item name="price">
                  <Input placeholder={(currentItem?.price || '0')} />
                </Form.Item>
              </div>
              <div className="setPrice">
                <div className="leftText">
                  {translate('creator.myVideos.46')}
                </div>
                <Form.Item name="tryminute">
                  <Input placeholder={currentItem?.tryminute || '0'} />
                </Form.Item>
              </div>
            </div>}

          </div>
        )}
        <FormItem label={translate('creator.myVideos.38')} name="thumb" className="thumb-area">
          <ImageUploader width={240} height={135} />
        </FormItem>
        <div className="other-info">
          {translate('creator.myVideos.13')}: <span className="gray">{currentItem?.updatetime ?? currentItem?.createtime}</span><br />
          <span className="gray">{formatCount(parseInt(currentItem?.viewcount ?? '0', 10))}</span> {translate('creator.myVideos.2')}
          <span className="gray">{formatCount(parseInt(currentItem?.likecount ?? '0', 10))}</span> {translate('creator.myVideos.14')}
        </div>
        <div className="oCenter">
          <Button htmlType="submit" className="submit-btn" size="large" loading={loading}>{translate('creator.myVideos.15')}</Button>
        </div>
      </Form>
    </Modal>
  );
}

export interface VidoeCollectEditModelProps {
  onComplete: () => void;
  onClose: () => void;
  current?: CollectionItem;
  visible: boolean;
}

const createColltion = (data: any) => new CreateCollectService(data).send();

export function VidoeCollectEditModel({ visible, current, onComplete, onClose }: VidoeCollectEditModelProps) {
  const [free, setFree] = useState(false);
  const { translate } = useTranslator();
  const { loadWithPromise: addColl, loading: collSubmiting } = useRequest(createColltion, { autoLoad: false });
  const [auth, setAuth] = useState<string>('');
  const [collForm] = useForm();

  const doSubmitColl = (data: any) => {

    let values = JSON.parse(JSON.stringify(data));
    values.isfree = 0;
    if (auth == 'price') {
      delete values.isfree;
    } else if (auth == 'free') {
      values.price = '0';
    } else {
      values.isfree = 1;
    }


    addColl(current ? { ...values, id: current.id } : values).then(() => {
      collForm.resetFields();
      showSuccess(mainStore.isEn ? 'Success' : (current ? '修改完成' : "创建完成"));
      onClose();
      onComplete();
    })
      .catch((e) => {
        // debugger;
        showError(e.message);
      })
  }

  useEffect(() => {
    setAuth('');
    if (current) {
      collForm.setFieldsValue({ ...current });
      setFree(parseFloat(current.price) <= 0);
      if (parseFloat(current.price) > 0) {
        let _auth = current.isfree == '0' ? 'price' : 'member';
        setAuth(_auth);
      } else {
        let _auth = current.isfree == '0' ? 'free' : 'member';
        setAuth(_auth);
      }
    } else {
      setAuth('price');
    }
  }, [current]);
  return (
    <Modal title={translate('creator.playlist.5')} width={px(mainStore.isEn ? 580 : 530)} visible={visible} onCancel={onClose} footer={null}>
      <div className="video-edit-modal">
        {/* <VideoCollectForm onComplete={() => { setVisibleModel(false); load({ id }); }} /> */}
        <Form form={collForm} onFinish={doSubmitColl}>
          <FormItem label={translate('creator.playlist.6')} name="name">
            <Input />
          </FormItem>
          <FormItem label={translate('creator.playlist.16')} name="describe">
            <Input.TextArea maxLength={140} />
          </FormItem>
          {auth && <div className="editTitle">
            <div className="text">{translate('creator.playlist.7')}</div>
            <div className="other">
              <Radio.Group defaultValue={auth} onChange={(v) => {
                setAuth(v.target.value);
              }} buttonStyle="solid">
                <Radio.Button value="price">{translate('creator.myVideos.11')}</Radio.Button>
                <Radio.Button value="free">{translate('creator.myVideos.10')}</Radio.Button>
                <Radio.Button value="member">{translate('creator.myVideos.52')}</Radio.Button>
              </Radio.Group>

              {/* <div className="toggle" onClick={() => {
                setFree(!free)
              }}>
                {free && <div className="txtLeft">{translate('creator.playlist.15')}</div>}
                <div className="round"></div>
                {!free && <div className="txtRight">{translate('creator.playlist.8')}</div>}
              </div> */}
            </div>
          </div>}
          <div className="editInput">
            {auth != 'free' && (
              <div className="authContent">
                <div className="setPrice">
                  <div className="leftText">
                    {translate('creator.playlist.9')} ($)
                  </div>
                  <Form.Item name="price">
                    <Input placeholder={current?.price || '0'} />
                  </Form.Item>
                </div>
                <div className="setPrice" style={{ width: px(mainStore.isEn ? 290 : 240) }}>
                  <div className="leftText" style={{ width: px(mainStore.isEn ? 180 : 130) }}>
                    <Tooltip title={translate(`creator.myVideos.56`)}>
                      <Button ><img src={imgTips} alt="" /> {translate('creator.playlist.10')}  </Button>
                    </Tooltip>
                  </div>

                  <Form.Item name="trycount">
                    <Input placeholder={current?.trycount || '0'} />
                  </Form.Item>
                </div>
              </div>
            )}
          </div>
          <FormItem label={translate('creator.playlist.11')} name="cover" className="thumb-area">
            <ImageUploader width={240} height={135} />
          </FormItem>
          <div className="oCenter">
            <Button htmlType="submit" className="submit-btn" loading={collSubmiting}>{translate('creator.playlist.12')}</Button>
          </div>
        </Form>
      </div>
    </Modal>
  );
}