
import './News.scss';
import classNames from 'classnames';
import { useEffect, useRef, useState } from 'react';
import { px } from '../../utils/rem';
import { useTranslator } from '../../utils/hooks';

export interface OliItemProps {
    title: string;
    content: string;
    url?: string;
    source: string;
    createtime: string;
    image?: string;
    history?: any;
    useHtml?: boolean
}

export const OliItem = (props: OliItemProps) => {
    const [needFold, setNeedFold] = useState(false);
    const [fold, setFold] = useState(true);
    const txtCxtRef = useRef<HTMLDivElement>(null);
    const { translate } = useTranslator();

    useEffect(() => {
        setNeedFold(!!txtCxtRef.current && txtCxtRef.current.offsetHeight > px(110));
    }, [props.content]);

    const conentDom = props.useHtml && !props.url ? <div dangerouslySetInnerHTML={{ __html: props.content }}>
    </div> : <div className="content" >
        <div className="text" >
            {props.content}
        </div>
    </div>;

    return (
        <div className="OliItem">
            <div className="newsTitle">{props.title}</div>
            <div className={classNames('middle', { 'no-fold': needFold && !fold })}>
                {conentDom}
            </div>
            <div className="test-content" ref={txtCxtRef}>
                {conentDom}
            </div>
            <div className="date flex-between">
                <div className="time">
                    {props.createtime.slice(5, 10).replace("-", '/')} {props.createtime.slice(11, 16)}
                </div>
                {needFold && <a className="fold-btn" onClick={() => setFold(!fold)}>{fold ? translate('common.b') : translate('common.d')}</a>}
            </div>
        </div>
    );
}
