import { Charts } from "./Charts/Charts";
import { useEffect } from "react";
import { observer, } from "mobx-react";
import { FullScreenCharts } from "./Charts/FullScreenChart";
import chartStore from "../../../stores/ChartStore";
import './StockPage.scss';
import { userStore } from "../../../stores/UserStore";
import { Loading_icon } from "../../../images/Loading";
import NoData from "../../../components/NoData";
import { Icon_pointer } from "../../../images/market/pointer";
import { Icon_circle } from "../../../images/market/circle";
import { Icon_bear } from "../../../images/market/bear";
import { Icon_bull } from "../../../images/market/bull";
import Translate from "../../../components/Text";
import { mainStore } from "../../../stores/MainStore";
import statisticStore from "../../../stores/StatisticStore";
import { isMobile } from "../../../utils/globalFuncs";

export const StockPage = observer((props: any) => {
  let symbol = props.match.params.symbol;
  let strategy = props.match.params.strategy;
  useEffect(() => {
   
    statisticStore.symbol = symbol;
    chartStore.getData(symbol, strategy);

  }, []);
  let rules = userStore.userInfo?.rules || [];
  var subscribed = rules?.indexOf('technicalForm') > -1;
  var supportResistance = chartStore.patternData?.support_resistance || []

  var patterns = chartStore.patternData?.patterns || [];
  var quotaAbove = chartStore.quotaAbove || [];
  var quotaBelow = chartStore.quotaBelow || [];
  return (
    <div className="StockWrapper">
      {chartStore.pending ? <div className="loading" >
        <img src={Loading_icon} alt="" />
      </div> :
        <div>
          <div className="chartContent">
            <div className="title"><Translate text="markets.technic.d" /></div>
            <div className="wrapper">
              <div className="leftContent">
                <Charts />
                <div className="resistance">
                  <div className="name">
                    <Translate text="markets.technic.a" />
                  </div>
                  <div className="list">
                    {supportResistance.map((r, i) => <div key={i} className={chartStore.resistanceValue == r ? "active" : "item"} onClick={() => {
                      chartStore.drawResistance(r, chartStore.patternData?.support_resistance_start_date || '');
                    }}>
                      {r.toFixed(2)}
                    </div>)}
                  </div>
                </div>
              </div>
              <div className="rightContent">
                <div className="info">
                  <div className="item"><Translate text="markets.technic.b" /></div>
                  <div className="item"><Translate text="markets.technic.c" /></div>
                </div>
                {patterns.length ? <div className="patterns">
                  {patterns.map((p, i) => <div key={i} className={chartStore.strategy == (p.strategy_name + p.begin_date + p.end_date) ? 'active' : 'item'} onClick={() => {
                    chartStore.addAnnotation(p.strategy_name, p);
                  }}>
                    <div className="icon"><img src={`https://aolinews.oss-accelerate.aliyuncs.com/patterns/${p.strategy_name}.png`} alt="" /></div>
                    <div className="name">{mainStore.isEn ? p.strategy_name_display : p.strategy_name_ch}</div>
                    <div className="date">{p.begin_date} - {p.end_date}</div>
                  </div>)}
                </div> : <NoData />}
              </div>
            </div>


          </div>
          <div className="rankContent">
            <div className="title"><Translate text="markets.technic.e" /></div>
            {!subscribed ? <NoData text={mainStore.isEn ? 'VIP only' : '订阅后可查看'} /> :

              (quotaAbove.length == 0 && quotaBelow.length == 0) ? <NoData /> :
                <div className="contentWrapper">
                  <div className="leftContent">
                    {['markets.quota.b', 'markets.quota.a'].map((t, index) => {
                      var list = (index == 0 ? quotaAbove : quotaBelow);
                      return <div key={index} className="data">
                        <div className="name">
                          <Translate text={t} />
                        </div>
                        {list.length > 0 ? <div className="list">
                          {list.map((q: any, i) => {
                            return <div className="item" key={i}>
                              <div className="text">{mainStore.isEn ? q.en : q.ch}</div>
                              <div className="progress">
                                <div className="value" style={{ width: `${q.rank * 100}%`, background: index == 0 ? "#61DBAD" : "#5B8FF9" }}></div>
                              </div>
                              <div className="bottom">
                                <div>{q.ratio}</div>
                                <div>{(q.rank * 100).toFixed(2)}%</div>
                              </div>
                            </div>
                          })}
                        </div> : <NoData />}

                      </div>
                    })}

                  </div>

                  <div className="rightContent">
                    <div className="circle">
                      <img className="rate" src={Icon_circle} alt="" />
                      <img className="pointer" style={{ transform: `rotate(${90 * chartStore.bullCircleRate}deg) translateX(-50%)` }} src={Icon_pointer} alt="" />
                    </div>
                    <div className="rateList">
                      <div className="bear">
                        <div className="text">
                          <Translate text="markets.quota.a" />

                        </div>
                        <div className="value">  <img src={Icon_bear} alt="" />  {chartStore.bearRate}%</div>
                      </div>
                      <div className="bull">
                        <div className="text">
                          <Translate text="markets.quota.b" />
                        </div>
                        <div className="value"><img src={Icon_bull} alt="" /> {chartStore.bullRate}%</div>
                      </div>
                    </div>

                    <div className="info">
                      <div className="word">
                        <Translate text="markets.quota.c" args={
                          {
                            symbol: chartStore.quotaSummery?.symbol?.split('_')[0] || 'NaN',
                            sector: chartStore.quotaSummery?.tag || 'NaN'
                          }
                        } />
                      </div>

                      <div className="word">
                        <Translate text="markets.quota.d" args={
                          {
                            nbAbove: chartStore.quotaSummery?.nb_above || 'NaN',
                            nbBelow: chartStore.quotaSummery?.nb_below || 'NaN'
                          }
                        } />
                      </div>

                    </div>
                  </div> :</div>}
          </div>
        </div>
      }

    </div>
  );
});
