import { makeAutoObservable, } from "mobx";
import { showError, showLoading, showSuccess } from "../components/Message";
import { CommentItem, CommentListService, CommentService } from "../services/CommentService";
import { userStore } from "./UserStore";

class CommentStore {
  public loading: boolean = false;
  public loadingMore: boolean = false;
  public commentting: boolean = false;
  public commentList: CommentItem[] = [];
  public detail?: CommentItem;
  public order: string = 'likecount';
  public objectid?: string;
  public objecttype?: number;
  public commentContent?: string;
  private _lastid?: string;
  private _page: number = 1;
  private noMore: boolean = false;


  constructor() {
    makeAutoObservable(this);
  }

  public async getData(loadmore?: boolean) {

    if (this.loading || this.loadingMore) {
      return;
    }
    let response: Array<CommentItem>;
    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this._page = 1;
      this.loading = true;
    }

    try {
      response = await new CommentListService(this.objectid || '0', this.objecttype || 5, this.order, this._page).send();
    } catch (e: any) {
      this.loading = false;
      return;
    }
    this.loading = false;
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length === 0;
      this.commentList.push(...response);
    } else {
      this.commentList = response;
    }

  }

  async comment() {
    if (!this.commentContent || this.commentting) {
      return;
    }
    this.commentting = true;;
    let res;
    try {
      res = await new CommentService(this.objectid || '0', this.objecttype || 5, this.commentContent || '').send();
    } catch (e) {
      showError(`${e}`);
      this.commentting = false;
      return;
    }
    let _list = JSON.parse(JSON.stringify(this.commentList));
    var Dates = new Date();
    var Y = Dates.getFullYear();
    var M = Dates.getMonth() + 1;
    var D = Dates.getDate();
    var H = Dates.getHours();
    var Mu = Dates.getMinutes();
    var times = Y + (M < 10 ? "-0" : "-") + M + (D < 10 ? "-0" : "-") + D + ' ' + (H < 10 ? "0" : "") + H + (Mu < 10 ? ":0" : ":") + Mu;
    let _resultList = [{
      id: res,
      userid: userStore.userInfo?.id,
      username: userStore.userInfo?.username,
      userimg: userStore.userInfo?.avatar,
      content: this.commentContent,
      createtime: times,
      likecount: '0',
      islike: 0
    }];
    _resultList.push(..._list);
    this.commentList = _resultList;
    this._lastid = this.commentList[this.commentList.length - 1].id;
    this.commentting = false;
    var input: any = document.getElementById('commentInput');
    input.value = '';
    this.commentContent = '';
  }
}

const commentStore = new CommentStore();

export { commentStore };