import { Form, Input, Button, Select } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined, PhoneOutlined } from '@ant-design/icons';
import { LoginModel, loginStore, RegisterType } from '../../stores/LoginStore';
import { observer } from 'mobx-react';
import './Login.scss';
import { areaCodes } from './AreaCodes';
import { translate } from '../../components/Text';
import { Icon_password } from '../../images/index/password';
import { Icon_captcha } from '../../images/index/captcha';
import { useEffect, useState } from 'react';
import GlobalConfig from '../../config/GlobalConfig';
import { Spin } from 'antd';
import { useRequest } from '../../utils/hooks';
import { CaptchaResponse, SendCaptchaService } from '../../services/LoginService';
import { randomWord } from '../../utils/globalFuncs';

const { Option } = Select;
interface registerProps {
    close: Function;
}

export const RegisterPage = observer((props: registerProps) => {
    var areacode = "61";
    const { loading, data, load } = useRequest(() =>
        new SendCaptchaService().send()
    );
    const form = Form.useForm()[0];
    useEffect(() => {
        load({});
    }, [])
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }} defaultValue="61" onChange={(e) => {
                areacode = e;
            }}>
                {areaCodes.map((e, i) => <Option key={i} value={e.areacode}>+{e.areacode}</Option>)}
            </Select>
        </Form.Item>
    );
    return (
        <div className="info">
            <Form
                name="normal_login"
                form={form}
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={(v) => {
                    delete v.captcha;
                    if (loginStore.registerType == RegisterType.phone) {
                        v.areacode = areacode;
                    }
                    v.username = randomWord(10);
                    loginStore.register(v, () => {
                        props.close();
                        if (loginStore.fromIndex || document.referrer !== '') {
                            loginStore.fromIndex = false;
                            window.location.href = "main.html#/main/index/recommend"
                        } else {
                            GlobalConfig.history.goBack();
                        }
                    })
                }}
            >
                {loginStore.registerType == RegisterType.email ? <div>
                    <Form.Item
                        name="email"
                        rules={[{
                            required: true,
                            pattern: /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/,
                            message: translate('mine.register.d')
                        }]}
                    >
                        <Input placeholder={translate('mine.register.c')} />
                    </Form.Item>
                    <div className="captcha">
                        <Form.Item
                            className="codeInput"
                            name="code"
                            rules={[{ required: true, message: translate('mine.login.k') }]}
                        >
                            <Input
                                placeholder={translate('mine.login.k')} />
                        </Form.Item>
                        <Button htmlType="submit" className={`forget-form-button ${((loginStore.countDwon < 60 && loginStore.countDwon > 0) ||loginStore.sending) ?'':'send-button'}`} onClick={() => { loginStore.sendEmial(form.getFieldsValue(["email"]).email, 2) }}>
                            {translate(loginStore.sendBtnText)}
                        </Button>
                    </div>
                    <Form.Item
                        name="password"
                        rules={[{
                            required: true,
                            pattern: /^(?:(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])).{8,15}$/,
                            message: translate('mine.register.f')
                        }]}
                    >
                        <Input
                            type="password"
                            placeholder={translate('mine.register.e')}
                        />
                    </Form.Item>
                </div>
                    :
                    <div>
                        <div className="phone-type phone">
                            <Form.Item
                                name="phone"
                                rules={[{
                                    required: true,
                                    pattern: /^(4|04)\d{8}$/,
                                    message: translate('mine.register.j')
                                }]}
                            >
                                <Input addonBefore={prefixSelector} placeholder={translate('mine.register.i')} />
                            </Form.Item>
                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="captcha"
                                rules={[{ required: true, message: translate('mine.login.j') }]}
                            >

                                <Input placeholder={translate('mine.login.j')} />
                            </Form.Item>
                            <div className="captchaImg forget-form-button" onClick={() => {
                                load({});
                            }} >
                                {loading ? <Spin spinning={true} size='small'>
                                </Spin> : <img src={data?.content} className="forget-form-button" />}
                            </div>

                        </div>
                        <div className="captcha phone-type">
                            <Form.Item
                                className="codeInput"
                                name="code"
                                rules={[{ required: true, message: translate('mine.login.l') }]}
                            >
                                <Input placeholder={translate('mine.login.l')} />
                            </Form.Item>
                            <Button htmlType="submit" className={`forget-form-button ${((loginStore.countDwon < 60 && loginStore.countDwon > 0) ||loginStore.sending) ?'':'send-button'}`} onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                loginStore.sendPhone(form.getFieldsValue(["phone"]).phone, 1, areacode, data?.id || '', form.getFieldsValue(["captcha"]).captcha)
                            }}>
                                {translate(loginStore.sendBtnText)}
                            </Button>
                        </div>
                    </div>}

                <div>
                    <Button htmlType="submit" className="login-form-button">
                        {translate('mine.register.g')}
                    </Button>
                    <div className="form-wrapper">
                        <div className="toLogin"><span>{translate('mine.register.h')}</span><span className="loginLabel " onClick={() => {
                            if (loginStore.registerLoading) return;
                            loginStore.setLoginModel(LoginModel.login)
                        }}> {translate('mine.login.h')}</span></div>
                    </div>
                </div>
            </Form>
        </div>
    );
})













