import { Ajax } from "../utils/http";

export class CommunityService {
    private data: any;
    constructor(_data: any) {
        this.data = _data;
    }

    public async send(): Promise<Array<CommunityData>> {
        let response: Array<CommunityData>;
        try {
            response = await new Ajax<Array<CommunityData>>('/community/dynamicList', this.data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export class CommunityDetailService {
    private id: string;
    private creatorid: string;
    constructor(_id: string,_creatorid: string) {
        this.id = _id;
        this.creatorid = _creatorid;
    }

    public async send(): Promise<CommunityData> {
        let response: CommunityData;
        let data = {
            id: this.id,
            creatorid: this.creatorid,
            p:1,
            psize:1
        }
        try {
            response = await new Ajax<CommunityData>('/community/dynamicList', data).post();
        } catch (e) {
            throw e;
        }
        return response[0];
    }
}


export interface CommunityData {
    id: string;
    userid: string;
    images: string;
    content: string;
    visibletype: string;
    visibleuserid: string;
    likecount: string;
    commentcount: string;
    createtime: string;
    islike: number;
    avatar: string;
    username: string;
    isfollow: number;
}
