import './Research.scss';
import { ResearchDetailStore } from '../../stores/ResearchDetailStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Icon_Back } from '../../images/back';
import { Loading_icon } from '../../images/Loading';
import { Icon_collect_on } from '../../images/market/collectOn';
import { Icon_collect } from '../../images/market/collect';

import { Pagination } from 'antd';

import { Select } from 'antd';
import { Icon_Scale_Up } from '../../images/ScaleUp';
import { Icon_Scale_Down } from '../../images/ScaleDown';
import { isMobile, verifyRule } from '../../utils/globalFuncs';
import { LocalStorageService } from '../../utils/storage';
import { LockView } from '../../components/Lock';

const { Option } = Select;

const scaleRange = ['100%', '125%', '150%', '180%', '200%', '225%', '250%'];

let researchDetailStore: ResearchDetailStore = new ResearchDetailStore();
export const ResearchDetail = observer((props: any) => {
    let type = props.match.params.type;
    let id = props.match.params.id;
    useEffect(() => {
        if (isMobile()) {
            window.location.href = `https://mobile.oliview.com.au/#/ResearchDetail?id=${id}`;
            return;
        }
        researchDetailStore.getData(id);
    }, [id]);
    let isVip = verifyRule('researchReport', undefined, true)
    return (
        <div className="researchDetailWrapper">
            <div style={{ height: "100%", filter: isVip || researchDetailStore.loading || researchDetailStore.detail?.isfree == '0' ? 'none' : 'blur(10px)' }}>
                <div className="tabs">

                    <img className="back" src={Icon_Back} alt="" onClick={() => {
                        props.history.push(`/main/news/research/${type}`);
                    }} />

                    {!researchDetailStore.loading && <div className="tools"><Pagination simple defaultCurrent={1} onChange={(page) => {
                        var researchDetailContent: any = document.getElementsByClassName('researchDetailContent')[0];
                        researchDetailContent.scrollTo(0, 0)
                        researchDetailStore.showPdf(page);
                    }} total={researchDetailStore.totlaPage} pageSize={1} />

                        <div className="scales">
                            <img src={Icon_Scale_Down} alt="" onClick={() => researchDetailStore.scalePdf(false)} />
                            <div className="divider"></div>
                            <img src={Icon_Scale_Up} alt="" onClick={() => researchDetailStore.scalePdf(true)} />
                        </div>
                        <Select value={researchDetailStore.scaleText} onChange={(value: string) => {
                            researchDetailStore.setScale(value);
                        }} style={{ width: 100 }} >
                            {scaleRange.map((s) => <Option value={s}>{s}</Option>)}
                        </Select>
                    </div>}

                    <div className="items">
                        <img className='follow' src={researchDetailStore.detail?.isfollow == 1 ? Icon_collect_on : Icon_collect} alt="" onClick={() => {
                            researchDetailStore.follow();
                        }} />
                    </div>
                </div>
                {researchDetailStore.loading ? <div className="loading" >
                    <img src={Loading_icon} alt="" />
                </div> : <div className="researchDetailContent" >

                    <div className="pdfWrapper">
                        <div className="pdf" >
                            <div id="viewerContainer" >
                                <div className="viewWrapper">
                                    <canvas id="viewer" className="pdfViewer"></canvas>

                                </div>
                                {/* <div id="viewer" className="pdfViewer"></div> */}

                            </div>
                        </div>
                    </div>

                </div>}
            </div>

            {!isVip && !researchDetailStore.loading && researchDetailStore.detail?.isfree == '1' && <LockView />}

        </div>
    )
});