import { FC, useEffect, useState } from "react"
import { useParams } from "react-router";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { translate } from "../../components/Text";
import { CreatorFansService, CreatorSubscribeListService, CreatorSubscribeService, CreatorUser } from "../../services/CreatorService";
import { useCurrentUser, useRequest } from "../../utils/hooks";
import { IconChecked } from "./Icons";
import { userStore } from "../../stores/UserStore";
import { PropTypes } from "mobx-react";

const getFollowing = () => new CreatorSubscribeListService({ userid: userStore.userInfo?.id, psize: 999 }).send()

const getFollowers = () => new CreatorFansService({ userids: userStore.userInfo?.id, psize: 1 }).send();

const followUserRequest = ({ id = "", status = 1 }) => new CreatorSubscribeService({ objectid: id, objecttype: 1, status: status }).send();
const FollowerList: FC<{ list: CreatorUser[], isFans?: boolean }> = ({ list, isFans }) => {
  const [unFollowList, setUnFollowList] = useState<string[]>([]);
  const { load: followUser, loading: following } = useRequest(followUserRequest, { autoLoad: false });
  return (
    <div className="follower-list">
      {list.map(it => {
        let isFollow = unFollowList.indexOf(it.id) == -1;

        return (
          <div className="follower-item" key={it.id} >
            <img className="avatar" src={it.avatar} onClick={() => {
              GlobalConfig.history.push(`/main/userCenter/${it.id}/videoList`)
            }} />
            <div className="follower-info">
              <h1>{it.username} {isFollow && !isFans && IconChecked}</h1>
              <div className="info">
                <div className="item">
                  <div className="count">{it.fanscount}</div>
                  <div>{translate("creator.myCenter.2")}</div>
                </div>
                <div className="item">
                  <div className="count">{it.followcount}</div>
                  <div>{translate("creator.myCenter.3")}</div>
                </div>
              </div>
              <div className="desc">
                {it.profile}
              </div>
              {!isFans && <div className={`follow-sign ${isFollow ? '' : 'unfollow'}`} onClick={() => {
                if (following) {
                  return;
                }
                let _list = JSON.parse(JSON.stringify(unFollowList));
                let idx = _list.indexOf(it.id);
                if (idx == -1) {
                  _list.push(it.id);
                } else {
                  _list.splice(idx, 1);
                }
                setUnFollowList(_list);
                followUser({ id: it.id, status: idx == -1 ? 0 : 1 });
              }}>
                {translate(`creator.myCenter.${isFollow ? 4 : 3}`)}
              </div>}

            </div>
          </div>
        )
      })}
    </div>
  );
};

export const Followers: FC<{}> = () => {
  const { data = [], loading, load } = useRequest(getFollowers, { autoLoad: false });
  useEffect(() => { load({}) }, []);
  return (
    <div>
      {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : <FollowerList list={data} isFans={true} />}
    </div>
  );
};

export const Following: FC<{}> = () => {
  const { id } = useParams<any>();
  const { data = [], loading, load } = useRequest(getFollowing, { autoLoad: false });
  useEffect(() => { load({ id }) }, [id]);
  return (
    <div>
      {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : <FollowerList list={data} />}
    </div>
  );
}