

import { translate } from './Text';
import './VideoAuthLabel.scss';

export interface VideoAuthLabelProps {
    item: any
    isLeft?: boolean
}

function VideoAuthLabel(props: VideoAuthLabelProps) {
    const { item ,isLeft } = props;
    return (
        <div className={`authLabel ${isLeft?'leftLabel':''}`}>
            {parseInt(item.price || '0') > 0 && (item.islookauth || item.lookauth) != 1 && item.isfree != '1' && <div className="payAuth">${item.price}</div>}
            {parseInt(item.price || '0') > 0 && (item.islookauth || item.lookauth) == 1 && <div className="purchasedAuth">{translate('creator.common.7')}</div>}
            {((parseInt(item.price || '0') > 0 && (item.islookauth || item.lookauth) != 1 && item.isfree == '1')
                || (parseInt(item.price || '0') == 0 && item.isfree == '1'))
                && <div className="vipAuth">VIP</div>}

        </div>
    );
}

export default VideoAuthLabel;



