import classNames from 'classnames';
import type { FC } from 'react';
import Icon_Vip from '../images/main/vip.png';
import Icon_Vip_Bg from '../images/main/vipBg.png';
import './Image.scss';

export type ImageProps = React.ImgHTMLAttributes<HTMLImageElement> & { fallback?: string };

export const Image: FC<ImageProps> = ({ className, fallback, src = '', ...rest }) => {
  return (
    <img {...rest}
      src={src}
      className={classNames('oli-image', className)}
      onError={(evt) => {
        evt.currentTarget.src = fallback ?? "data:image/svg+xml;base64,";
      }}
    />
  );
}

export const Avatar: FC<ImageProps> = (props) => {
  return (
    <Image {...props} className="oli-avatar" fallback="data:image/svg+xml;base64," />
  );
}


export type ImgProps = React.ImgHTMLAttributes<HTMLImageElement>;
export const Img: FC<ImageProps> = ({ className, src = '', ...rest }) => {
  return (
    <img {...rest}
      src={`${src.indexOf('data:') == 0 ? '' : '.'}${src}`}
      className={className}
    />
  );
}
