import { Spin } from "antd";
import { useState } from "react"
import ReactPlayer from 'react-player'
import { LoadingOutlined } from '@ant-design/icons';
import { mainStore } from "../stores/MainStore";

export interface YouTubePlayerProps {
    url: string;
    getPlayer?: any;
    hideController?: boolean
}
const antIcon = <LoadingOutlined style={{ fontSize: 60, color: '#ffffff' }} spin />;
export const YouTubePlayer = (props: YouTubePlayerProps) => {
    const { url } = props;
    const [ready, setReady] = useState(false);

    return (
        <>
            <ReactPlayer
                ref={(player)=>{
                    props.getPlayer && props.getPlayer(player);
                }}
                muted={true}
                width='100%'
                height='100%'
                config={{
                    youtube: {
                        playerVars: {
                            autoplay: 1,
                            showinfo: 0,
                            controls: props.hideController ? 0 : 1,
                            modestbranding: 1,
                            hl: mainStore.isEn ? 'en' : 'zh'
                        },
                        embedOptions: {
                            onReady: (e: any) => {
                                console.log("youtube onReady", e)
                            },
                            onStateChange: (status: any) => {
                                console.log("youtube status", status)
                            },
                            onError: (error: any) => {
                                console.log("youtube onError", error)

                            }
                        },
                        onUnstarted: () => {
                            console.log("onUnstarted");
                        }
                    },
                }}
                onReady={() => {
                    console.log("play ready")
                    setReady(true);
                }}
                onError={(e) => {
                    console.log("play error", e)
                }}
                controls={true}
                url={url} />
            {!ready && <div className="videoLoading">
                <Spin indicator={antIcon} size='large' />
            </div>
            }
        </>
    )
}