
import { useEffect, useState } from 'react';
import { newsStore } from '../../stores/NewsStore';
import './News.scss';
import GlobalConfig from '../../config/GlobalConfig';
import TranslateWidget from '../../components/Text';
import { TranslateService } from '../../services/TranslateService';
import { mainStore } from '../../stores/MainStore';

export interface NewsItemProps {
    title: string;
    content: string;
    url?: string;
    source: string;
    createtime: string;
    image?: string;
    history?: any;
    useHtml?: boolean
}

export const NewsItem = (props: NewsItemProps) => {

    var needTranslate: boolean = (props.url != undefined && !mainStore.isEn);
    const [title, setTitle] = useState(needTranslate ? '' : props.title);
    const [content, setContent] = useState(needTranslate ? '' : props.content);
    useEffect(() => {
        if (needTranslate) {
            translateText(props.title).then((result: string) => {
                setTitle(result);
            }, () => {
                setTitle(props.title);

            })
            translateText(props.content).then((result: string) => {
                setContent(result);
            }, () => {
                setContent(props.content);
            })
        }
    }, []);

    var translateText = async (text: string) => {
        const result = await new TranslateService(text).send();
        return result;
    }
    //    var test = 'https://www.marketwatch.com/story/coming-up-u-s-new-home-sales-11643208406';
    //     var link = `http://translate.google.com/translate?u=${escape(test)}&amp;langpair=en%7Czh-CN`;
    //     console.log('link',link)
    return (
        <div className="newsItem" onClick={() => {
            if (!props.url) {
                return;
            }
            var link = `http://translate.google.com/translate?u=${props.url}&amp;langpair=en%7Czh-CN`;
            window.open(mainStore.isEn ? link : props.url, '_blank');
            //     if (GlobalConfig.newsSkip.indexOf(props.source) > -1) {
            // } else {
            //     newsStore.detailTitle = props.title;
            //     newsStore.detailUrl = props.url || '';
            //     GlobalConfig.history.push('/main/newsDetail');
            // }
        }}>
            <div className="newsTitle">{title.slice([':', '：'].indexOf(title[0]) > -1 ? 1 : 0)}</div>
            <div className="middle">
                <div className="tit-pic">
                    {props.image && props.url && <img src={props.image} />
                    }              </div>

                {props.useHtml && !props.url ? <div dangerouslySetInnerHTML={{ __html: props.content }}>

                </div> : <div className="content" >
                    <div className="text">
                        {content}
                    </div>
                    <div className="date">
                        <div className="source">
                            {props.source}
                        </div>
                        <div className="time">
                            {props.createtime.slice(5, 10).replace("-", '/')} {props.createtime.slice(11, 16)}
                        </div>
                    </div>
                </div>}

            </div>


        </div>
    );
}
