import { DatePicker, Modal } from 'antd';
import { observer } from 'mobx-react';
import { Form, Input, Button } from 'antd';
import { userStore } from '../../stores/UserStore';
import { settingStore } from '../../stores/SettingStore';
import Translate, { translate } from '../../components/Text';
import moment, { Moment } from 'moment'
import { useState } from 'react';
interface LoginModalProps {
    visible: boolean;
    cancle: Function;
}
const titleMap = {
    'phone': 'mine.e',
    'username': 'mine.n',
}


export const EditSingleModal = observer((props: LoginModalProps) => {
    let item = <div></div>;


  
    if (settingStore.editType === 'username') {
        item = <Form.Item
            name="username"
            label={translate('mine.m')}
            initialValue={userStore.userInfo?.username}
        >
            <Input />
        </Form.Item>
    } else if (settingStore.editType === 'phone') {
        item = <Form.Item
            name="phone"
            label={translate('mine.o')}
            initialValue={userStore.userInfo?.phone}
        >
            <Input />
        </Form.Item>
    } else if (settingStore.editType === 'birthday') {
        item = <Form.Item
            name="birthday"
            label={translate('mine.b')}
        >
            <DatePicker  placeholder="请输入你的生日1" defaultValue={userStore.userInfo?.birthday ? moment(userStore.userInfo?.birthday, 'YYYY-MM-DD') : undefined} />
        </Form.Item>
    } else if (settingStore.editType === 'profile') {
        item = <Form.Item name='profile' label="简介" initialValue={userStore.userInfo?.profile}>
            <Input.TextArea />
        </Form.Item>
    }
    return (
        <Modal className='editUserModal' title={translate(titleMap[settingStore.editType])} visible={props.visible}
            width={360}
            footer={null}
            centered={true}
            onCancel={() => { props.cancle() }}
        >
            <Form
                name="normal_login"
                className="login-form edit-form"
                initialValues={{ remember: true }}
                onFinish={(v) => settingStore.editSetting(v)}
            >
                {item}
                <Form.Item>
                    <Button htmlType="submit" className="login-form-button modalSubmit" >
                        {translate('mine.l')}
                    </Button>
                </Form.Item>
            </Form>
        </Modal>
    );
})













