import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { LockOutlined } from '@ant-design/icons';
import { Form, Input, Button } from 'antd';
import { settingStore } from '../../stores/SettingStore';
import Icons from '../../images/images';
import { Icon_password } from '../../images/index/password';
import { translate } from '../../components/Text';
import './ChangeModal.scss';

interface LoginModalProps {
    visible: boolean;
    cancle: Function;
}

export const ChangePassword = observer((props: LoginModalProps) => {
    return (
        <Modal className='changePasswordModal' title={translate('mine.g')} visible={props.visible}
            width={360}
            footer={null}
            centered={true}
            onCancel={() => { props.cancle() }}
        >
            <Form
                name="normal_login"
                className="login-form"
                initialValues={{ remember: false }}
                onFinish={(v) => settingStore.changePssowrd(v)}
            >

                <Form.Item
                    name="oldpassword"
                    rules={[{ required: true, message: translate('mine.setting.password.b') }]}
                >
                    <Input
                        type="password"
                        placeholder={translate('mine.setting.password.b')}
                    />
                </Form.Item>

                <Form.Item
                    name="password"
                    rules={[{ required: true, message: translate('mine.setting.password.c') }]}
                >
                    <Input
                        type="password"
                        placeholder={translate('mine.setting.password.c')}
                    />
                </Form.Item>

                <Form.Item
                    name="confirmpassword"
                    rules={[{ required: true, message: translate('mine.setting.password.d') },
                    ({ getFieldValue }) => ({
                        validator(_, value) {
                            if (!value || getFieldValue('password') === value) {
                                return Promise.resolve();
                            }
                            return Promise.reject(new Error('新密码两次输入不一致!'));
                        },
                    }),
                    ]}
                >
                    <Input
                        type="password"
                        placeholder={translate('mine.setting.password.d')}
                    />
                </Form.Item>


                <Form.Item>
                    <Button htmlType="submit" className="login-form-button modalSubmit">
                        {translate('mine.l')}
                    </Button>
                </Form.Item>
            </Form>
        </Modal>
    );
})













