import { Ajax } from '../utils/http';

export class KlineDetailService {

  private strategy: string;
  constructor(_strategy: string) {

      this.strategy = _strategy;
  }


    public async send(): Promise<KlinDetailResponse> {
        var response: KlinDetailResponse;
        var data = {
          strategy:this.strategy
        }
        try {
            response = await new Ajax<KlinDetailResponse>( '/latest_strategy', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


/*Pattern_name*/
export interface Pattern_name {
  ch: string;
  en: string;
}

/*Description*/
export interface Description {
  ch: string;
  en: string;
}

/*Symbols*/
export interface SymbolItem {
  symbol: string;
  name: string;
  latest_price: number;
  latest_return: number;
  latest_change: number;
  start_date: string;
  end_date: string;
}

/*tsModel3*/
export interface KlinDetailResponse {
  pattern_name: Pattern_name;
  trend: string;
  description: Description;
  symbols: SymbolItem[];
}

