import { observer } from 'mobx-react';
import StatisticStore from '../../../../stores/StatisticStore';
import { useEffect } from 'react';
import { Loading } from '../../../../components/Loading';
import NoData from '../../../../components/NoData';
import './Statistic.scss';
import { Loading_icon } from '../../../../images/Loading';
import { StockLogo } from '../../../../components/StockLogo';
import { indexStore } from '../../../../stores/IndexStore';
import GlobalConfig from '../../../../config/GlobalConfig';
import Translate, { translate } from '../../../../components/Text';
import { AnalystData, Recommendation } from '../../../../services/StockRateService';
import moment from 'moment';
import { Icon_Back } from '../../../../images/back';

export const AnalystPage = observer(() => {
    useEffect(() => {
        StatisticStore.getAnalystDetail();
    }, []);
    return <div className="analystWrapper">
        <div className="top">
            <div className="back" onClick={() => {
                GlobalConfig.history.goBack()
            }}>
                <img src={Icon_Back} alt="" />
                <div className="text">{translate('common.r')}</div>
            </div>

            <div className="line"></div>
            <div className="text">{StatisticStore.analyst}</div>
        </div>
        {StatisticStore.analystLoading ? <div className="loading">
            <img src={Loading_icon} alt="" />
        </div> : StatisticStore.followedItems.length == 0 ?
            <div style={{ height: '80px', margin: "0 auto" }}>
                <NoData />
            </div>
            :
            <div className="list">
                {StatisticStore.analysts.length >0 &&StatisticStore.analysts.map((f: AnalystData, i) => {
                    return <div className="info" key={i} onClick={() => {
                        window.scrollTo(0, 0);
                        indexStore.link = `/statistic/${f.symbol}`;
                        StatisticStore.symbol = f.symbol;
                        GlobalConfig.history.push(`/main/stock/statistic/${f.symbol}`);
                    }}>
                        <StockLogo symbol={f.symbol} />
                        <div className="followInfo">
                            <div className="titleInfo">
                                <div className="title">{f.symbol}</div>
                                {/* <div className="subtitle">{f.firm_name}</div> */}
                            </div>
                            <div className="change">{f.rating}</div>
                            <div className="data">{moment(f.date).format( 'YYYY-MM-DD')}</div>
                        </div>
                    </div>
                })}
            </div>
        }
    </div>

});