import './Research.scss';
import { researchStore } from '../../stores/ResearchStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import ResearchItemPage from './ResearchItem';
import NoData from '../../components/NoData';
import { Loading_icon } from '../../images/Loading';
import { indexStore, SearchType } from '../../stores/IndexStore';


export const ResearchPage = observer((props: any) => {
    let type = props.match.params.type;
    if (researchStore.type && researchStore.type != type) {
        researchStore.keyword = '';
        researchStore.type = type;
        researchStore.getData();
    }
    useEffect(() => {
        researchStore.type = type;
        researchStore.getData();
        var middle: any = document.getElementsByClassName('ant-layout-content')[0];
        let scrollFuc = (event: any) => {
            if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - 100)) {
                researchStore.getData(true);
            }
        }
        middle.addEventListener("scroll", scrollFuc)
        return () => {
            indexStore.searchType = undefined;
            middle.removeEventListener("scroll", scrollFuc);
        }
    }, []);

    let list: any = researchStore.list;
    let left = list.length % 4;
    for (let i = 0; i < left; i++) {
        list.push(undefined);
    }
    indexStore.searchType = SearchType.research;
    return (
        <div className="researchWrapper">
            {researchStore.loading?<div className="loading">
            <img src={Loading_icon} alt="" />
        </div>:<div className="contentWrapper"  >
                <ul className="researchContent">
                    {researchStore.categories.length != 0 && researchStore.list.length == 0 ? <NoData /> : researchStore.list.map((item, idx) => {
                        if (!item) {
                            return <li className="item"></li>
                        }
                        return <ResearchItemPage type={type} item={item} id={idx} />
                    })
                    }
                </ul>
                {researchStore.loadingMore && <div className="loadmore">
                    <img src={Loading_icon} alt="" />
                </div>}
            </div>}

        </div>
    )
});