import { observer } from 'mobx-react';
import './Header.scss';
import {  Input } from 'antd';
import { Icon_search_black } from '../../images/index/blackSearch';
import { translate } from '../../components/Text';
import { videoSearchStore } from '../../stores/VideoSearchStore';
import { useEffect } from 'react';


export const VideoSearchInput = observer((props: any) => {
    useEffect(() => {
      videoSearchStore.searchValue = '';
      return () => {
        videoSearchStore.searchValue = '';
      }
    }, []);
    return (
        <div className='searchResearchWrapper'>
            <Input placeholder={translate('common.o')} value={videoSearchStore.searchValue} onChange={(v)=>{
                videoSearchStore.doChangeSearchValue(v.target.value);
            }} onPressEnter={()=>{
                videoSearchStore.doSubmit();
            }} />
            <div className='search' onClick={()=>{
                videoSearchStore.doSubmit();
            }}>
                <img className='icon' src={Icon_search_black} alt="" />
            </div>
        </div>

    );
})


