import { Ajax } from '../utils/http';

export class GoldService {
    public async send(): Promise<Array<GoldItem>> {
        let response: Array<GoldItem>;

        try {
            response = await new Ajax<Array<GoldItem>>('/oliview_goldlist', {}, true).get();
        } catch (e) {
            throw e;
        }

        return response;
    }
}

export class GoldApplyService {
    private data: Object;
    constructor(_data: Object) {
        this.data = _data;
    }
    public async send(): Promise<string> {
        let response: string;

        try {
            response = await new Ajax<string>('/goldpaypay/goldpayApply', this.data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


export interface GoldItem {
    type: string;
    title: string;
    price: string;
    image: string;
}

