import {
    Route,
    Switch,
    Redirect
} from "react-router-dom";
import GlobalConfig from "../config/GlobalConfig";
import AnimatedRoute from "react-animated-routes";
import { LocalStorageService } from "../utils/storage";
interface RoutesProps {
    routes: any;
}

function Routes(props: RoutesProps) {
    const { routes } = props;
    let RoutesPage: Array<any> = routes.map((route: any, i: any) => (
        <RouteWithSubRoutes key={i} {...route} />
    ));
    RoutesPage.push(<Redirect key="default" to="/" />)
    return (
        <Switch>
            {RoutesPage}
        </Switch>
    );
}

export default Routes;



function RouteWithSubRoutes(route: any) {
    return (

        // <AnimatedRoute exact path={route.path} timeout={300} classNames="fade">
        //     <route.component routes={route.routes} />
        // </AnimatedRoute>


        <Route
            path={route.path}
            render={props => {
                GlobalConfig.history = props.history;
                if (!route.hideRecord) {
                    let path = window.location.hash.replace("#", '');
                    LocalStorageService.set('path', path)
                }
                return (
                    // pass the sub-routes down to keep nesting
                    <route.component {...props} routes={route.routes} />
                );
            }}
        />
    );
}

