import { Ajax } from '../utils/http';

export class KlineService {

    public async send(): Promise<PatternItem[]> {
        var response: PatternItem[];
        var data = {
        }
        try {
            response = await new Ajax<PatternItem[]>( '/latest_strategy_lists', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}
