import { IconEditWhite } from "./Icons";
import './UserCenter.scss';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import { SettingService } from '../../services/SettingService';
import { ApplyPage } from '../Publish/Apply';
import { translate } from '../../components/Text';
// import defaultBg from '../../images/creator/my_center.png';
import { ImageUploader } from '../../components/ImageUploader';
import { px } from '../../utils/rem';
import { AvatarUpload } from '../Profile/Upload';
import Routes from '../../components/Routes';
import IndexTabs from '../IndexTabs';
import { TabsConfig } from '../../config/TabsConfig';
import { defaultBg, formatCount } from './Common';

export const UserIndexPage = observer((props: any) => {
    const user = userStore.userInfo;
    const onChangeBg = (imgUrl: string) => {
        new SettingService({ background: imgUrl }).send().then(() => userStore.refreshUser());
    }
    if (!user) {
        return <div />;
    }
    return (
        <div className="my-center-header">
            <div className="header-bg">
                <img src={user.background || defaultBg} />
                <div className="edit-btn">
                    <ImageUploader width={px(1173)} height={px(230)} onChange={onChangeBg}>
                        <div className="btn-content">
                            {IconEditWhite} {translate('creator.myCenter.1')}
                        </div>
                    </ImageUploader>
                </div>
            </div>
            <div className="user-info-area-wrap">
                <div className="user-info-area">
                    <div className="avatar" >
                        <AvatarUpload />
                    </div>
                    <div className="info-area">
                        <div className="top">
                            <h1>{user.username}</h1>
                            <div className="describe">{user.profile}</div>
                        </div>
                        <div className="number-area">
                            <div className="number-item">
                                <div className="gray">{translate('creator.myCenter.12')}</div>
                                <div className="number-num">{formatCount(parseInt(user.followcount, 10))}</div>
                            </div>
                            <div className="number-item">
                                <div className="gray">{translate('creator.myCenter.2')}</div>
                                <div className="number-num">{formatCount(parseInt(user.fanscount, 10))}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <IndexTabs tabs={TabsConfig.userTabs.slice(0, TabsConfig.userTabs.length + (userStore.userInfo?.identity == 'creator' ? 0 : -1))} />
            <div className="appContent">
                <Routes routes={props.routes} />
            </div>
        </div>
    );
});