export interface PayData {
    id: string;
    object: string;
    amountSubtotal: number;
    amountTotal: number;
    cancelUrl: string;
    currency: string;
    livemode: boolean;
    mode: string;
    paymentMethodTypes: string[];
    paymentStatus: string;
    successUrl: string;
    url: string;
}


export interface SubscribeItem {
    id: string;
    priceid: string;
    name: string;
    price: string;
    desc: string;
    rules: string[];
    duration: string;
    createtime: string;
    updatetime: string;
    status: string;
    isSubscribe: number;
    isRenew: number;
}


