import { Popconfirm } from "antd";
import { FC, useEffect, useState } from "react";
import { useParams } from "react-router";
import { Loading } from "../../components/Loading";
import { showError } from "../../components/Message";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { CollectionListService } from "../../services/CollectionListService";
import { CreateDelCollectService } from "../../services/PublishService";
import { useRequest, useTranslator } from "../../utils/hooks";
import { VidoeCollectEditModel } from "../CreatorCenter/VideoEditorModal";
import { VideoCollectForm } from "../Publish/PublishVideo";
import { Header, ListPagenation, ListToolBar, Modal } from "./Common";
import { IconDelete, IconEdit, IconPlayList, IconTry } from "./Icons";

import './UserCenter.scss';

const getCollListData = ({ id = '', sortType = 'hot', sortAsc = 'desc', searchkey = '' }) => {
  return new CollectionListService({ psize: 999, userid: id, sort: sortType, order: sortAsc, searchkey }).send().then((list) => ({ list, sortType, sortAsc, searchkey }));
};
const delColl = ({ id = '' }) => new CreateDelCollectService({ id }).send();

export const CreatorCenterPlayList: FC<{}> = () => {
  const { id } = useParams<any>();
  const { translate } = useTranslator();
  const { loading, data, load } = useRequest(getCollListData, { autoLoad: false });
  const [visibleModal, setVisibleModel] = useState(false);
  const { list = [], sortAsc = 'desc', sortType = 'hot', searchkey = '' } = data ?? {};
  useEffect(() => load({ id }), []);
  return (
    <Header addBtn={translate('creator.playlist.5')} onAdd={() => setVisibleModel(true)}>
      {loading ? <Loading size="mini" /> : !list.length ? <></> : <>
        <div className="videos-tools">
          <ListToolBar searchkey={searchkey} sortType={sortType} sortAsc={sortAsc} useSearch onChange={(t, a, s) => load({ id, sortAsc: a, sortType: t, searchkey: s })} />
        </div>
        <div className="play-list">
          <div className="list-header">
            <div className="item-thumb">
            </div>
            <div className="item-info">
              {translate('creator.playlist.1')}
            </div>
            <div className="item-counts price">
              {translate('creator.playlist.26')}
            </div>
            <div className="item-views">
              {translate('creator.playlist.27')}
            </div>
            <div className="item-operations">

            </div>
          </div>
          {list.map((it) => (
            <div className="list-item" key={it.id} onClick={() => {
              GlobalConfig.history.push(`/main/userCenter/${id}/myChannel/playlistDetails?id=${it.id}&title=${encodeURIComponent(it.name)}`);
            }}>
              <div className="thumb">
                <img src={it.cover} />
                <div className="thumb-info">
                  <span>{it.videocount} {translate('creator.playlist.4')}</span>
                  {IconPlayList}
                </div>
              </div>
              <div className="item-info">
                <div className="title">{it.name}</div>
                <div className="intro">
                  <div className="gray">{translate('creator.playlist.2')} {it.createtime}</div>
                  <div className="fee-btn">{parseInt(it.price, 10) > 0 ? `$${it.price}` : it.isfree == '1' ? 'VIP' : translate('creator.playlist.14')}</div>
                </div>
                <div className="desc gray">
                  {it.describe}
                </div>
              </div>
              <div className="item-counts gray">
                <span>{it.trycount}</span>
              </div>
              <div className="item-operations" onClick={e => e.stopPropagation()}>
                <Popconfirm title={translate('creator.playlist.13')} onConfirm={() => delColl({ id: it.id }).then(() => load({ id })).catch(showError)}>
                  <a className="remove-btn">{IconDelete}</a>
                </Popconfirm>
              </div>
            </div>
          ))}
        </div>
      </>}
      <VidoeCollectEditModel visible={visibleModal} onClose={() => setVisibleModel(false)} onComplete={() => load({ id, sortType })} />
    </Header>
  );
};