
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { newsStore } from '../../stores/NewsStore';
import './News.scss';
import { Loading_icon } from '../../images/Loading';
import { NewsItem } from './NewsItem';
import Translate, { translate } from '../../components/Text';
import eventBus from '../../utils/eventBus';
import { mainStore } from '../../stores/MainStore';
import { OliItem } from './OliItem';

export const NewsPage = observer((props: any) => {
  useEffect(() => {
    newsStore.getData();
    var middle: any = document.getElementsByClassName('ant-layout-content')[0];
    let scrollFuc = (event: any) => {
      if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - 100)) {
        newsStore.getData(true);
      }
    }
    middle.addEventListener("scroll", scrollFuc);
    eventBus.addListener('translate', () => {
      newsStore.getData();
    })
    return () => {
      middle.removeEventListener("scroll", scrollFuc);
    }
  }, [])
  return (
    <div className="NewsPage">
      <div className="newsContent">
        {newsStore.loading ? <div className="loading" >
          <img src={Loading_icon} alt="" />
        </div> :
          <div className='newsList'>
            <div className="list">
              {newsStore.newsList.map((n, i) => {
                let isThird: boolean = n.articletype == '8';
                let title: string = mainStore.isEn && !isThird ? n.entitle : n.title;
                let content: string = (mainStore.isEn && !isThird) ? n.encontent : (n.url ? (n.abstract || n.content): n.content);
                return <div key={i} >
                  <NewsItem useHtml={n.isdetail == '1'} title={title} history={props.history} image={n.thumb} url={n.url} content={content} source={n.source} createtime={n.createtime} />
                </div>
              })}
            </div>

            <div className="list">
              <div className="top">{translate("research.b")}</div>
              {newsStore.oliList.map((n, i) =>{
                  let isThird: boolean = n.articletype == '8';
                  let title: string = mainStore.isEn && !isThird ? n.entitle : n.title;
                  let content: string = (mainStore.isEn && !isThird) ? n.encontent : (n.url ? (n.abstract || n.content): n.content);
                return <div key={i} >
                  <OliItem useHtml={n.isdetail == '1'} title={title} history={props.history} image={n.thumb} url={n.url} content={content} source={n.source} createtime={n.createtime} />
                </div>})}
            </div>
          </div>


        }
        {newsStore.loadingMore && <div className="loadmore">
          <img src={Loading_icon} alt="" />
        </div>}
      </div>
    </div>
  );
}
)