import { observable } from 'mobx';

export interface IVideoSearchContext {
  searchValueChange?: (value: string) => void;
  searchSubmit?: (value: string) => void;
}

export const videoSearchStore = observable({
  searchValue: '',
  cxts: [] as Array<IVideoSearchContext & { key: string }>,
  doChangeSearchValue(val: string) {
    this.searchValue = val;
    this.cxts.forEach(({ searchValueChange }) => { searchValueChange && searchValueChange(val) });
  },
  doSubmit() {
    this.cxts.forEach(({ searchSubmit }) => { searchSubmit && searchSubmit(this.searchValue) });
  },
  addContext(ctx: IVideoSearchContext) {
    const key = `${new Date().getTime()}-${Math.random()}`;
    this.cxts.push({ ...ctx, key });
    return key;
  },
  removeContext(key: string) {
    const i = this.cxts.findIndex(c => c.key === key);
    if (i > -1) {
      this.cxts.splice(i, 1);
    }
  },
  visible() {
    return this.cxts.length > 0;
  }
});

