import { FC, useEffect, useState } from "react";
import { } from "./Common";
import { IconAdd2, IconUploadSuccess, } from "./Icons";
import { ImageUploader } from "../../components/ImageUploader";
import { useTranslator } from "../../utils/hooks";
import VideoImage from "../../components/VideoImage";
import eventBus from "../../utils/eventBus";
import imgClose from '../../images/main/close.png';

export const VideoImageSelect: FC<{ uploadInfo?: any, onImageSelectd?: Function, onImgUploaded?: Function, maxCount?: number,tips?:string ,isUrlUpload?:boolean}> = ({ uploadInfo, onImageSelectd, onImgUploaded, maxCount,tips,isUrlUpload }) => {
  const { translate } = useTranslator();

  const [image, setImageSelected] = useState('');
  const [images, setImages] = useState<Array<string>>([]);
  const [timePoints, setTimePoints] = useState<Array<number>>([]);
  useEffect(() => {
    let getTimePoints = () => {
      let _timePoints = [];
      if (uploadInfo.url && uploadInfo.duration) {
        let splitValue = uploadInfo.duration / 10;
        for (var i = 0; i < 10; i++) {
          _timePoints.push(Math.floor(i * splitValue || 2));
        }
        setTimePoints(_timePoints);
      }
    }
    eventBus.addListener("videoUploaded", getTimePoints);
    return () => {
      eventBus.removeListener("videoUploaded", getTimePoints);
    }
  }, [])
  return (
    <div className="input-field covers">
      {uploadInfo && <label>{translate('creator.myVideos.40')} ({translate('creator.myVideos.36')})</label>}
      <div className="content">
        <ImageUploader className="thumb-uploader" width={240} height={135}
          disable={maxCount != undefined && images.length >= maxCount}
          onChange={(value) => {
            let _images: Array<string> = JSON.parse(JSON.stringify(images));
            _images.unshift(value)
            if (uploadInfo || isUrlUpload) {
              setImageSelected(value);
            }
            setImages(_images);
            onImageSelectd && onImageSelectd(value);
            onImgUploaded && onImgUploaded(_images);
          }}
          displayScale={.5}
          displayContent={(
            <div className="disp">
              {IconAdd2}
              <span>{translate('creator.myVideos.41')}</span>
            </div>
          )} />

        {[...images, ...timePoints].length > 0 ? <div className="images">
          {[...images.map((img, i) =>
            <div className="upload-img">
              <img key={img} src={img} onClick={() => {
                onImageSelectd && onImageSelectd(img);
                if (uploadInfo|| isUrlUpload) {
                  setImageSelected(img);
                }
              }} />
              {image == img && <div className="active">
                {IconUploadSuccess}
              </div>}
              {onImgUploaded && <div className="close" onClick={() => {
                let _images = JSON.parse(JSON.stringify(images));
                _images.splice(i, 1);
                setImages(_images);
                onImgUploaded(_images);
              }}><img src={imgClose} alt="" /> </div>}
            </div>
          )
            , ...timePoints.map((p, i) => <VideoImage
              key={i}
              url={uploadInfo.url}
              time={p}
              onClick={(img: string) => {
                onImageSelectd && onImageSelectd(img);
                setImageSelected(`${p}`);
              }}
              mask={image == `${p}` ? <div className="active">
                {IconUploadSuccess}
              </div> : <></>}
            />)]}
        </div> : <div className="tips"> <span>{ translate("community.3")}{tips || ''}</span></div>}
      </div>
    </div>

  );
}