import { Ajax } from '../utils/http';

export class StockQuotaService {
    private symbol: Object;
    constructor(_symbol: Object,) {
        this.symbol = _symbol;
    }
    public async send():Promise<StockQuotaResponse> {
        let response;
        try {
            response = await new Ajax<StockQuotaResponse>(`/oliview_market_bear_bull`,{symbol:this.symbol,classification_id:3},true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

/*Above*/
export interface Above {
    ch: string;
    en: string;
    rank: number;
    ratio: number;
  }
  
  /*Below*/
  export interface Below {
    ch: string;
    en: string;
    rank: number;
    ratio: number;
  }
  
  /*Name_rank_ratio*/
  export interface Name_rank_ratio {
    above: Above[];
    below: Below[];
  }
  
  /*Summary*/
  export interface QuotaSummary {
    cal_date: string;
    country: string;
    nb_above: number;
    nb_below: number;
    sector: string;
    tag: string;
    symbol: string;
  }
  
  /*tsModel4*/
  export interface StockQuotaResponse {
    name_rank_ratio: Name_rank_ratio;
    summary: QuotaSummary;
  }
  

  export enum QuotaType {
    above = 0,
    below = 1,
}
  