import { Drawer, Avatar, Divider, Col, Row } from 'antd';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import { useState } from 'react';
import { AvatarUpload } from './Upload';
import './Profile.scss';
import { settingStore, SettingsType } from '../../stores/SettingStore';
import { ChangePassword } from './ChangePassword';
import { EditSingleModal } from './EditSingle';
import Icons from '../../images/images';
import { Icon_profile_bg } from '../../images/index/profileBg';
import { Icon_edit } from '../../images/index/edit';
import GlobalConfig from '../../config/GlobalConfig';
import { Icon_more } from '../../images/index/rightArrow';
import { EditBasicModal } from './EditBasic';
import Translate from '../../components/Text';

interface ProfileProps {
    visible?: boolean
    onClose?: ((e: any) => void) | undefined
}


export const Profile = observer((props: ProfileProps) => {
    // const [visible, setVisible] = useState(false);


    // const showDrawer = () => {
    //     setVisible(true)
    // };

    // const onClose = () => {
    //     setVisible(false)
    // };

    return (
        <>
            <Drawer
                className='editDrawer'
                placement="right"
                closable={false}
                onClose={props.onClose}
                visible={props.visible || false}
            >
                <AvatarUpload />
                <div className="editInfo">
                    <div className="text">
                        <Translate text='mine.a' />
                    </div>
                    <div className="edit" onClick={() => {
                        settingStore.editModalVisible = true;
                    }}>
                        <div className="text"><Translate text='mine.k' /></div>
                        <img src={Icon_more} alt="" />
                    </div>
                </div>
                <div className="basicInfo">
                    <Translate text='mine.b' />
                    ：{userStore.userInfo?.birthday}
                </div>
                <div className="basicInfo ">
                    <Translate text='mine.c' />
                    ：{userStore.userInfo?.profile}
                </div>
                <div className="editInfo bottom">
                    <div className="text">                        <Translate text='mine.d' />
                    </div>
                </div>
                <div className="securityItem">
                    <div className="info">
                        <div className='title'>                        <Translate text='mine.e' />
                        </div>
                        <div>                        <Translate text='mine.f' />
                            ：{userStore.userInfo?.phone}</div>
                    </div>

                    <div className="edit" onClick={() => {
                        settingStore.editSingleModalVisible = true;
                        settingStore.editType = 'phone';
                    }}>
                        <div className="text"> <Translate text='mine.k' /></div>
                        <img src={Icon_more} alt="" />
                    </div>
                </div>

                <div className="securityItem">
                    <div className="info">
                        <div className='title'>                        <Translate text='mine.g' />
                        </div>
                        <div>                        <Translate text='mine.h' />
                        </div>
                    </div>

                    <div className="edit" onClick={() => {
                        settingStore.passwordModalVisible = true
                    }}>
                        <div className="text">                        <Translate text='mine.k' />
                        </div>
                        <img src={Icon_more} alt="" />
                    </div>
                </div>

                <Row>
                    <div className='logout' onClick={() => userStore.logout()}>
                        <div>                        <Translate text='mine.i' />
                        </div>
                    </div>
                </Row>
            </Drawer>
            <EditSingleModal visible={settingStore.editSingleModalVisible} cancle={() => { settingStore.editSingleModalVisible = false }} />
            <ChangePassword visible={settingStore.passwordModalVisible} cancle={() => { settingStore.passwordModalVisible = false }} />
            <EditBasicModal visible={settingStore.editModalVisible} cancle={() => { settingStore.editModalVisible = false }} />
        </>
    );
});
