import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { DatePicker, Input } from 'antd';
import 'react-calendar/dist/Calendar.css';
import { eCalendarStore } from '../../../stores/ECalendarStore';
import { CalendarItem } from '../../../services/StockCalendar';
import './ECalendar.scss';
import zh_CN from "antd/lib/locale-provider/zh_CN";
import en_US from "antd/lib/locale-provider/en_US";
import { ConfigProvider } from 'antd';
import NoData from '../../../components/NoData';
import { Loading_icon } from '../../../images/Loading';
import Translate from '../../../components/Text';
import { mainStore } from '../../../stores/MainStore';
import { numFormatter } from '../../../utils/globalFuncs';

const { RangePicker } = DatePicker;


const { Search } = Input;
export const ECalendarPage = observer(() => {
  useEffect(() => {
    eCalendarStore.changeType(1);
  }, []);
  const tabs = [{ text: "markets.date.a", id: 0 },
  { text: "markets.date.b", id: 1 },
  { text: "markets.date.c", id: 2 },
  { text: "markets.date.d", id: 3 },
  { text: "markets.date.e", id: 4 },]
  return (
    <div className="calendarWrapper">
      <div className="calendarContentWrapper">
        <div className="topNav">
          <div className="menu">
            {
              tabs.map((r) => {
                return <div className="option" style={{ color: eCalendarStore.type === r.id ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                  // window.scrollTo(0, 0);
                  eCalendarStore.changeType(r.id);
                }}>
                  <div className={eCalendarStore.type === r.id ? "checked" : "check"}>
                    <div className="round"></div>
                  </div>
                  <div className="text">
                    <Translate text={r.text} />
                  </div>
                </div>
              })
            }
          </div>
          <div className="datePicker">
            <ConfigProvider locale={mainStore.isEn? en_US:zh_CN}>
              <RangePicker onChange={(c) => {
                eCalendarStore.setRange(c);
              }} />
            </ConfigProvider>
          </div>
        </div>

        {eCalendarStore.loading ? <div className="loading" >
          <img src={Loading_icon} alt="" />
        </div> : eCalendarStore.list.length == 0 ? <div>
          <NoData />
        </div>
          :
          <div>

            <div className="calendarContent">
              {eCalendarStore.list?.map((item: CalendarItem, i) => {
                return <div className="calendarItem" key={i}>
                  <div className="left">
                    <div className="country">{item.country}</div>
                    <div className="time">{item.time.slice(5, 10).replaceAll('-','.')} {item.time.slice(11, 16)}</div>
                  </div>
                  <div className="right">
                    <div className="event">
                      {item.event}
                    </div>
                    <div className="info">
                      <div className="actual">
                        Act: <span style={{ color: item.actual < 0 ? '#EC4D3D' : '#4CB94D' }}> {numFormatter(item.actual,2)}</span>
                      </div>
                      <div className="divider"></div>
                      <div className="estimate">
                        Est:
                        <span style={{ color: item.estimate < 0 ? '#EC4D3D' : '#4CB94D' }}> {numFormatter(item.estimate,2)}</span>

                      </div>

                      <div className="divider"></div>
                      <div className="prev">
                        Prev:
                        <span style={{ color: item.prev < 0 ? '#EC4D3D' : '#4CB94D' }}>

                          {numFormatter(item.prev,2)}
                        </span>
                      </div>

                      <div className="divider"></div>
                      <div className="impact">
                        Imp:
                        <span >
                          {item.impact}
                        </span>

                      </div>
                    </div>
                  </div>



                </div>
              })}
            </div>
          </div>}

      </div>
    </div>
  )
});