import { Ajax } from '../utils/http';

export class IndexBannerService {

    public async send(): Promise<BannerItem[]> {
        let response: BannerResponse;
        try {
            response = await new Ajax<BannerResponse>('/index/bannerList', { type: 1 }).post();
        } catch (e) {
            throw e;
        }
        return response.list;
    }
}

/*List*/
export interface BannerItem {
    id: string;
    type: string;
    url: string;
    objectid: string;
    objecttype: string;
}

/*Data*/
export interface BannerResponse {
    list: BannerItem[];
    total: number;
}
