import { observer } from 'mobx-react';
import { useEffect } from 'react';
import GlobalConfig from '../../../../config/GlobalConfig';
import { Loading_icon } from '../../../../images/Loading';
import { Icon_collect } from '../../../../images/market/collect';
import { Icon_collect_on } from '../../../../images/market/collectOn';
import { Icon_stock_info } from '../../../../images/market/info';
import { Dividend } from '../../../../services/StockDescriptionService';
import StatisticStore from '../../../../stores/StatisticStore';
import NoData from '../../../../components/NoData';
import './Statistic.scss';
import Translate, { translate } from '../../../../components/Text';
import { Icon_Back } from '../../../../images/back';
export const CompanyPage = observer((props: any) => {
    useEffect(() => {
        StatisticStore.getCompanyInfo();
    }, [])

    let weekHigh: any = StatisticStore.stockDescription?.stat['52 Week High'] || ['', ''];
    let weekLow: any = StatisticStore.stockDescription?.stat['52 Week Low'] || ['', ''];
    return (
        <div className="companyWrapper">
            {StatisticStore.descriptionLoading ? <div className="loading" >
                <img src={Loading_icon} alt="" />
            </div> : <div className='contentWrapper'>

                <div className="leftContent">
                    <div className="top">
                        <div className="back" onClick={() => {
                            props.history.goBack()
                        }}>
                            <img src={Icon_Back} alt="" />
                            <div className="text">{translate("common.r")}</div>
                        </div>

                        <div className="line"></div>
                        <div className="text"><Translate text='markets.stock.c' /></div>
                    </div>
                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.d' /></div>
                        <div className="text">{StatisticStore.stockDescription?.overview['Company Name']}</div>
                    </div>
                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.e' /></div>
                        <div className="text">{StatisticStore.stockDescription?.overview['IPO Date']}</div>
                    </div>
                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.f' /></div>
                        <div className="text">{StatisticStore.stockDescription?.overview.Industry}</div>
                    </div>
                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.g' /></div>
                        <div className="text">{StatisticStore.stockDescription?.overview.Description}</div>
                    </div>
                    <div className="divider"></div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.h' /></div>
                        <div className="text">{weekHigh[0]}&nbsp;&nbsp;({weekHigh[1]})</div>
                    </div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.i' /></div>
                        <div className="text">{weekLow[0]}&nbsp;&nbsp;({weekLow[1]})</div>
                    </div>

                    <div className="item">
                        <div className="name">Beta</div>
                        <div className="text">{StatisticStore.stockDescription?.stat.beta}</div>
                    </div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.j' /></div>
                        <div className="text">{StatisticStore.stockDescription?.stat['Market Capitalization']}</div>
                    </div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.k' /></div>
                        <div className="text">{StatisticStore.stockDescription?.stat['Dividend Yield']}</div>
                    </div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.l' /></div>
                        <div className="text">{StatisticStore.stockDescription?.stat.PE}</div>
                    </div>

                    <div className="item">
                        <div className="name"> <Translate text='markets.stock.m' /></div>
                        <div className="text">{StatisticStore.stockDescription?.stat.EPS}</div>
                    </div>
                </div>
                <div className="rightContent">
                    <div className="topTitle"> <Translate text='markets.stock.n' /></div>
                    <div>
                        <div className="titles">
                            <div className="time"> <Translate text='markets.stock.o' /></div>
                            <div className="amount">
                                <Translate text='markets.stock.p' />
                            </div>
                            <div className="adjustedAmount">
                                <Translate text='markets.stock.q' />
                            </div>
                        </div>
                        {StatisticStore.stockDescription?.dividend.length == 0 ? <NoData /> : <div className=" dividendContent">
                            {StatisticStore.stockDescription?.dividend?.map((item: Dividend, i) => {
                                return <div className="dividendItem" key={i} style={{ background: i % 2 == 0 ? "#fff" : "#F3F5F9" }}>
                                    <div className="time">{item.recordDate.slice(2).split('-').join('.')}</div>
                                    <div className="amount">
                                        {item.amount}
                                    </div>
                                    <div className="adjustedAmount">
                                        {item.adjustedAmount}
                                    </div>
                                </div>
                            })}
                        </div>}
                    </div>

                </div>
            </div>}
        </div>

    )
})

export const StockInfoTabs = observer(() => {
    return <div className="stockTabs">
        <img src={Icon_stock_info} alt="" className="info" onClick={() => {
            GlobalConfig.history.push("/main/stockCompany")
        }} />
        <img src={StatisticStore.followedSymbols.indexOf(StatisticStore.symbol.toLowerCase()) == -1 ? Icon_collect : Icon_collect_on} alt="" className="watch" onClick={() => {
            StatisticStore.follow();
        }} />
    </div>
})