import { Col, Row } from 'antd';
import { Modal, Button } from '../UserCenter/Common';
import { observer } from 'mobx-react';
import { stripeStore } from '../../stores/StripeStore';
import { loadStripe } from '@stripe/stripe-js';
import {
  CardElement,
  CardNumberElement,
  CardExpiryElement,
  CardCvcElement,
  Elements,
  useStripe,
  useElements,
} from '@stripe/react-stripe-js';
import classNames from 'classnames';

import './Stripe.scss';
import { showError, showLoading, showSuccess } from '../../components/Message';
import { translate } from '../../components/Text';
import { Component, FC, useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { userInfo } from 'os';
import { userStore } from '../../stores/UserStore';
import { IconAddCircle, IconCardChecked, IconCardUnchecked, IconVideoPlay } from '../UserCenter/Icons';
import { px } from '../../utils/rem';
import { mainStore } from '../../stores/MainStore';
import GlobalConfig from '../../config/GlobalConfig';
import { useRequest } from '../../utils/hooks';
import { getAllPayments, iconCardMap } from '../UserCenter/Payment';
import { Loading } from '../../components/Loading';
import IconCardDef from '../../images/creator/card_default.png';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { conversionTime } from '../../utils/globalFuncs';

export interface TargetInfo { title: string; avatar: string; subTitle: string };


interface EditModalProps {
  targetInfo?: TargetInfo;
  visible: boolean;
  getSercet?: Function;
  cancle: Function;
  success: Function;
}

export const StripeModal = observer((props: EditModalProps) => {
  const { targetInfo } = props;
  const [stripePromise, setStripePromise] = useState<any>();
  const isEn = mainStore.isEn;
  const { data = {}, loading, load: loadPayments } = useRequest(getAllPayments, { autoLoad: false });
  const { list: allPayments, def } = data as any;
  const defPaymentId = def?.invoice_settings?.default_payment_method ?? '';


  useEffect(() => {
    if (props.visible) {
      loadPayments({});
    }
  }, [props.visible]);
  useEffect(() => {
    setStripePromise(loadStripe(GlobalConfig.stripeKey));
  }, [isEn]);
  return (
    <Modal
      title={translate("payment.1")}
      wrapClassName="stripe-modal"
      visible={props.visible}
      width={px(510)}
      footer={null}
      centered={true}
      onCancel={() => props.cancle()}
    >
      <div className="stripe-bill-info">
        {targetInfo && (
          <div className="target-info">
            <img className="avatar" src={targetInfo.avatar} />
            <div>
              <h1>{targetInfo.title}</h1>
              <div className="desc">{targetInfo.subTitle}</div>
            </div>
          </div>
        )}

        <div className="price-area">
          <span>{translate("payment.2")}:</span>
          <span>${stripeStore.amount}</span>
        </div>
      </div>
      <Elements stripe={stripePromise} options={{ locale: 'en' }}>
        {loading ? <Loading size="mini" /> : allPayments?.length > 0 ? (
          <PaymentSelector payments={allPayments} defaultPayment={defPaymentId} success={props.success} getSercet={props.getSercet} />
        ) : (
          <CheckoutForm success={props.success} getSercet={props.getSercet} />
        )}

      </Elements>
    </Modal>
  );
})


interface CheckFormProps {
  success: Function;
  getSercet?: Function;
}

const CheckoutForm = observer((props: CheckFormProps) => {
  const stripe = useStripe();
  const elements = useElements();

  const handleSubmit = async (event: any) => {
    event.preventDefault();
    if (elements == null) {
      return;
    }
    let element = elements.getElement(CardNumberElement);
    if (stripe && element) {
      let loading = showLoading(translate('gold.form.d'));
      try {
        const { error, paymentMethod } = await stripe.createPaymentMethod({
          type: 'card',
          card: element,
        });
        console.log("paymentMethod", paymentMethod)
        let secret = props.getSercet ? (await props.getSercet()) : (await stripeStore.getOrder());
        console.log("secretsecret", secret)
        stripe.confirmCardPayment(secret, { payment_method: { card: element } }).then(function (result) {
          console.log("result", result)
          //此处data.client_secret变量为生成订单后 后端返回给前端的秘钥  将这个秘钥返回给stripe去做校验
          if (result.paymentIntent && result.paymentIntent.status == "succeeded") {
            //支付成功的逻辑操作
            loading();
            props.success && props.success();
          } else {
            showError(translate('gold.form.e', { value: '' }));
          }
        })
      } catch (e) {
        showSuccess(translate('gold.form.e', { value: `${e}` }));

      }


    }
  };

  return (
    <form onSubmit={handleSubmit} className="stripe-form">
      <label>{translate("payment.3")}</label>
      <CardNumberElement />
      <div className='card-info'>
        <div className='info'>
          <label>{translate("payment.4")}</label>
          <CardExpiryElement />
        </div>
        <div className='info'>
          <label>{translate("payment.5")}</label>
          <CardCvcElement />
        </div>
      </div>
      <div className="oCenter">
        <Button htmlType="submit" className="stripeSubmit" >
          {translate("payment.6")}
        </Button>
      </div>
      <div className="extra-info">
        {translate("payment.7")} <a target="_blank" href="https://aolinews.oss-accelerate.aliyuncs.com/pdf/subscription.pdf">{translate("payment.8")}</a>
      </div>
    </form>
  );
});

export interface PaymentSelectorProps {
  defaultPayment: string;
  payments: any[];
  success: Function;
  getSercet?: Function;
  hideExtraInfo?: boolean;
  onAdd?: () => void;
  submitBtnTxt?: string;
}

export const PaymentSelector: FC<PaymentSelectorProps> = observer(({ submitBtnTxt, payments = [], defaultPayment, success, getSercet, hideExtraInfo, onAdd }) => {
  const [selInd, setSelInd] = useState(0);
  const stripe = useStripe();
  useEffect(() => {
    if (payments.length > 0 && defaultPayment) {
      const index = payments.findIndex(it => it.id === defaultPayment);
      if (index > -1) {
        setSelInd(index);
      }
    }
  }, [defaultPayment, payments]);

  const { loading, load: submit } = useRequest(async () => {
    if (stripe) {
      try{
      const method = payments[selInd];
      const secret = getSercet ? (await getSercet(method.id)) : (await stripeStore.getOrder(method.id));
      const { paymentIntent } = await stripe.confirmCardPayment(secret, { payment_method: method.id });
      if (paymentIntent && paymentIntent.status == "succeeded") {
        success && success();
      } else {
        showError(translate('gold.form.e', { value: '' }));
      }
    }catch(e){
       showError(`${e}`);
    }
  } 
  }, { autoLoad: false });

  return (
    <div className="payment-selector">
      {payments.map((it, i) => (
        <div className="payment-item" key={it.id}>
          <a className={classNames("check-btn", { active: i === selInd })} onClick={() => setSelInd(i)}>
            {i === selInd ? IconCardChecked : IconCardUnchecked}
          </a>
          <img className="card-icon" src={iconCardMap[it.card.brand] ?? IconCardDef} />
          <div className="info">
            <div className="name">{it.card.brand.toUpperCase()}</div>
            <div className="num">Credit ****{it.card.last4}</div>
          </div>
        </div>
      ))}
      <div className={onAdd ? 'between' : "oCenter"}>
        {onAdd && <a className="add-card-btn" onClick={onAdd}>{IconAddCircle}{translate('payment.14')}</a>}
        <Button className="stripeSubmit" onClick={submit} loading={loading}>
          {submitBtnTxt ?? translate("payment.6")}
        </Button>
      </div>

      {!hideExtraInfo && <div className="extra-info">
        {translate("payment.7")} <a target="_blank" href="https://aolinews.oss-accelerate.aliyuncs.com/pdf/subscription.pdf">{translate("payment.8")}</a>
      </div>}
    </div>
  );
});


export interface StripePayAreaProps {
  price: string;
  objectid: string;
  ordertype: number;
  className?: string;
  onClick?: () => void;
  onComplete?: () => void;
  disableMask?: boolean;
  targetInfo?: TargetInfo;
  duration?:string;
}

export const StripePayArea: FC<StripePayAreaProps> = (props) => {
  const [visible, setVisible] = useState(false);
  const needBuy = parseFloat(props.price) > 0;
  const doClick = () => {
    if (needBuy) {
      if (!userStore.userInfo) {
        loginStore.LoginModel = LoginModel.login;
        userStore.loginModalVisible = true;
        return;
      }
      stripeStore.objectid = props.objectid;
      stripeStore.ordertype = props.ordertype;
      stripeStore.pay(props.price);
      setVisible(true);
    } else if (props.onClick) {
      props.onClick();
    }
  };
  const afterPay = () => {
    setVisible(false);
    showSuccess(mainStore.isEn ? 'Payment success' : '支付完成');
    if (props.onComplete) {
      props.onComplete();
    }
  }

  return (
    <div className={classNames('stripe-area', props.className)} onClick={!needBuy ? props.onClick : undefined}>
      {props.children}
      {props.duration && <div className="duration">{ conversionTime(props.duration,true)}</div>}
      <div className={classNames("price-mask", { 'optc': props.disableMask })} onClick={doClick}>
        {needBuy ? <span>$ {props.price}</span> : <div className="icon-play">{IconVideoPlay}</div>}
      </div>
      {needBuy && <StripeModal
        visible={visible}
        success={afterPay}
        targetInfo={props.targetInfo}
        cancle={() => {
          setVisible(false);
        }}
      />}
    </div>
  );
};







