import { FC, useEffect, useState } from "react";
import { RouteComponentProps } from "react-router";
import { useRequest, useTranslator } from "../../utils/hooks";
import { Button, Modal } from "./Common";
import { IconAdd, IconCardChecked, IconCardUnchecked } from "./Icons";
import IconVisa from '../../images/creator/card_visa.png';
import IconMaster from '../../images/creator/card_master.png';
import IconYY from '../../images/creator/card_yy.png';
import IconYL from '../../images/creator/card_yl.png';
import IconCardDef from '../../images/creator/card_default.png';
import './Payment.scss';
import { px } from "../../utils/rem";
import { Col, Input, ModalProps, Popconfirm, Row } from "antd";
import { CardCvcElement, CardExpiryElement, CardNumberElement, Elements, useElements, useStripe } from "@stripe/react-stripe-js";
import { mainStore } from "../../stores/MainStore";
import { loadStripe } from "@stripe/stripe-js";
import GlobalConfig from "../../config/GlobalConfig";
import { Ajax } from "../../utils/http";
import { userStore } from "../../stores/UserStore";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { showError, showSuccess } from "../../components/Message";

export const iconCardMap = {
  'visa': IconVisa,
  'mastercard': IconMaster
};

async function setupStripeUser() {
  if (!userStore.userInfo) {
    await userStore.refreshUser();
  }

  if (!userStore.userInfo?.customer) {
    await new Ajax('/paymethod/createCustomer').post();
    await userStore.refreshUser();
  }
}

export async function getAllPayments() {
  await setupStripeUser();

  return {
    list: await new Ajax<Array<any>>('/paymethod/allPaymentMethod').post().then((res: any) => res.data),
    def: await new Ajax<any>('/paymethod/retrieveCustomer').post()
  };
}



function delPayments(id: string) {
  return new Ajax('/paymethod/detachPaymentMethod', { id }).post();
}

function saveDefault(id: string) {
  return new Ajax('/paymethod/saveDefaultPaymentMethod', { id }).post();
}

export const Payment: FC<RouteComponentProps<{}>> = ({ }) => {
  const { translate } = useTranslator();
  const [stripePromise, setStripePromise] = useState<any>();
  const [visible, setVisible] = useState(false);
  const [defId, setDefId] = useState('');
  const isEn = mainStore.isEn;

  const { data = {}, loading, load } = useRequest(getAllPayments);
  const { list = [], def } = data as any;
  const defPaymentId = def?.invoice_settings?.default_payment_method ?? '';

  const { loading: submitting, loadWithPromise: submit } = useRequest(saveDefault, { autoLoad: false });

  useEffect(() => {
    setDefId(defPaymentId)
    if (list.length == 1 && !defPaymentId) {
      setDefId(list[0].id);
      submit(list[0].id);
    }
  }, [defPaymentId, list]);
  useEffect(() => {
    setStripePromise(loadStripe(GlobalConfig.stripeKey));
  }, [isEn]);
  return (
    <div>
      <div className="payment-detail">
        <h1 className="payment-detail-title">{translate('creator.myCenter.6')}</h1>
        <div className="payment-detail-block">
          <div className="card-top">
            <h2>{translate('creator.myCenter.7')}</h2>
            <a onClick={() => setVisible(true)}>
              {IconAdd}
              {translate('creator.myCenter.8')}
            </a>
          </div>
          {loading ? <Loading size="mini" /> : list.length <= 0 ? <NoData /> : list.map((it: any) => (
            <div className="card-item" key={it.id}>
              <a className="check-btn" onClick={() => setDefId(it.id)}>{defId === it.id ? IconCardChecked : IconCardUnchecked}</a>
              <img className="card-icon" src={iconCardMap[it.card.brand] ?? IconCardDef} />
              <div className="info">
                <div className="name">{it.card.brand.toUpperCase()}</div>
                <div className="num">Credit ****{it.card.last4}</div>
              </div>
              <Popconfirm title={translate('payment.13')} onConfirm={() => delPayments(it.id).then(() => load({}))}>
                <Button size="small" className="remove-btn">{translate('creator.myCenter.9')}</Button>
              </Popconfirm>
            </div>
          ))}
        </div>
       {list.length>0&& <div className="oCenter">
          <Button
            loading={submitting}
            size="large"
            className="save-btn"
            onClick={() => {
              console.log('defId',defId)
              if(!defId){
                showError(mainStore.isEn?'Please select your card':'请选择信用卡');
                return ;
              }
              submit(defId)
                .then(() => {
                  showSuccess(mainStore.isEn ? 'Save done' : '保存默认支付方式完成');
                  load({});
                })
                .catch(e => showError(e.message));
            }}>
            {translate('creator.myCenter.10')}
          </Button>
        </div>}
      </div>

      {visible && (
        <Modal
          title={translate("payment.12")}
          wrapClassName="stripe-modal"
          width={px(510)}
          footer={null}
          visible={true}
          centered={true}
          onCancel={() => setVisible(false)}
        >
          <Elements stripe={stripePromise} options={{ locale: 'en'  }}>
            <AddCardForm onComplete={() => { load({}); setVisible(false); }} />
          </Elements>
        </Modal>
      )}
    </div>
  );
}




export const AddCardForm: FC<{ onComplete: () => void }> = ({ onComplete }) => {
  const { translate } = useTranslator();
  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);
  const stripe = useStripe();
  const elements = useElements();

  const submitAddCard = async (e: any) => {
    e.preventDefault();
    setLoading(true);
    try {
      if (!stripe || !elements) {
        return;
      }
      const card = elements.getElement(CardNumberElement);
      if (!card) {
        return;
      }
      const { client_secret = '' } = await new Ajax<any>('/paymethod/createSetupIntent').post();
      const { error: confirmError, setupIntent } = await stripe.confirmCardSetup(client_secret, {
        payment_method: { card }
      });
      if (confirmError) {
        throw confirmError;
      }

      await new Ajax<any>('/paymethod/attachPaymentMethod', {
        id: setupIntent?.payment_method
      }).post();
      onComplete();
    } catch (e: any) {
      console.log("errrorr $e", e)
      showError(e.message || e);
    } finally {
      setLoading(false);
    }
  }

  return (
    <form onSubmit={submitAddCard} className="stripe-form">
      <div className="credit-card">
        <div className="dh" />
        <div className="txt">Credit Card</div>
        <img src={IconVisa} />
        <img src={IconMaster} />
        <img src={IconYL} />
        <img src={IconYY} />
      </div>
      {/* <label>{translate('payment.11')}</label>
      <Input className="name-input" value={name} onChange={(evt) => setName(evt.target.value)} /> */}
      <label>{translate("payment.3")}</label>
      <CardNumberElement />
      <div className="line">
        <div className="col">
          <label>{translate("payment.4")}</label>
          <CardExpiryElement />
        </div>
        <div className="col">
          <label>{translate("payment.5")}</label>
          <CardCvcElement />
        </div>
      </div>
      <div className="oCenter">
        <Button htmlType="submit" className="stripeSubmit" loading={loading}>
          {translate("payment.14")}
        </Button>
      </div>
      <div className="extra-info">
        {translate("payment.7")} <a target="_blank" href="https://aolinews.oss-accelerate.aliyuncs.com/pdf/subscription.pdf">{translate("payment.8")}</a>
      </div>
    </form>
  );
}