import { makeAutoObservable, } from "mobx";
import { showError, showLoading, showSuccess, } from "../components/Message";
import { ChangeAccountService, ChangePasswordService } from "../services/ChangePasswordService";
import { ResearchDetailData } from "../services/ResearchDetailService";
import { SettingService } from "../services/SettingService";
import { userStore } from "./UserStore";
import GlobalConfig from "../config/GlobalConfig";
import { timestamp } from "../utils/globalFuncs";
import { SendEmialService, SendPhoneService, UserInfo } from "../services/LoginService";
import { mainStore } from "./MainStore";


function dataURLtoBlob(dataurl: any) {
  var arr = dataurl.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], { type: mime });
}
//将blob转换为file
function blobToFile(theBlob: any, fileName: any) {
  theBlob.lastModifiedDate = new Date();
  theBlob.name = fileName;
  return theBlob;
}


enum SettingsType {
  avatar = 'avatar',
  username = 'username',
  phone = 'phone',
  sex = 'sex',
  birthday = 'birthday',
  profile = 'profile',
  chatnotice = 'chatnotice',
  customerservicenotice = 'customerservicenotice',
  systemnotice = 'systemnotice'
}

class SettingStore {
  public detail?: ResearchDetailData;
  public loading: boolean = false;
  public type?: SettingsType;
  public passwordModalVisible: boolean = false;
  public emailModalVisible: boolean = false;
  public phoneModalVisible: boolean = false;
  public editModalVisible: boolean = false;
  public editSingleModalVisible: boolean = false;
  public editType: string = 'username';

  public sending: boolean = false;

  public sendBtnText: string = 'mine.find.d';

  public countDwon: number = 60;

  private timer: any;


  constructor() {
    makeAutoObservable(this);
  }

  public async sendEmial(_emial: string, _type: number) {
    if (this.sending || (this.countDwon < 60 && this.countDwon > 0)) {
      return;
    }
    this.sending = true;

    this.sendBtnText = 'mine.find.f';
    try {
      await new SendEmialService(_emial, _type).send();
    } catch (e: any) {
      this.sending = false;
      this.sendBtnText = 'mine.find.d';
      showError(e);
      return;
    }
    this.sending = false;
    showSuccess(mainStore.isEn ? 'Code sent successfully' : '发送成功！');
    this.timer = setInterval(() => {
      if (this.countDwon === 0) {
        this.countDwon = 60;
        this.sendBtnText = 'mine.find.d';
        clearInterval(this.timer);
        return;
      }
      this.countDwon -= 1;
      this.sendBtnText = `${this.countDwon}s`;
    }, 1000);
  }

  public async sendPhone(_phone: string, _type: number, _areacode: string, _imagecodeid: string, _imagecode: string) {
    if (this.sending || (this.countDwon < 60 && this.countDwon > 0)) {
      return;
    }
    this.sending = true;
    var phoneNum = _phone.indexOf('0') == 0 ? _phone : `0${_phone}`;

    this.sendBtnText = 'mine.find.f';
    try {
      await new SendPhoneService(phoneNum, _type, _areacode, _imagecodeid, _imagecode).send();
    } catch (e: any) {
      this.sending = false;
      this.sendBtnText = 'mine.find.d';
      showError(e);
      return;
    }
    this.sending = false;
    showSuccess(mainStore.isEn ? 'Code sent successfully' : '发送成功！');
    this.timer = setInterval(() => {
      if (this.countDwon === 0) {
        this.countDwon = 60;
        this.sendBtnText = 'mine.find.d';
        clearInterval(this.timer);
        return;
      }
      this.countDwon -= 1;
      this.sendBtnText = `${this.countDwon}s`;
    }, 1000);
  }



  public async editSetting(data: any) {
    console.log("data", data);
    // if(){

    // }
    var reg = /^[0-9]*$/;
    if (data['phone'] && !reg.test(data['phone'])) {
      showError('请输入正确的手机号');
      return;
    }
    let _data: any = {};
    for (let key in data) {
      if (data[key]) {
        _data[key] = data[key];
      }
    }
    if (_data.birthday) {
      _data.birthday = _data.birthday.format("YYYY-MM-DD")
    }
    let originData = JSON.parse(JSON.stringify(_data));
    showLoading(mainStore.isEn ? 'Submitting...' : "提交中...");
    try {
      await new SettingService(_data).send();
      if (userStore.userInfo) {
        let _userInfo: UserInfo = userStore.userInfo;
        for (let key in originData) {
          _userInfo[key] = originData[key];
        }
        userStore.updateUser(_userInfo);
      }
    } catch (e: any) {
      showError(e)
      return;
    }
    showSuccess(mainStore.isEn ? 'Edit done!' : "修改成功！");
    this.editModalVisible = false;
    this.editSingleModalVisible = false;
  }

  public async changeAccount(data: any) {
    showLoading(mainStore.isEn ? 'Submitting...' : "提交中...");
    try {
      await new ChangeAccountService(data).send();
    } catch (e: any) {
      showError(e)
      return;
    }
    this.passwordModalVisible = false;
    showSuccess(mainStore.isEn ? 'Edit done!' : "修改成功！");
  }


  public async changePssowrd(data: any) {
    showLoading(mainStore.isEn ? 'Submitting...' : "提交中...");
    try {
      await new ChangePasswordService(data).send();
    } catch (e: any) {
      showError(e)
      return;
    }
    this.passwordModalVisible = false;
    showSuccess(mainStore.isEn ? 'Edit done!' : "修改成功！");
  }


  public async OssUpload(data: any, callback?: Function, isBase64?: boolean) {
    !callback && showLoading('上传中');
    var client = new (window as any).OSS({
      region: GlobalConfig.ossRegion, //阿里云获取
      accessKeyId: GlobalConfig.ossId,
      accessKeySecret: GlobalConfig.ossSecret,
      endpoint: 'https://' + GlobalConfig.ossEndPoint,
      bucket: GlobalConfig.ossBucket  //要存储的目录名
    });
    var file;
    var fileName;
    if (isBase64) {
      var _blob = dataURLtoBlob(data);
      var suffix = data.split(';')[0].split(':')[1].split('/')[1];//文件扩展名
      fileName = String(new Date().getTime()) + '.' + suffix;
      file = blobToFile(_blob, fileName)
    } else {
      file = data;
      var nameSplit = file.name.split(".");
      fileName = timestamp() + '.' + nameSplit[nameSplit.length - 1];
    }
    var storeAs = (!callback ? 'avatar/' : 'covers/') + fileName;
    client.put(storeAs, file).then(function (result: any) {
      var url = result.res.requestUrls[0];
      if (callback) {
        callback(url);
        return;
      }
      new SettingService({ avatar: url }).send().then(() => {
        !callback && showSuccess('上传成功！')
        if (userStore.userInfo) {
          var _userInfo: UserInfo = userStore.userInfo;
          _userInfo.avatar = url;
          userStore.updateUser(_userInfo);
        }

      }, () => {
        showError('上传失败！');
      });
      return null;
    }).catch(function (err: any) {
      !callback && showError('上传失败！');
      console.log('oss upload error', err);
      callback && callback(null);
      return null;
    });

  }

}

const settingStore = new SettingStore();

export { settingStore, SettingsType };