import { FC, useEffect, useState } from "react";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { mainStore } from "../../stores/MainStore";
import { useRequestLoadMore, useScrollToBottomEvent, useTranslator } from "../../utils/hooks";
import { Ajax } from "../../utils/http";

import './Notification.scss';
import { observer } from "mobx-react";
import imgMessage from '../../images/main/message.png';
import imgFold from '../../images/main/fold.png';
import imgExpand from '../../images/main/expand.png';
import { Img } from "../../components/Image";
import { translate } from "../../components/Text";
import { Button, Form, Input } from "antd";
import { CommentReplyService } from "../../services/CommentService";
import { showError, showSuccess } from "../../components/Message";
import { userStore } from "../../stores/UserStore";
import { UnreadMessage, UserInfo } from "../../services/LoginService";
import { setTimeout } from "timers";
import { divide } from "lodash";


const tabs = [
  { type: 1, key: 'systemMessageCount' },
  { type: 2, key: '' },
  { type: 3, key: 'replyToMyMessageCount' },
  { type: 4, key: 'myAttentionMessageCount' },
];

interface NotificationData {
  id: string
  title: string
  content: string
  entitle: string
  encontent: string
  date: string
  type: string
  isread: string
  user?: NotificationUser
  comment?: NotificationComment
  video?: NotificationVideo
  dynamic?: NotificationDynamic
}

interface NotificationUser {
  userid: string
  avatar: string
  name: string
}

interface NotificationVideo {
  id: string
  collectId?: string
  cover: string
  title: string
  desc: string
}

interface NotificationDynamic {
  id: string
  userid: string
  content: string
  image: string
}

interface NotificationComment {
  id: string
  pid: string
  content: string
  objectid: string
  objecttype: string
}

async function getMessageData({ type = 1, pageNum = 1, pageSize = 10 }): Promise<NotificationData[]> {
  userStore.refreshUser();
  let res = await new Ajax<any[]>('/user/noticeList', { type, p: pageNum, psize: pageSize }).post();
  let result: NotificationData[] = res.map((r: any) => {
    let _data: NotificationData = {
      id: r.id,
      title: r.title,
      content: r.content,
      entitle: r.entitle,
      encontent: r.encontent,
      type: r.type,
      date: r.createtime,
      isread: r.isread
    }
    if (type == 3) {
      _data = Object.assign(
        {
          user: {userid:r.info.userid, avatar: r.info.userimg, name: r.info.username },
          comment: {
            id: r.info.id,
            pid: r.info.pid,
            content: r.info.content,
            objectid: r.info.objectid,
            objecttype: r.info.objecttype
          },
          video: r.info.info.visibletype ? undefined : {
            id: r.info.info.id,
            collectId: r.info.info.collectid,
            title: r.info.info.title,
            cover: r.info.info.thumb,
            desc: r.info.info.abstract
          },
          dynamic: r.info.info.visibletype ? {
            id: r.info.info.id,
            userid: r.info.info.userid,
            content: r.info.info.content,
            image: r.info.info.images ? r.info.info.images.split(',')[0] : ''
          } : undefined
        }, _data);
    } else if (type == 4) {
      _data = Object.assign(
        {
          user: { userid:r.info.userid,avatar: r.info.avatar || r.info.userimg, name: r.info.username },
          video: r.info.visibletype ? undefined : {
            id: r.info.id,
            collectId: r.info.collectid,
            title: r.info.title,
            cover: r.info.thumb,
            desc: r.info.abstract
          },
          dynamic: r.info.visibletype ? {
            id: r.info.id,
            userid: r.info.userid,
            content: r.info.content,
            image: r.info.images ? r.info.images.split(',')[0] : ''
          } : undefined
        }, _data);

    }
    return _data;
  });
  return result;
}


export const NotificationPage: FC<{}> = observer((props: any) => {
  let { type } = props.match.params;
  const { data = [], reloading, reload, moreLoad } = useRequestLoadMore(getMessageData);
  const { translate } = useTranslator();
  const [messageType, setMessageType] = useState(parseInt(type || '1'));
  useEffect(() => reload({ type: messageType }), [messageType]);
  useScrollToBottomEvent(moreLoad);
  let _message: UnreadMessage;
  if (userStore.userInfo) {
    _message = userStore.userInfo.unreadMessage;
  }

  return (
    <div>
      <div className="notification-page">
        <div className="notification-nav">
          {tabs.map((t) => t.key ? (
            <a className={t.type === messageType ? 'active' : ''} onClick={() => setMessageType(t.type)}>
              {translate(`notification.${t.type}`)}
              {_message[t.key] > 0 && <div className="dot"></div>}
            </a>
          ) : <></>)}
        </div>
        {reloading ? <Loading size="mini" /> : data.length <= 0 ? <NoData /> : (
          <div className="message-list">
            {data.map((d: NotificationData) => <NotificationItem isEn={mainStore.isEn} item={d} />)}
          </div>
        )}

      </div>
    </div>
  );
})

interface NotificationItemProps {
  item: NotificationData
  isEn: boolean
}

export const NotificationItem = (props: NotificationItemProps) => {
  const { item, isEn } = props;
  const [showMore, setShowMore] = useState(false);
  const [reply, setReply] = useState(false);
  const [read, setRead] = useState(item.isread == '1');
  const [replying, setReplying] = useState(false);

  var replyComment = async (replyText: string) => {
    if (!replyText || replying) {
      return;
    }
    setReplying(true);
    try {
      await new CommentReplyService(parseInt(item?.comment?.objectid || ''), item?.comment?.objecttype || '1', replyText, item.comment?.id || '', `${parseInt(item.comment?.pid || '0') || item.comment?.id}`).send();
    } catch (e) {
      setReplying(false);
      showError(`${e}`);
      return;
    }
    showSuccess(isEn ? 'Send successfully' : '发送成功');
    setReplying(false);
    setReply(false);
  }

  var readMessage = () => {
    new Ajax('/user/noticeList', { id: item.id }).post().then(() => {
      setRead(true);
      let key = tabs[parseInt(item.type) - 1].key;
      let _user: UserInfo = JSON.parse(JSON.stringify(userStore.userInfo));
      _user.unreadMessage[key] = (_user.unreadMessage[key] || 0) - 1;
      userStore.updateUser(_user);
    });
  }
  useEffect(() => {
    if (item.comment) {
      readMessage();
    }
  }, [])
  if (item.dynamic) {
    console.log("itemitem", item)
  }
  return (
    <div key={item.id} className="item">
      <div className="user">
        {item.type == '1' ? <div className="icon"><Img src={imgMessage} alt="" /></div> : <img className="avatar"  onClick={() => {
        GlobalConfig.history.push(`/main/userCenter/${item.user?.userid}/videoList`)
      }} src={item.user?.avatar} alt="" />}
        {!read && <div className="dot"></div>}
      </div>
      <div className="info">
        <div className="top">
          <div className="leftContent">
            <div className="topInfo">
              <span className="title">{isEn ? item.entitle : item.title}</span>
              <span className="date">{item.date.slice(0, 16)}</span>
            </div>
            {(showMore || item.comment) && <div className="text">{item.comment ? item.comment.content : isEn ? item.encontent : item.content}</div>}
          </div>
          {item.comment && <div className="reply" onClick={() => {
            setReply(!reply)
          }}>{translate(`home.video.${reply ? 'h' : 'g'}`)}</div>}
        </div>
        {reply && <div className="comment">
          <Form
            name="normal_login"
            className="login-form"
            onFinish={(v) => {
              replyComment(v.replyText);
            }}>
            <div className="ttp-comment-input reply-wrapper">
              <Form.Item
                name="replyText"
              >
                <Input.TextArea className="comment-textarea" id="commentInput" placeholder={translate('home.video.c')} />
              </Form.Item>
            </div>
            <div className="action">
              <Button className="submit-btn disable" htmlType="submit" loading={replying} >{translate('home.video.d')}</Button>
            </div>
          </Form>


        </div>}
        {(showMore || item.comment) && <div className="content">
          {item.video && <div className="videoContent" onClick={() => {
            let link = item.video?.collectId != '0' ? `/main/videoPlay/c${item.video?.collectId}/${item.video?.id}` : `/main/videoPlay/videocreator/${item.video.id}`;
            GlobalConfig.history.push(link)
          }}>
            <img src={item.video.cover} alt="" className="cover" />
            <div className="detail">
              <div className="title">{item.video.title}</div>
              <div className="desc">{item.video.desc}</div>
            </div>
          </div>}
          {item.dynamic && <div className="dynamicContent" onClick={() => {
            GlobalConfig.history.push(`/main/communityDetail/${item.dynamic?.userid}/${item.dynamic?.id}`);
          }}> {item.dynamic.image && <img src={item.dynamic.image} alt="" />}
            <div className="text">
              {item.dynamic.content}
            </div>
          </div>}

        </div>}

        {!item.comment && <div className="more" onClick={() => {
          setShowMore(!showMore);
          if (!read) {
            readMessage();
          }

        }}>
          <span>{translate(`common.${showMore ? 'd' : 'w'}`)}</span><img src={!showMore ? imgExpand : imgFold} alt="" />
        </div>}
      </div>
    </div>
  );
}