
import { Ajax } from '../utils/http';

export class StockFollowService {
    private symbols: string;
    constructor(_symbols: string) {
        this.symbols = _symbols;
    }

    public async send(): Promise<StockFollowItem[]> {
        var response: any;
        var data = {
            symbols_list: this.symbols,
        }
        try {
            response = await new Ajax('/oliview_watchlist', data, true).get();
        } catch (e) {
            throw e;
        }
        var result:StockFollowItem[] =[];
        (response||[]).forEach((r:any) => {
            var keys=Object.keys(r);
            var _r=r[keys[0]];
            _r['symbol'] = keys[0];
            result.push(_r);
        });
        return result;
    }
}

export interface StockFollowItem {
    symbol: string;
    date: string;
    symbol_name: string;
    price: number;
    daily_return: number;
    daily_change: number;
}