import { Headers } from './Header/Header';
import Routes from '../components/Routes';
import { useEffect } from 'react';
import { researchStore } from '../stores/ResearchStore';
import '../scss/index.scss';
import { userStore } from '../stores/UserStore';
import eventBus from '../utils/eventBus';
import { observer } from 'mobx-react';

import StatisticStore from '../stores/StatisticStore';
import { creatorVideoStore } from '../stores/CreatorVideoStore';
import { LocalStorageService } from '../utils/storage';
import { Layout, Menu, Breadcrumb, BackTop } from 'antd';

import SideTabs from './Main/SideTab';
import { mainStore } from '../stores/MainStore';

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

interface IndexProps {
  routes: any;
}
export const Index = observer((props: IndexProps) => {
  const { routes } = props;
  useEffect(() => {
    userStore.init();

    StatisticStore.getWatchSymbolist();
    StatisticStore.getFollowed();
    mainStore.getLabels();
    mainStore.getBanners();
    researchStore.getCategory();
    creatorVideoStore.getSubscribeList();
  }, []);
  return (
    <div className="appWrapper">
      <Layout style={{ minHeight: '100vh' }}>
        <Headers />
        <Layout className="contentWrapper">
          <SideTabs />
          <Content >
            <Routes routes={routes} />
          </Content>
        </Layout>
      </Layout>

      <BackTop target={() => document.getElementsByClassName('ant-layout-content')[0] as any} />
    </div>
  );
});

export default Index;
