import { translate } from "../../components/Text";
import { ApplyPage } from "../Publish/Apply";
import {  Modal } from "./Common";


export interface ApplyModalProps{
    onClose:() => void;
}

export const ApplyModal= (props:ApplyModalProps) => {
  return (
    <Modal className="creator-video-upload-modal creator-apply-modal" centered={true}  title={translate("creator.center.4")} visible={true} onCancel={props.onClose}>
        <ApplyPage />
    </Modal>
  );
}