import { FC, useEffect, useState } from "react";
import { NavLink, useHistory, useParams } from "react-router-dom";
import { ButtonProps, Modal as AntdModal, ModalProps, Button as AntdButton, Upload, Popconfirm, Input } from 'antd';
import { IconAdd, IconCreateVideo, IconDelete, IconEdit, IconFilter, IconPageFirst, IconPageLast, IconPageNext, IconPagePrev, IconPlayList, IconShare, IconSorter, IconUpload, IconVideoFiles, IconVideoFormat, IconVideoScreen, IconViews } from "./Icons";

import './UserCenter.scss';
import { Button, Header, ListPagenation, ListToolBar, Modal } from "./Common";
import { CreatorVideoDeleteService, CreatorVideoItem, CreatorVideoService } from "../../services/CreatorVideoService";
import { useRequest, useTranslator } from "../../utils/hooks";
import { VideoFormModal } from "./VideoFormModal";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { VidoeEditModal } from "../CreatorCenter/VideoEditorModal";
import { Image } from "../../components/Image";
import { GridView } from "../../components/GridView";
import { conversionTime } from "../../utils/globalFuncs";

const { Search } = Input;


const getVideosData = ({ id = '', pageNum = 1, pageSize = 999, sortType = 'hot', sortAsc = 'desc', searchkey = '' }) => {
  return new CreatorVideoService({ p: pageNum, psize: pageSize, userid: id, sort: sortType, order: sortAsc, searchkey }).send().then((list) => ({ list, sortType, sortAsc, searchkey }));
}

const deleteVideo = ({ id = '' }) => new CreatorVideoDeleteService({ objectid: id, objecttype: 2 }).send();

export const CreatorCenterMyChannelVideos: FC<{}> = () => {
  const { id } = useParams<any>();
  const { translate } = useTranslator();
  const { loading, data, load } = useRequest(getVideosData, { autoLoad: false });
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [curEditItem, setCurEditItem] = useState<CreatorVideoItem>();
  const his = useHistory();
  const { list = [], sortAsc = 'desc', sortType = 'hot', searchkey = '' } = data ?? {};

  useEffect(() => load({ id }), []);
  return (
    <Header onAdd={() => setShowCreateModal(true)} addBtn={<>{IconCreateVideo} {translate('creator.myVideos.3')}</>}>
      {loading ? <Loading size="mini" /> : !list.length ? <></> : <> <div className="videos-tools">
        <ListToolBar searchkey={searchkey} sortType={sortType} sortAsc={sortAsc} useSearch onChange={(t, a, s) => load({ id, sortAsc: a, sortType: t, searchkey: s })} />
      </div>
        <div className="videos-list">
          {loading ? <Loading size="mini" /> : !list.length ? <></> :
            <GridView id="videoList" space={10} itemCount={list.length} itemBuilder={(i) => {
              let it = list[i];
              const doJump = () => his.push(`/main/videoPlay/ua${id}/${it.id}`);
              return (
                <div key={it.id} className="video-item">
                  <div className="thumb">
                    <Image src={it.thumb} onClick={doJump} />
                    {it.duration && <div className="duration">{conversionTime(it.duration, true)}</div>}
                    {it.status=='2' && <div className="transcode">{translate("creator.myVideos.60")}</div>}
                  </div>
                  <div className="video-info">
                    <div className="title" onClick={doJump}>{it.title}</div>
                    <div className="info" onClick={doJump}>
                      <div className="time">
                        <div className="gray">{translate('creator.myVideos.1')}</div>
                        <div>{it.createtime.slice(0, 16)}</div>
                      </div>
                      <div className="gray">{it.viewcount} {translate('creator.myVideos.2')}</div>
                    </div>
                    <div className="desc gray">
                      {it.abstract}
                    </div>
                    <div className="btns">
                      {parseInt(it.price || '0') > 0 && it.isfree != '1' && <div className="fee-btn">${it.price}</div>}
                      {parseInt(it.price || '0') == 0 && it.isfree != '1' && <div className="fee-btn">FREE</div>}
                      {it.isfree == '1' && <div className="fee-btn">VIP</div>}
                      <div className="other-btns">
                        <a className="btn" onClick={() => setCurEditItem(it)}>{IconEdit}</a>
                        {it.isbuy != 1 && <Popconfirm title={translate('creator.myVideos.4')} onConfirm={() => deleteVideo({ id: it.id }).then(() => load({ id }))}>
                          <a className="btn">{IconDelete}</a>
                        </Popconfirm>}
                        {/* <a className="btn">{IconViews}</a>
                    <a className="btn">{IconShare}</a> */}
                      </div>
                    </div>
                  </div>
                </div>
              );
            }}></GridView>}
        </div>
      </>}
      {showCreateModal && <VideoFormModal onClose={() => setShowCreateModal(false)} onPublish={() => load({ id })} />}
      <VidoeEditModal
        visible={!!curEditItem}
        currentItem={curEditItem}
        onComplete={() => load({ id, sortType })}
        onClose={() => setCurEditItem(undefined)} />
    </Header>
  );
};
