
import { observer } from 'mobx-react';
import { newsStore } from '../../stores/NewsStore';
import './News.scss';
import { Icon_Back } from '../../images/back';
export const NewsDetailPage = observer((props: any) => {
  var link = `http://translate.google.com/translate?u=${newsStore.detailUrl}&amp;langpair=en%7Czh-CN`;
  return (
    <div className="NewsDetailPage">
      <div className="top">
        <img src={Icon_Back} alt="" onClick={() => {
          props.history.goBack()
        }} />
        <div className="title">{newsStore.detailTitle.slice(newsStore.detailTitle[0]==':'?1:0)}</div>
        <div></div>
      </div>
      <div className="detailContent">
        <iframe src={newsStore.detailUrl} scrolling="auto" frameBorder="0"></iframe>
      </div>
    </div>
  );
}
)