import { Upload } from 'antd';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import { Avatar } from 'antd';
import { settingStore } from '../../stores/SettingStore';
import { Icon_camera } from '../../images/index/camera';

export interface AvatarUploadProps {
    child?: any
}
export const AvatarUpload = observer((props: any) => {
    const beforeUpload = (file: any) => {
        settingStore.OssUpload(file);
        return false;
    }

    return (
        <Upload
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            beforeUpload={beforeUpload}
        >
            <Avatar src={userStore.userInfo?.avatar} />
            {props.child || <div className="mask">
                <img src={Icon_camera} alt="" />
            </div>}
        </Upload>
    );
})

