import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { StripeOrder, StripeOrderService } from "../services/StripeOrderService";

class StripeStore {
  public showStripe: boolean = false;
  public amount?: string;
  public ordertype?: number;
  public objectid?: string;
  public submitting:boolean = false;

  constructor() {
    makeAutoObservable(this);
  }

  pay(amount: string) {
    this.showStripe = true;
    this.amount = amount;
    setTimeout(() => {
      // let test = document.getElementById('引用的iframeId').contentWindow.document.getElementById('修改样式的Id');

      // test.style.width = "200px";
    }, 100)



  }

  async getOrder(id?:string): Promise<string> {
    let res: StripeOrder;
    let _amount = parseFloat(this.amount || '0') * 100;
    try {
      res = await new StripeOrderService(`${_amount}`,this.ordertype,this.objectid,id).send();
    }catch(e){
      throw e;
    }
    return res.client_secret;
  }
}

const stripeStore = new StripeStore();

export { stripeStore };