
import { Ajax } from '../utils/http';

export class StockMarketTitleService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<MarketTitle> {
        var response: MarketTitle;
        var data = {
            symbol: this.symbol,
        }
        response = await new Ajax<MarketTitle>('/oliview_market_title', data, true).get();
        return response;
    }
}
/*tsModel0*/
export interface MarketTitle {
    symbol: string;
    name: string;
    latest_date: string;
    latest_price: number;
    latest_return: number;
}
