import ContentLoader from "react-content-loader"
import './Skelleton.scss';

export const LableLoader = (props: any) => (
    <ContentLoader
        className="label"
        speed={2}
        // viewBox="0 0 400 460"
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
    >
        <rect className="img" x="0" y="0" rx="2" ry="2" width="400" height="400" />
    </ContentLoader>
)


export const BannerLoader = (props: any) => (
    <ContentLoader
        className="banner"
        speed={2}
        // viewBox="0 0 400 460"
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
    >
        <rect x="0" y="0" rx="2" ry="2" width="400" height="400" />
    </ContentLoader>
)

export const VideoLoader = (props: any) => (
    <ContentLoader
        className="video"
        speed={2}
        // viewBox="0 0 400 460"
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
    >
        <rect className="cover" x="0" y="0" rx="2" ry="2" width="400" height="400" />
        <circle className="avatar" cx="31" cy="151" r="15" />
        <rect className="title" x="58" y="188" rx="2" ry="2" width="140" height="10" />
        <rect className="name" x="58" y="154" rx="2" ry="2" width="140" height="10" />
    </ContentLoader>
)




export const SkelletonLoader = (props: any) => (
    <ContentLoader
        key={props.key}
        speed={2}
        className={props.className}
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
    >
        {props.children}
    </ContentLoader>
)