import styles from './Menu.module.scss';
import ChartStore from '../../../../stores/ChartStore';
import icons from '../../../../images/chartImages';
import { observer } from 'mobx-react';

export const DeleteBtn = observer((props:any) => {
    return (
        <li key={333} className="Indicators">
            <div aria-controls="simple-menu" style={{margin:'0 auto'}} aria-haspopup="true" onClick={() => {ChartStore.removeAnnotation()}}>
                <img className={styles.img} src={icons.annotations_visible} alt='' />
            </div>
       </li >
    );
})