import { FC, useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

import './MyCommunity.scss';
import { Header, } from "./Common";
import { useRequest, useRequestLoadMore, useScrollToBottomEvent, useTranslator } from "../../utils/hooks";
import { VideoFormModal } from "./VideoFormModal";
import { Loading } from "../../components/Loading";
import { CommunityDetailService, CommunityService } from "../../services/CommunityService";
import { userStore } from "../../stores/UserStore";
import { CommunityImgModal, CommunityPostModal } from "./CommunityPostModal";
import imgComment from '../../images/main/comment.png';
import IconLike from '../../images/main/like.png';
import { Img } from "../../components/Image";

import { CommentPage } from "../Creator/Comment";
import { CommentLikeService, CommunityLikeService } from "../../services/VideoService";
import { observer } from "mobx-react";


const getCommunityDetailData = ({ userid = '', id = '' }) => {
  return new CommunityDetailService(id, userid).send();
}


export const MyCommunityPage: FC<{}> = observer(() => {
  const { id } = useParams<any>();
  const { translate } = useTranslator();
  const [showPost, setShowPost] = useState(false);
  return (
    <Header  >
      <div className="community-wrapper">
        <div className="user">
          <div className="name">{userStore.userInfo?.username}{translate('community.11')}</div>
          <div className="post" onClick={() => setShowPost(true)}>{translate('community.9')}</div>
        </div>
        <CommunityPage userid={id} postComplete={!showPost} />
      </div>
      <CommunityPostModal
        visible={!!showPost}
        onComplete={() => { }}
        onClose={() => setShowPost(false)} />
    </Header>
  );
});



export const CommunityPage: FC<{ userid?: string, postComplete?: boolean }> = ({ userid, postComplete }) => {
  const { id } = useParams<any>();
  const [refresh, setRefresh] = useState(false);
  const [showImg, setShowImg] = useState(false);
  const [imgIdx, setImgIdx] = useState(0);
  const [imgs, setImgs] = useState<Array<string>>([]);
  const his = useHistory();

  const { reloading, data = [], reload, moreLoad } = useRequestLoadMore(({ pageNum, pageSize }) =>
  new CommunityService({ creatorid: userid||id, p: pageNum, psize: pageSize }).send()
  );

  useEffect(() => {
    (postComplete || !userid) && reload({});
  }, [userid, postComplete]);
  useScrollToBottomEvent(moreLoad);
  return (
    <>
      {(reloading || !data) ? <Loading size="mini" /> : !data!.length ? <></> : <>
        <div className="community-list">
          {data?.map((d, i) => {
            let images: Array<string> = d.images.split(",");
            return <div key={i} className="item">
              <div className="avatar"><img src={d.avatar} alt="" /></div>
              <div className="info">
                <div className="name">{d.username}</div>
                <div className="content">{d.content}</div>
                {d.images && <div className="pics">
                  {images.map((img, i) => <img key={i} src={img} alt="" onClick={() => {
                    setImgIdx(i);
                    setImgs(images);
                    setShowImg(true)
                  }} />)}
                </div>}
                <div className="bottom">
                  <div className="date">{d.createtime.slice(0, 16)}</div>
                  <Img className="comment" src={imgComment} alt="" onClick={() => {
                    his.push(`/main/communityDetail/${userid || id}/${d.id}`);
                  }} />
                  <div className="comment-count">{d.commentcount}</div>
                  <Img
                    className="like"
                    src={IconLike}
                    alt=""
                    onClick={() => {
                      new CommunityLikeService(d.id, d.islike == 0 ? 1 : 0).send();
                      for (var i = 0; i < data.length; i++) {
                        if (d.id == data[i].id) {
                          data[i].likecount = `${parseInt(data[i].likecount) + (data[i].islike == 0 ? 1 : -1)}`;
                          data[i].islike = data[i].islike == 0 ? 1 : 0;
                          break;
                        }
                      }
                      setRefresh(!refresh);

                    }}
                  />
                  <div className="likeCount left">{d.likecount}</div>
                </div>
              </div>
            </div>
          })}
        </div>
      </>}
      <CommunityImgModal visible={showImg} onClose={() => {
        setShowImg(false);
      }} images={imgs} idx={imgIdx} />
    </>
  );
};

export const CommunityDetailPage: FC<{}> = () => {
  const { userid, id } = useParams<any>();
  const { translate } = useTranslator();
  const { loading, data, load } = useRequest(getCommunityDetailData, { autoLoad: false });
  const [refresh, setRefresh] = useState(false);
  const his = useHistory();
  useEffect(() => load({ userid, id }), []);
  return (
    <div className="communityDetail">
      {(loading || !data) ? <Loading size="mini" /> : <>
        <div className="community-list">
          <div className="item">
            <div className="avatar"><img src={data.avatar} alt="" /></div>
            <div className="info">
              <div className="name">{data.username}</div>
              <div className="content">{data.content}</div>
              {data.images && <div className="pics">
                {data.images.split(",").map((img) => <img src={img} alt="" />)}
              </div>}
              <div className="bottom">
                <div className="date">{data.createtime.slice(0, 16)}</div>
                <Img className="comment" src={imgComment} alt="" />
                <div className="comment-count">{data.commentcount}</div>
                <Img
                  className="like"
                  src={IconLike}
                  onClick={() => {
                    new CommunityLikeService(id, data.islike == 0 ? 1 : 0).send();
                    let count: number = parseInt(data.likecount);
                    data.likecount = `${data.islike == 0 ? count + 1 : count - 1}`;
                    data.islike = data.islike == 0 ? 1 : 0;
                    setRefresh(!refresh);
                  }}
                  alt=""
                />
                <div className="likeCount left">{data.likecount}</div>
              </div>
            </div>
          </div>
        </div>
      </>}
      <div className="comment-content">
        <CommentPage isSelf={userid == userStore.userInfo?.id} objectid={id} objecttype={6} isCommunity={true} />
      </div>
    </div>
  );
};