import GlobalConfig from '../config/GlobalConfig';
import { Ajax } from '../utils/http';


export class TranslateService {
    private text: string;
    constructor(_text: string,) {
        this.text = _text;
    }
    public async send(): Promise<string> {
        var response: TranslationsData;
        var data = {
            target: 'zh',
            key: GlobalConfig.googleKey,
            q: this.text
        }
        try {
            response = await new Ajax<TranslationsData>('https://translation.googleapis.com/language/translate/v2', data, false, true).get();
        } catch (e) {
            throw e;
        }
        return response.data.translations[0].translatedText;
    }
}

/*Translations*/
export interface Translations {
    translatedText: string;
    detectedSourceLanguage: string;
}

/*Data*/
export interface TranslationsList {
    translations: Translations[];
}

/*tsModel2*/
export interface TranslationsData {
    data: TranslationsList;
}

