import { Button, Form, Input, Spin, Radio, Checkbox, Tooltip } from "antd";
import { useEffect, useState } from "react";
import { CreatorCollectionDetailService, CreatorVideoDeleteService, CreatorVideoItem, CreatorVideoService, CreatorVideoSortService } from "../../services/CreatorVideoService";
import { useRequestLoadMore, useScrollToBottomEvent, useQuery, useCurrentUser, useRequest, useVideoSearch, useTranslator } from '../../utils/hooks';
import { NavHeader } from "./NavHeader";
import { VideoList } from "./VideoList";
import './CreatorVideoList.scss';
import { Loading } from "../../components/Loading";
import { VidoeEditModal } from "./VideoEditorModal";
import { useHistory } from "react-router";
import { CollectionDataService, CollectionListService } from "../../services/CollectionListService";
import sortImg from '../../images/creator/filter_list.png';
import { IconCreateVideo, IconDelete, IconEdit, IconEditCover, IconEditWhite, IconSort, IconVideoPlay, IconVideoPlayAll, IconVideoScreen } from "../UserCenter/Icons";
import TextArea from "antd/lib/input/TextArea";
import { CreateCollectService } from "../../services/PublishService";
import { showError, showSuccess } from "../../components/Message";
import { VideoFormModal } from "../UserCenter/VideoFormModal";
import GlobalConfig from "../../config/GlobalConfig";
import { ImageUploader } from "../../components/ImageUploader";
import { px } from "../../utils/rem";
import { translate } from "../../components/Text";
import NoData from "../../components/NoData";
import { mainStore } from "../../stores/MainStore";
import imgTips from '../../images/main/tips.png';

const IconInfo = (
  <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd" clip-rule="evenodd" d="M11 6C11 8.76142 8.76142 11 6 11C3.23858 11 1 8.76142 1 6C1 3.23858 3.23858 1 6 1C8.76142 1 11 3.23858 11 6ZM5.5 3.5C5.5 3.22386 5.72386 3 6 3C6.27614 3 6.5 3.22386 6.5 3.5V6.5C6.5 6.77614 6.27614 7 6 7C5.72386 7 5.5 6.77614 5.5 6.5V3.5ZM6 8C5.72386 8 5.5 8.22386 5.5 8.5C5.5 8.77614 5.72386 9 6 9C6.27614 9 6.5 8.77614 6.5 8.5C6.5 8.22386 6.27614 8 6 8Z" fill="#333333" />
  </svg>
);


const deleteVideo = ({ id = '' }) => new CreatorVideoDeleteService({ objectid: id, objecttype: 2 }).send();

const sortVideosData = ({ id = '', vids = '' }) => new CreatorVideoSortService({
  collectid: id,
  videocreateid: vids
}).send();

const getVideoColletData = ({ id = '' }) => Promise.all([
  new CollectionDataService(id).send(),
  new CreatorCollectionDetailService(id).send(),
]);

const saveVideoColletData = (data: any) => Promise.all([
  new CreateCollectService(data).send().then(() => {
    showSuccess(translate("report.5"))
  }, (err) => {
    showError(err)
  }),
]);

/**
 * 页面: 指定视频集锦内的视频
 * @returns 
 */
export function CreatorCollectionDetailVideoList() {
  const his = useHistory();
  const user = useCurrentUser();
  const { translate } = useTranslator();
  const { id } = useQuery();
  const [sortMode, setSortMode] = useState(false);
  const [delMode, setDelMode] = useState(false);
  const [curEditItem, setCurEditItem] = useState<CreatorVideoItem>();
  const { data: reqData, loading, load } = useRequest(getVideoColletData, { autoLoad: false });
  const { loading: loadingSave, load: loadSave } = useRequest(saveVideoColletData, { autoLoad: false });
  const { loading: sorting, loadWithPromise: submitSort } = useRequest(sortVideosData, { autoLoad: false });
  const { loading: deling, loadWithPromise: submitDel } = useRequest(deleteVideo, { autoLoad: false });
  const [sortData, setSortData] = useState([] as CreatorVideoItem[]);
  const [delData, setDelData] = useState([] as string[]);
  const [visibleModal, setVisibleModel] = useState(false);
  const [cover, setCover] = useState('');
  const [collInfo, rowData = []] = reqData ?? [];
  const [free, setFree] = useState(collInfo?.price != '0');
  const [auth, setAuth] = useState<string>('price');
  const price = collInfo?.price ?? '1';
  const isSelf = rowData.every((it) => it.userid === user?.id);
  const data = rowData.map(it => ({ ...it, collectid: id, lookauth: isSelf ? 1 : it.lookauth, price }));

  const doSort = () => {
    submitSort({
      id,
      vids: sortData.map(it => it.id).join(',')
    }).then(() => {
      setSortMode(false);
      load({ id });
    }, (e) => {
      showError(`${e}`);
    });
  };

  const doDel = () => {
    submitDel({
      id: delData.join(',')
    }).then(() => {
      setDelMode(false);
      load({ id });
    }, (e) => {
      showError(`${e}`);
    });
  };

  useEffect(() => load({ id }), [id]);
  useEffect(() => {
    if (collInfo) {
      if (parseFloat(collInfo.price) > 0) {
        let _auth = collInfo.isfree == '0' ? 'price' : 'member';
        setAuth(_auth);
      } else {
        let _auth = collInfo.isfree == '0' ? 'free' : 'member';
        setAuth(_auth);
      }
    }
  }, [collInfo]);

  useEffect(() => {
    if (sortMode) {
      setSortData(data);
    }
  }, [sortMode]);
  const onChangeBg = (imgUrl: string) => {
    new CreateCollectService({ id: id, cover: imgUrl }).send().then(() => setCover(imgUrl));
  }
  return (
    <div className="creator-video-list-page">
      <NavHeader title={collInfo?.name ?? ''} className="creator-video-list-header">
        <div className="create-btn-area">
          <div className="create-btn" onClick={() => setVisibleModel(true)}>{IconCreateVideo} {translate('creator.playlist.17')}</div>
        </div>
      </NavHeader>
      {loading ? <Loading size="mini" /> : (
        !collInfo ? <div><NoData /></div> :
          <div className="collectionContent">
            <div className="creator-collect-list">
              <div className="collect-cover" >
                <img src={cover || collInfo.cover} alt="" />
                <ImageUploader width={px(240)} height={px(135)} className="play" onChange={onChangeBg}>
                  <div className="play">
                    <div className="icon-play">{IconEditCover}</div>
                    <div >{translate('creator.playlist.28')}</div>
                  </div>
                </ImageUploader>
              </div>


              <Form
                name="basic"
                initialValues={{ remember: true }}
                autoComplete="off"
                onFinish={async (data) => {
                  data.id = collInfo.id;
                  let values = JSON.parse(JSON.stringify(data));
                  if (auth == 'price') {
                    delete values.isfree;
                  } else if (auth == 'free') {
                    values.isfree = 0;
                    delete values.price;
                    delete values.trycount;
                  } else {
                    values.isfree = 1;
                  }
                  loadSave(values);
                }}
              >
                <div className="editTitle">
                  <div className="leftContent">
                    <div className="text">{translate('creator.playlist.6')}:</div>
                  </div>
                </div>
                <div className="editInput">
                  <Form.Item
                    name="name"
                  >
                    <Input placeholder={collInfo.name} />
                  </Form.Item>
                </div>

                <div className="editTitle">
                  <div className="leftContent">
                    <div className="text">{translate('creator.playlist.16')}:</div>
                  </div>
                </div>
                <div className="editInput">
                  <Form.Item
                    name="describe"
                  >
                    <Input.TextArea placeholder={collInfo.describe} />
                  </Form.Item>
                </div>


                <div className="editTitle">
                  <div className="leftContent">
                    <div className="text">{translate('creator.playlist.7')}:</div>
                  </div>

                  <Radio.Group defaultValue={auth} onChange={(v) => {
                    setAuth(v.target.value);
                  }} buttonStyle="solid">
                    <Radio.Button value="price">{translate('creator.myVideos.11')}</Radio.Button>
                    <Radio.Button value="free">{translate('creator.myVideos.10')}</Radio.Button>
                    <Radio.Button value="member">{translate('creator.myVideos.52')}</Radio.Button>
                  </Radio.Group>
                  {/* <div className="toggle" onClick={() => {
                    setFree(!free)
                  }}>
                    {free && <div className="txtLeft">{translate('creator.playlist.15')}</div>}
                    <div className="round"></div>
                    {!free && <div className="txtRight">{translate('creator.playlist.8')}</div>}
                  </div> */}
                </div>

                {auth != 'free' && <div className="setPrice" style={{ width: px(mainStore.isEn ? 340 : 290) }}>
                  <div className="leftText" style={{ width: px(mainStore.isEn ? 180 : 130) }}>
                    {translate('creator.playlist.9')} ($)
                  </div>
                  <Form.Item
                    name="price"
                  >
                    <Input placeholder={collInfo.price} />
                  </Form.Item>
                </div>}

                {auth != 'free' && <div className="setPrice" style={{ width: px(mainStore.isEn ? 340 : 290) }}>

                  <div className="leftText" style={{ width: px(mainStore.isEn ? 180 : 130) }}>
                    <Tooltip title={translate(`creator.myVideos.56`)}>
                      <Button ><img src={imgTips} alt="" /> {translate('creator.playlist.10')}  </Button>
                    </Tooltip>
                  </div>
                  
                  <Form.Item
                    name="trycount"
                  >
                    <Input placeholder={collInfo.trycount} />
                  </Form.Item>
                </div>}
                {auth != 'free' && <div className="line"></div>}
                <Form.Item >
                  <Button className="submit" htmlType="submit">
                    {loadingSave && <Spin size="small" />}  <span>{translate('creator.playlist.12')}</span>
                  </Button>
                </Form.Item>
              </Form>


            </div>
            <div className="creator-video-list">
              {data.length>0&& <div className="tools">
                {isSelf && ((sortMode || delMode) ? sortMode ? (
                  <div className="sort-tools">
                    {IconInfo}
                    <span>{translate('creator.playlist.20')}</span>
                    <Button disabled={sorting} className="cancel-btn" onClick={() => setSortMode(false)}>{translate('creator.playlist.21')}</Button>
                    <Button loading={sorting} className="confirm-btn" onClick={doSort} >{translate('creator.playlist.22')}</Button>
                  </div>
                ) : (
                  <div className="del-tools">
                    <div className="top">
                      {IconInfo}
                      <span>{translate('creator.playlist.30')}</span>
                      <Button disabled={deling} className="cancel-btn" onClick={() => {
                        setDelMode(false);
                        setDelData([]);
                      }}>{translate('creator.playlist.21')}</Button>
                      <Button loading={deling} className="confirm-btn" onClick={doDel} >{translate('creator.playlist.22')}</Button>

                    </div>

                    <div className="all">
                      <Checkbox onChange={(e) => {
                        setDelData(e.target.checked ? data.map((d) => d.id) : []);
                      }}>{translate('creator.playlist.31')}</Checkbox>
                    </div>

                  </div>
                ) : (
                  <>
                    <div className="sort-btn" onClick={() => setSortMode(true)}>
                      {IconSort}
                      <span>{translate('creator.playlist.18')}</span>
                    </div>
                    <div className="del-btn" onClick={() => {
                      let buyFlag = false;
                      data.forEach((d) => {
                        if (d.isbuy == 1) buyFlag = true
                      })
                      if (buyFlag) {
                        showError(translate('creator.playlist.32'));
                      } else {
                        setDelMode(true)
                      }
                    }
                    }>
                      {IconDelete}
                      <span>{translate('creator.playlist.29')}</span>
                    </div>
                  </>
                ))}
              </div>}

              <VideoList list={sortMode ? sortData : data}
                sortMode={sortMode}
                delMode={delMode}
                price={collInfo.price}
                delData={delData}
                trycount={parseInt(collInfo.trycount)}
                payProps={{ price, ordertype: 4, objectid: id, onComplete: () => load({ id }) }}
                onClick={(it) => his.push(`/main/videoPlay/c${id}/${it.id}`)}
                onEdit={isSelf ? setCurEditItem : undefined}
                onDelete={isSelf ? (it) => deleteVideo({ id: it.id }).then(() => load({ id })) : undefined}
                onSort={sortMode ? setSortData : undefined}
                onMulDelChange={delMode ? (id: string) => {
                  let _delData = delData;
                  let idx = _delData.indexOf(id);
                  if (idx > -1) {
                    _delData.splice(idx, 1);
                  } else {
                    _delData.push(id);
                  }
                  console.log('_delData', _delData);
                  setDelData(_delData);
                } : undefined}
              />

              <VidoeEditModal
                visible={!!curEditItem}
                currentItem={curEditItem}
                onComplete={() => load({ id })}
                onClose={() => setCurEditItem(undefined)} />
            </div>

          </div>

      )}
      {visibleModal && <VideoFormModal collectid={id} onClose={() => setVisibleModel(false)} onPublish={() => load({ id })} />}
    </div>
  )
}
