import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { DatePicker } from 'antd';
import { IpoCalendarItem } from '../../../services/StockCalendar';
import './IpoCalendar.scss';
import zh_CN from "antd/lib/locale-provider/zh_CN";
import en_US from "antd/lib/locale-provider/en_US";
import { ConfigProvider } from 'antd';
import NoData from '../../../components/NoData';
import { Loading_icon } from '../../../images/Loading';
import { ipoCalendarStore } from '../../../stores/IpoCalendarStore';
import { mainStore } from '../../../stores/MainStore';
import Translate from '../../../components/Text';

const { RangePicker } = DatePicker;

export const IpoCalendarPage = observer(() => {
  useEffect(() => {
    ipoCalendarStore.changeType(1);
  }, []);
  const tebs = [{ text: "markets.date.a", id: 0 },
  { text: "markets.date.b", id: 1 },
  { text: "markets.date.c", id: 2 },
  { text: "markets.date.d", id: 3 },
  { text: "markets.date.e", id: 4 },]
  return (
    <div className="ipoCalendarWrapper">
        <div className="topNav">
          <div className="menu">
            {
              tebs.map((r) => {
                return <div className="option" onClick={() => {
                  ipoCalendarStore.changeType(r.id);
                }}>
                  <div className={ipoCalendarStore.type === r.id ? "checked" : "check"}>
                    <div className="round"></div>
                  </div>
                  <div className="text">
                    <Translate text={r.text} />
                  </div>
                </div>
              })
            }
          </div>
          <div className="datePicker">
            <ConfigProvider locale={mainStore.isEn? en_US:zh_CN}>
              <RangePicker onChange={(c) => {
                ipoCalendarStore.setRange(c);
              }} />
            </ConfigProvider>
          </div>
        </div>

        {ipoCalendarStore.loading ? <div className="loading" >
          <img src={Loading_icon} alt="" />
        </div> : ipoCalendarStore.list.length == 0 ? <div>
          <NoData />
        </div>
          :
          <div>
            <div className="titles">
              <div className="time"><Translate text={'markets.ipo_title.a'} /></div>
              <div className="company"><Translate text={'markets.ipo_title.b'} /></div>
              <div className="exchange">
              <Translate text={'markets.ipo_title.c'} />
              </div>
              <div className="price">
              <Translate text={'markets.ipo_title.d'} />
              </div>
              <div className="totalSharesValue">
              <Translate text={'markets.ipo_title.e'} />
              </div>
              <div className="status">
              <Translate text={'markets.ipo_title.f'} />
              </div>
            </div>
            <div className="calendarContent">
              {ipoCalendarStore.list?.map((item: IpoCalendarItem, i) => {
                return <div className="calendarItem" key={i} style={{background: i%2==0?"#fff":"#F3F5F9"}}>
                  <div className="time">{item.date.slice(2).split('-').join('.')}</div>
                  <div className="company">{item.name}</div>
                  <div className="exchange">
                    {item.exchange}
                  </div>
                  <div className="price">
                    {item.price}
                  </div>
                  <div className="totalSharesValue">
                    {item.totalSharesValue}
                  </div>
                  <div className="status">
                    {item.status}
                  </div>
                </div>
              })}
            </div>
          </div>}

    </div>
  )
});