const FormItem = Form.Item;
const Dragger = Upload.Dragger;

interface DraggerUploaderFile {
  rawInfo: any;
  key: string;
  formInfo?: any;
  status: 'uploading' | 'uploaded' | 'published';
}

interface DraggerUploaderProps {
  value?: DraggerUploaderFile[];
  onChange?: (files: DraggerUploaderFile[]) => void;
}

