const VideoUploader: FC<{ collections: CollectionItem[] }> = ({ collections }) => {
  const [files, setFiles] = useState([] as DraggerUploaderFile[]);
  const [visibleMap, setVisibleMap] = useState({} as Record<string, boolean>);
  const doChange = (index: number, val: DraggerUploaderFile) => {
    setFiles(files.map((it, i) => i === index ? val : it));
  };
  const doDelete = (index: number) => {
    setFiles(files.filter((_, i) => i !== index));
  };
  const setModalVisible = (key: string, visible: boolean) => {
    setVisibleMap({ ...visibleMap, [key]: visible });
  };

  return (
    <div className="panel">
      <h1>发布视频</h1>
      <DraggerUploader value={files} onChange={setFiles} />
      <div className="uploader-item">
        {files.map((it, i) => (
          <>
            <Uploader
              key={'uploader-' + it.key}
              file={it.rawInfo}
              onPublish={() => setModalVisible(it.key, true)}
              onComplete={(url, videoId) => doChange(i, { ...it, formInfo: { url, alivideoid: videoId }, status: 'uploaded' })}
              onDelete={() => doDelete(i)}
            />
            <VidoeEditModal
              mode="add"
              key={'modal-' + it.key}
              visible={visibleMap[it.key]}
              currentItem={it.formInfo}
              collections={collections}
              onClose={() => setModalVisible(it.key, false)}
              onComplete={() => doDelete(i)}
            />
          </>
        ))}
      </div>
    </div>
  );
};

const getCollListData = ({ id = '' }) => new CollectionListService({ psize: 999, userid: id }).send();

const createColltion = (data: any) => new CreateCollectService(data).send();

