export interface VideoCollectFormProps {
  onComplete: () => void;
  current?: CollectionItem;
}

export const VideoCollectForm: FC<VideoCollectFormProps> = ({ current, onComplete }) => {
  const { loadWithPromise: addColl, loading: collSubmiting } = useRequest(createColltion, { autoLoad: false });
  const [collForm] = useForm();

  const doSubmitColl = (data: any) => {
    addColl(current ? { ...data, id: current.id } : data).then(() => {
      collForm.resetFields();
      showSuccess(mainStore.isEn?'Success': (current ? '修改完成' : "创建完成"));
      onComplete()
    })
    .catch((e) => {
      // debugger;
      showError(e.message);
    })
  }

  useEffect(() => {
    if (current) {
      collForm.setFieldsValue({ ...current });
    }
  }, [current]);

  return (
    <Form form={collForm} onFinish={doSubmitColl}>
      <FormItem label="合集名称" name="name">
        <Input placeholder="请输入合集名称" />
      </FormItem>
      <FormItem label="合集价格" name="price">
        <Input placeholder="请输入合集价格" />
      </FormItem>
      <FormItem label="合集简介" name="describe">
        <Input placeholder="请输入合集简介" />
      </FormItem>
      <FormItem label="试看集数" name="trycount">
        <Input placeholder="请输入试看集数" />
      </FormItem>
      <FormItem label="集合封面" name="cover">
        <ImageUploader width={240} height={135} />
      </FormItem>
      <div className="footer">
        <Button htmlType="submit" className="submit-btn" loading={collSubmiting}>提交</Button>
      </div>
    </Form>
  );
}

const PublishVideoForm = observer(() => {
  const id = userStore.userInfo?.id;
  const { data: collections, load: reloadColl } = useRequest(getCollListData, { autoLoad: false });

  useEffect(() => {
    reloadColl({ id });
  }, [id]);

  return (
    <div className="publish-video">
      <VideoUploader collections={collections ?? []} />
      <div className="panel">
        <h1>创建合集</h1>
        <VideoCollectForm onComplete={() => reloadColl({ id })} />
      </div>
    </div>
  );
});

export const PublishVideoPage = observer(() => {
  return (
    <div className="publish-video-page-wrap">
      {userStore.userInfo?.identity == 'creator' ? <PublishVideoForm /> : <ApplyPage />}
    </div>
  );
})
