import { Ajax } from '../utils/http';

export class VodService {
    private name: string;
    constructor(_emal: string) {
        this.name = _emal;
    }

    public async send(): Promise<VodAuth> {
        let response: VodAuth;
        let data = {
            name: this.name,
        }
        try {
            response = await new Ajax<VodAuth>('/video/getVodUploadAuth', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


export interface VodAuth {
    uploadAuth: string;
    uploadAddress: string;
    videoId: string;
  }