import { Button, Form, Input, message, Modal, Radio, Select } from "antd";
import { useEffect, useState } from "react";
import { showSuccess } from "../../components/Message";
import { CollectionItem, CollectionListService } from "../../services/CollectionListService";
import { PostService } from "../../services/PublishService";
import { mainStore } from "../../stores/MainStore";
import { userStore } from "../../stores/UserStore";
import { useRequest, useTranslator } from "../../utils/hooks";
import { px } from "../../utils/rem";
import './MyCommunity.scss';
import { VideoImageSelect } from "./VideoImageSelect";

const getCollListData = ({ id = '', sortType = 'hot', sortAsc = 'desc', searchkey = '' }) => {
    return new CollectionListService({ psize: 999, userid: userStore.userInfo?.id, sort: sortType, order: sortAsc, searchkey }).send().then((list) => ({ list, sortType, sortAsc, searchkey }));
};

function submitForm(val: any) {
    return new PostService(val)
        .send()
        .then(() => {
            showSuccess(mainStore.isEn ? 'Post succeed!' : '发布完成');
        })
        .catch((err) => {
            message.error(err);
            return Promise.reject(err);
        });
}

export interface CommunityPostModalProps {
    visible: boolean
    onComplete: Function
    onClose: () => void;
}


export function CommunityPostModal({ onComplete, onClose, visible }: CommunityPostModalProps) {
    const { translate } = useTranslator();
    const [visibletype, setVisibletype] = useState(1);
    const [collectid, setCollectid] = useState('');
    const [images, setImages] = useState('');
    const [content, setContent] = useState('');
    const { loading, loadWithPromise: submit } = useRequest(submitForm, { autoLoad: false });
    const { loading: loadingList, data: collectList, load } = useRequest(getCollListData, { autoLoad: true });
    const doSubmit = () => {
        if (!content) {
            return;
        }
        let data: any = {
            content,
            images,
            collectid,
            visibletype
        }
      
        submit(data).then(() => {
            setImages('');
            setContent('');
            setCollectid('');
            setVisibletype(1);
            onComplete && onComplete();
    Modal.destroyAll()
    onClose();
        });
    };
    return (
        <Modal destroyOnClose={true} width={px(1060)} title={false}  visible={visible} className="community-post-modal" onCancel={onClose} footer={false}>
            <Form labelCol={{ span: 4 }}
                onFinish={doSubmit}>
                <div className="title">{translate('community.1')}</div>
                <Form.Item
                    name="content"
                >
                    <Input.TextArea onChange={(v) => {
                        setContent(v.target.value)
                    }} value={content} className="content-textarea" id="commentInput" placeholder={translate('community.2')} />
                </Form.Item>
                <VideoImageSelect maxCount={9}
                    tips={translate("community.12")}
                    onImgUploaded={(images: any) => {
                        setImages(images.join(","))
                    }} onImageSelectd={(v: any) => {
                    }} />
                <div className="options">
                    <Radio.Group onChange={(v) => {
                        setVisibletype(v.target.value)
                    }} value={visibletype}>
                        <Radio value={1}>{translate('community.4')}</Radio>
                        <Radio value={2}>{translate('community.5')}</Radio>
                        {/* <Radio value={3}>{translate('community.6')}</Radio> */}
                        {loadingList ? <></> : <Radio value={4}>{translate('community.7')}

                        </Radio>}
                    </Radio.Group>
                    {visibletype == 4 && <div className="collectList"><Select mode="multiple" placeholder={translate('community.10')} showArrow={true} onChange={(v) => {
                        setCollectid(v.join(","))
                    }}>
                        {(collectList?.list ?? []).filter((c) => parseFloat(c.price || '0') > 0).map((l: CollectionItem) => <Select.Option key={l.id} value={l.id}>{l.name}</Select.Option>)}
                    </Select></div>}
                </div>
                <div className="btns">
                    <Button size="large" onClick={() => {
                        onClose()
                    }}>{translate('community.8')}</Button>
                    <Button htmlType="submit" className={`submit-btn ${content ? '' : 'disable'}`} size="large" loading={loading}>{translate('community.9')}</Button>
                </div>
            </Form>
        </Modal>
    );
}


export interface CommunityImgModalProps {
    visible: boolean;
    onClose: () => void;
    images: Array<string>;
    idx: number
}

export function CommunityImgModal({ onClose, visible, images, idx }: CommunityImgModalProps) {
    const { translate } = useTranslator();
    const [selectIdx, setIdx] = useState(idx);
    useEffect(() => {
        setIdx(idx)
    }, [idx])
    return (
        <Modal width={px(880)} title={false} visible={visible} className="community-img-modal" onCancel={onClose} footer={false}>
            <div className="image-gallery">
                <div className="images">
                    <img className="img-display" src={images[selectIdx]} alt="" />
                    <div className="list">
                        {images.map((img, i) => <div key={i} className={`item ${selectIdx == i ? 'active' : ''}`} onClick={() => {
                            setIdx(i);
                        }}>
                            <img src={img} alt="" />
                        </div>)}
                    </div>
                </div>
            </div>
        </Modal>
    );
}
