import { Modal } from 'antd';
import { observer } from 'mobx-react';
import Translate, { translate } from '../../components/Text';
import { Result, Button } from 'antd';


interface PaySuccessModalProps {
    visible: boolean;
    cancle: Function;
}

export const PaySuccessModal = observer((props: PaySuccessModalProps) => {


    return (
        <Modal title='' visible={props.visible}
            footer={null}
            centered={true}
            className='editUserModal buyGoldModal'
            onCancel={() => { props.cancle() }}
        >
            <Result
                status="success"
                title={translate('gold.form.f')}
                subTitle={translate('gold.form.g')}
                // extra={[
                //     <Button key="buy">{translate('gold.form.h')}</Button>,
                // ]}
            />
        </Modal>
    );
})













