import { useEffect, useState } from 'react';
import { Ajax } from '../utils/http';

export interface StockLogoProps {
    symbol?: string
}
let getLogo = async (symbol?: string, callback?: Function) => {
    let response;
    try {
        response = await new Ajax(`https://aolinews.oss-accelerate.aliyuncs.com/logos/${symbol}.png`, {}, false, true).get();
    } catch (e) {
        callback && callback(false);
        return;
    }
    callback && callback(true);
}

export const StockLogo = (props: StockLogoProps) => {
    const [showRemote, setRemote] = useState(true);
    useEffect(() => {
        getLogo(props.symbol, (result: any) => {
            setRemote(result);
        })
    }, [props.symbol]);

    return (
        <div >
            {!showRemote ? <div className="icon">{(props.symbol || '')[0]}</div> :
                <img className="icon" style={{ backgroundColor: "transparent" }} src={`https://aolinews.oss-accelerate.aliyuncs.com/logos/${props.symbol}.png`} alt="" />}
        </div>
    )
}

