import { makeAutoObservable, } from "mobx";
import { showError, } from "../components/Message";
import { ResearchDetailData, ResearchDetailService, ResearchFollowService, ResearchLikeService } from "../services/ResearchDetailService";

class ResearchDetailStore {
  public detail?: ResearchDetailData;
  public loading: boolean = false;
  public liking: boolean = false;
  public pdfHeight: number = 1000;
  public scale: number = 1.8;
  public scaleText: string = '180%';
  public page: number = 1;
  public totlaPage: number = 0;
  private pdfDocument: any;
  constructor() {
    makeAutoObservable(this);
  }

  public async getData(id: string) {
    let response: ResearchDetailData;

    this.loading = true;
    try {
      response = await new ResearchDetailService(id).send();
    } catch (e: any) {
      this.loading = false;
      showError(e);
      return;
    }
    this.detail = response;
    this.initPdf();
  }

  public initPdf() {

    let pdfjsLib = (window as any).pdfjsLib;
    let pdfjsViewer = (window as any).pdfjsViewer;
    pdfjsLib.GlobalWorkerOptions.workerSrc =
      "https://aoliapp.oss-cn-shenzhen.aliyuncs.com/pdf/pdf.worker.js";
    const CMAP_URL = "https://aoliapp.oss-cn-shenzhen.aliyuncs.com/pdf/cmaps/";
    const container = document.getElementById("viewerContainer");
    const eventBus = new pdfjsViewer.EventBus();
    const pdfLinkService = new pdfjsViewer.PDFLinkService({
      eventBus,
    });
    // const pdfViewer = new pdfjsViewer.PDFViewer({
    //   container,
    //   eventBus,
    //   linkService: pdfLinkService,
    //   renderer: "svg",
    //   textLayerMode: 0,
    // });
    // pdfLinkService.setViewer(pdfViewer);

    eventBus.on("pagesinit", () => {
      // pdfViewer.currentScaleValue = "page-width";
      let viewer = document.getElementById("viewer");
      this.pdfHeight = viewer?.clientHeight || 0;
    });

    const loadingTask = pdfjsLib.getDocument({
      url: this.detail?.pdfurl.replace("http://", "https://"),
      cMapUrl: CMAP_URL,
      cMapPacked: true,
    });
    loadingTask.promise.then((pdfDocument: any) => {
      // pdfViewer.setDocument(pdfDocument);

      // pdfLinkService.setDocument(pdfDocument, null);
      let viewer = document.getElementById("viewer");
      this.pdfHeight = viewer?.clientHeight || 0;
      window.test = pdfDocument;
      this.pdfDocument = pdfDocument;
      this.totlaPage = pdfDocument.numPages;
      this.loading = false;
      this.showPdf(1);
    });
  }

  public scalePdf(up: boolean) {
    if (up) {
      if (this.scale>=2.5) {
        return
      }
      this.scale += 0.1;
    } else {
      if (this.scale<=0.6) {
        return
      }
      this.scale -= 0.1;
    }
    this.scaleText = (this.scale * 100).toFixed(0) + '%';
    this.showPdf();
  }
  public setScale(value: string) {
    this.scaleText = value;
    value = value.replace('%', '');
    this.scale = parseInt(value) / 100
    console.log('this.scale', this.scale)
    this.showPdf();
  }

  public showPdf(_page?: number) {
    this.page = _page || this.page;
    this.pdfDocument.getPage(this.page).then((page: any) => {
      var scale = this.scale;
      var viewport = page.getViewport({ scale: scale, });
      // Support HiDPI-screens.
      var outputScale = window.devicePixelRatio || 1;

      var canvas: any = document.getElementById('viewer');
      var context = canvas.getContext('2d');

      canvas.width = Math.floor(viewport.width * outputScale);
      canvas.height = Math.floor(viewport.height * outputScale);
      canvas.style.width = Math.floor(viewport.width) + "px";
      canvas.style.height = Math.floor(viewport.height) + "px";

      var transform = outputScale !== 1
        ? [outputScale, 0, 0, outputScale, 0, 0]
        : null;

      var renderContext = {
        canvasContext: context,
        transform: transform,
        viewport: viewport
      };
      page.render(renderContext);
    })
  }



  public async like() {
    if (this.liking) {
      return;
    }
    this.liking = true;
    if (this.detail) {
      this.detail.islike = this.detail.islike == 1 ? 0 : 1;
      let count = parseInt(this.detail.likecount);
      if (this.detail.islike == 1) {
        count += 1;
      } else {
        count -= 1;
      }
      this.detail.likecount = count.toString();
    }
    try {
      await new ResearchLikeService(this.detail?.id, this.detail?.islike).send();
    } catch (e: any) {
      if (this.detail) {
        this.detail.islike = this.detail.islike == 1 ? 0 : 1;
        let count = parseInt(this.detail.likecount);
        if (this.detail.islike == 1) {
          count += 1;
        } else {
          count += 1;
        }
        this.detail.likecount = count.toString();
      }
      showError(e);
      this.liking = false;
      return;
    }
    this.liking = false;
  }

  public async follow() {
    if (this.detail) {
      this.detail.isfollow = this.detail.isfollow == 1 ? 0 : 1;
    }
    try {
      await new ResearchFollowService(this.detail?.id, this.detail?.isfollow).send();
    } catch (e: any) {
      if (this.detail) {
        this.detail.isfollow = this.detail.isfollow == 1 ? 0 : 1;
      }
      showError(e);
      return;
    }
  }




}

export { ResearchDetailStore };