import { Ajax } from '../utils/http';

export class StockOwnerShipService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<OwnershipData> {
        var response: OwnershipData;
        var data = {
            symbol: this.symbol,
        }
        try {
            response = await new Ajax<OwnershipData>( '/oliview_market_ownerships', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


export class StockTraderService {
  private name: string;
  constructor(_name: string) {
      this.name = _name;
  }

  public async send(): Promise<Array<OwnerShipTrade>> {
      var response: Array<OwnerShipTrade>;
      var data = {
          name: this.name,
      }
      response = await new Ajax<Array<OwnerShipTrade>>('/inside_trader', data, true).get();
      console.log('response ',response)
      return response;
  }
}


