
import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import './BuyGold.scss';
import { Loading_icon } from '../../images/Loading';
import Translate from '../../components/Text';
import { GoldInfo, goldStore } from '../../stores/GoldStore';
import { PayGoldModal } from './PayGoldModal';
import { StripeModal } from '../Stripe/Stripe';
import { TabsConfig } from '../../config/TabsConfig';
import { PaySuccessModal } from './SuccessModal';
import { LocalStorageService } from '../../utils/storage';
import GlobalConfig from '../../config/GlobalConfig';
import NoData from '../../components/NoData';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { mainStore } from '../../stores/MainStore';
import eventBus from '../../utils/eventBus';
import { userStore } from '../../stores/UserStore';

export const BuyGoldPage = observer((props: any) => {
  useEffect(() => {
    goldStore.getData();
    let translateCb = () => goldStore.setPriceChart();
    eventBus.addListener('translate', translateCb)
    return () => {
      goldStore.unmounted = true;
      eventBus.removeListener('translate', translateCb);
    }
  }, [])
  return (
    <div className="BuyGoldWrapper">
      <div className="indexTabs" style={{ width: mainStore.tabWidth }}>
        <div className="tabContent">
          {
            TabsConfig.goldTabs.map((t, i) => {
              return <div key={i} className={goldStore.type == t.type ? 'tabActive' : 'tab'} onClick={() => {
                goldStore.changeType(t.type, t.symbol);
              }}>
                <Translate text={t.name} />
              </div>
            })
          }
        </div>
        <div className="buy" onClick={() => {
          let isLogin = LocalStorageService.get("_userInfo");
          if (!isLogin) {
            loginStore.LoginModel = LoginModel.login
            userStore.loginModalVisible = true;
            return;
          }
          goldStore.payModalVisible = true;
        }}>
          <Translate text='gold.a' />
        </div>
      </div>

      <div className="BuyGoldPage">
        {goldStore.loading ? <div className="loading" >
          <img src={Loading_icon} alt="" />
        </div> :
          <>
            <div className="goldInfo">
              <div className="title">
                <Translate text={GoldInfo[goldStore.type].title} />
              </div>
              <div className="info">
                <Translate text={GoldInfo[goldStore.type].info} />
              </div>
            </div>
            <div className="tradingview-widget-container">
              <div id="tradingview_meta"></div>
            </div>
            <div className="goldContent">
              {goldStore.items.length ? goldStore.items.map((item, i) =>
                <div key={i} className="goldItem" >
                  <div className="cover">
                    <img src={item.image} alt="" />
                  </div>
                  <div className="title">{item.title}</div>
                  <div className="price">${item.price}</div>
                </div>) : <NoData />}
            </div>
          </>}
      </div>
      {goldStore.payModalVisible && <PayGoldModal visible={goldStore.payModalVisible} cancle={() => { goldStore.payModalVisible = false }} />}
      {goldStore.showStripe && <StripeModal visible={goldStore.showStripe} success={() => {
        goldStore.showStripe = false;
        goldStore.payModalVisible = false;
        goldStore.paySuccess = true;
      }} cancle={() => { goldStore.showStripe = false }} getSercet={goldStore.purchase.bind(goldStore)} />}
      <PaySuccessModal visible={goldStore.paySuccess} cancle={() => { goldStore.paySuccess = false }} />
    </div>

  );
}
)