import { makeAutoObservable, } from "mobx";
import { BannerItem, IndexBannerService } from "../services/IndexBannerService";
import { LabelItem, VideoLabelService } from "../services/VideoLabelSerice";
import { LocalStorageService } from "../utils/storage";

class MainStore {
  public isEn: boolean = false;
  public menuCollapsed: boolean = false;
  public menuKey: number = 0;
  public tabWidth?: number;
  public sideWidth?: number;
  public labels: Array<LabelItem> = [];
  public banners: Array<BannerItem> = [];
  private _pathRecordFlag= false;
  constructor() {
    makeAutoObservable(this);
  }

  async getLabels() {
    let _labels : Array<LabelItem>=[];
    _labels = await new VideoLabelService().send();
    _labels = _labels.filter((l) => ['傲利','基金'].indexOf(l.name) == -1);
    this.labels= _labels;
  }


  async getBanners() {
    this.banners = await new IndexBannerService().send()
  }

}

const mainStore = new MainStore();

export { mainStore };