
import { Ajax } from '../utils/http';

export class StockDescriptionService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<StockDescriptionData> {
        var response: StockDescriptionData;
        var data = {
            symbol: this.symbol,
        }
        try {
            response = await new Ajax<StockDescriptionData>('/oliview_market_description', data, true).get();
        } catch (e) {
            throw e;
        }
      
        return response;
    }
}

/*Overview*/
export interface Overview {
    'Company Name': string;
    'IPO Date': string;
    Exchange: string;
    Location: string;
    Sector: string;
    Industry: string;
    Description: string;
  }
  
  /*Stat*/
  export interface Stat {
    '52 Week High': number[];
    '52 Week Low': number[];
    beta: number;
    'Market Capitalization': number;
    'Dividend Yield': number;
    PE: number;
    EPS: number;
  }
  
  /*Dividend*/
  export interface Dividend {
    recordDate: string;
    adjustedAmount: number;
    amount: number;
  }
  
  /*tsModel3*/
  export interface StockDescriptionData {
    overview: Overview;
    stat: Stat;
    dividend: Dividend[];
  }
  
  