import { Ajax } from '../utils/http';


export class VideoService {
    constructor(
        private p: number,
        private psize: number,
        private extraParams: Record<string, any> = {}
    ) {

    }

    public async send(): Promise<VideoData[]> {
        let response: VideoResponse;
        let data = {
            ...this.extraParams,
            p: this.p,
            psize: this.psize,
        }
        try {
            response = await new Ajax<VideoResponse>('/index/newvideoList', data).post();
        } catch (e) {
            throw e;
        }
        return response.list;
    }
}


export class VideoDetailService {
    private id: any;
    constructor(_id: any) {

        this.id = _id;
    }

    public async send(): Promise<VideoDetail> {
        let response: VideoDetail;
        let data = {
            id: this.id,
        }
        try {
            response = await new Ajax<VideoDetail>('/article/videoInfo', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export class VideoLikeService {
    private id?: string;
    private status?: number;
    constructor(_id?: string, _status?: number) {
        this.id = _id;
        this.status = _status;
    }

    public async send() {
        let data = {
            objectid: this.id,
            status: this.status,
            objecttype: 5
        }
        try {
            await new Ajax('/article/articleLike', data).post();
        } catch (e) {
            throw e;
        }
    }
}

