
import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { GridView } from '../../components/GridView';
import { Loading } from '../../components/Loading';
import NoData from '../../components/NoData';
import { translate } from '../../components/Text';
import VideoAuthLabel from '../../components/VideoAuthLabel';
import GlobalConfig from '../../config/GlobalConfig';
import { CollectionItem, CollectionListService } from '../../services/CollectionListService';
import { CreateDelCollectService } from '../../services/PublishService';
import { mainStore } from '../../stores/MainStore';
import eventBus from '../../utils/eventBus';
import { useCurrentUserCheck, useRequest } from '../../utils/hooks';
import { getAgo, videoLink, ViewBlock } from '../CreatorCenter/CreatorCenter';
import { VideoCollectionList } from '../CreatorCenter/VideoCollectionList';
import { VideoCollectForm } from '../Publish/PublishVideo';
import { CreatorVideoLoader } from '../Skelleton/creator';
import { IconPlayList } from '../UserCenter/Icons';
import './UserCollectionList.scss';

const getCollListData = ({ id = '', searchkey = '' }) => new CollectionListService({ psize: 999, userid: id, searchkey: searchkey }).send();

export const UserCollectionList = observer(() => {
  const { id = '' } = useParams<any>();
  const { data = [], loading, load } = useRequest(getCollListData, { autoLoad: false });
  const isSelf = useCurrentUserCheck(id);
  const reload = () => {
    load({ id })
    eventBus.addListener('userSearch', (key: any) => {
      load({ id, searchkey: key })
    });
  };

  useEffect(reload, []);

  return (
    <div className="label-video-list user-video-list user-collect-list">
      {loading ? <GridView id="creatorVideoListLoader" space={10} itemCount={20} itemBuilder={(idx) => <CreatorVideoLoader />} /> : data.length === 0 ? <NoData text={translate('common.c')} /> :
        <GridView id="videoList" space={10} itemCount={data.length} itemBuilder={(idx) => {
          let it: any = data[idx];
          return (
            <div className='video-item' key={it.id} onClick={() => videoLink(it, true)}>
              <div className="thumb">
                <img src={it.cover} />
                <div className="thumb-info">
                  <span>{it.videocount} {mainStore.isEn ? 'Videos' : '集'}</span>
                  {IconPlayList}
                </div>
                <VideoAuthLabel item={it} isLeft={true} />
              </div>
              <div className="info">
                <div>
                  <div className="title">{it.name}</div>
                </div>
              </div>
            </div>
          );
        }} />
      }
    </div>
  );
});