import { Ajax } from '../utils/http';

export class CollectionListService {
    private data?: any;
    constructor(_data?:any) {
        this.data = _data;
    }
    public async send(): Promise<CollectionItem[]> {
        let response: CollectionItem[];
        try {
            response = await new Ajax<CollectionItem[]>('/creator/videoCollectList',this.data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export class CollectionDataService {
    constructor(private id: string) {}
    public send(): Promise<CollectionItem | undefined> {
        // return new CollectionListService({ p: 1, psize: 999 }).send().then(res => res.find(it => it.id === this.id))
        return new Ajax<CollectionItem>('/creator/videoCollectList', { id: this.id }).post();
    }
}


/*Data*/
export interface CollectionItem {
    id: string;
    userid: string;
    name: string;
    cover: string;
    avatar: string;
    describe: string;
    trycount: string;
    ishot: string;
    price: string;
    createtime: string;
    videocount: number;
    isfree:string;
    lookauth:number;
  }
  
  