import { FC, useEffect, useState } from "react";
import { RouteComponentProps } from "react-router";
import { useRequest, useTranslator } from "../../utils/hooks";
import { Button, Modal } from "./Common";
import { IconAdd, IconCardChecked, IconDelete } from "./Icons";
import IconCardDef from '../../images/creator/card_default.png';
import './Withdraw.scss';
import { px } from "../../utils/rem";
import { Input, Popconfirm, Form } from "antd";
import { Elements, useElements, useStripe } from "@stripe/react-stripe-js";
import { mainStore } from "../../stores/MainStore";
import { loadStripe } from "@stripe/stripe-js";
import GlobalConfig from "../../config/GlobalConfig";
import { Ajax } from "../../utils/http";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import { showError, showSuccess } from "../../components/Message";
import { CreatorUserListService } from "../../services/CreatorService";
import { userStore } from "../../stores/UserStore";
import NumericInput from "../../components/NumericInput";
import eye_icon from '../../images/main/eye.png';
import eye_no_icon from '../../images/main/eye_no.png';
import balance_bg from '../../images/main/balanceBg.png';
import { Img } from "../../components/Image";

const WITHDRAW_MIN_AMOUNT = 100;

export async function getAllPayments() {
  return {
    list: await new Ajax<Array<any>>('/creator/creatorBankCardList').post(),
  };
}
export async function getCreator() {
  let userInfo: any = localStorage.getItem("_userInfo");
  if (userInfo) {
    userInfo = JSON.parse(userInfo);
  }
  return await new CreatorUserListService({ userid: userInfo?.id }).send()
}

async function getOrders() {
    return  new Ajax('/creator/withdrawApplyList', { p: 1, psize: 3 }).post();
   }


function delPayments(id: string) {
  return new Ajax('/creator/delCreatorBankCard', { id }).post();
}

function saveDefault(data: any) {
  return new Ajax('/creator/withdrawApply', data).post();
}

export const WithDrawPage: FC<RouteComponentProps<{}>> = ({ }) => {
  const { translate } = useTranslator();
  const [visible, setVisible] = useState(false);
  const [showAmount, setShowAmount] = useState(true);
  const { data = {}, loading, load } = useRequest(getAllPayments);
  const { data: creator, loading: loadingCreator, load: loadCreator } = useRequest<any>(getCreator);
  const { data: orders, loading: loadingOrders, load: loadOrder } = useRequest<any>(getOrders);

  const { list = [] } = data as any;

  const { loading: submitting, loadWithPromise: submit } = useRequest(saveDefault, { autoLoad: false });
  return (
    <div>
      <div className="withdraw-detail">
        {(loading || loadingCreator) ? <Loading size="mini" /> : <>
          <div className="withdraw-detail-block">
            <Form
              name="basic"
              initialValues={{ remember: true }}
              autoComplete="off"
              onFinish={(v) => {
                if (!v.money) {
                  showError(mainStore.isEn ? "Please enter the amount!" : "请输入提现金额!");
                  return;
                }
                if (parseFloat(v.money) > parseFloat(creator?.commission ?? '0')) {
                  showError(mainStore.isEn ? "The amount is beyond the Limit!" : "提现金额不可超出当前余额!");
                  return;
                } else if (parseFloat(v.money) < WITHDRAW_MIN_AMOUNT) {
                  showError(mainStore.isEn ? "The amount is below the Limit!" : "提现金额低于最小提现金额!")
                  return;
                }
                submit({ bankcardid: list[0].id, money: v.money })
                  .then(() => {
                    showSuccess(mainStore.isEn ? 'Your withdraw request has been submitted.' : '提现申请已完成');
                    load({});
                    loadCreator({});
                  })
                  .catch(e => showError(e.message));
              }}
            >
              <div className="card-top">
                <h1 className="withdraw-detail-title">{translate("withdraw.2")}</h1>
                <a onClick={() => setVisible(true)}>
                  {IconAdd}
                  {translate('withdraw.5')}
                </a>
              </div>
              <div className="card-content">
                {list.length <= 0 ? <NoData /> : list.map((it: any) => (
                  <div className="card-item" key={it.id}>
                    <a className="check-btn">{IconCardChecked}</a>
                    <Img className="card-icon" src={IconCardDef} />
                    <div className="info">
                      <div className="name"> {it.bankname}</div>
                      <div className="num"> **** {it.cardnumber.slice(it.cardnumber.length - 4, it.cardnumber.length)}</div>
                      <div className="name"> {it.bsb}</div>
                    </div>
                    <Popconfirm title={translate('payment.13')} onConfirm={() => delPayments(it.id).then(() => load({}))}>
                      <a className="check-btn">{IconDelete}</a>
                    </Popconfirm>
                  </div>
                ))}
                {list.length > 0 && <div className="amountInput">
                  <div className="editTitle">
                    <div className="text">{translate('withdraw.6')}:</div>
                  </div>
                  <div className="editInput">
                    <Form.Item
                      name="money"
                    >
                      <Input placeholder={translate("withdraw.21") + WITHDRAW_MIN_AMOUNT} />
                    </Form.Item>
                  </div>
                </div>}

                {list.length > 0 && <div className="oCenter">
                  <Button
                    loading={submitting}
                    size="middle"
                    className="save-btn"
                    htmlType="submit"
                  >
                    {translate('withdraw.7')}
                  </Button>
                </div>}
              </div>

            </Form>
          </div>
          <div className="right-content">
            <h1 onClick={() => {
              setShowAmount(!showAmount);
            }} className="withdraw-detail-title">{translate("withdraw.22")}  <Img src={showAmount ? eye_icon : eye_no_icon} alt="" /></h1>
            <div className="amount" style={{ backgroundImage: `url(${balance_bg})` }}>$ {showAmount ? creator?.commission ?? '0' : '* * * * * * '}</div>
          <div className="records">
            <div className="top">
              <div className="title">{translate('withdraw.4')}</div>
              <a href="#/main/myCenter/payorders/7" className="more">{translate('withdraw.23')}</a>
            </div>
            <div className="list">
              {(orders||[]).map((o:any,i:number)=>
              <div className="item" key={i}>
                  <div className="money">$ {showAmount?o.money:"* * * *"}</div>
                 <div className="date">{o.createtime.slice(0,16)}</div> 
              </div>
              )}
            </div>
          </div>
          </div>
        </>}
      </div>

      {visible && (
        <Modal
          title={translate("withdraw.5")}
          wrapClassName="stripe-modal"
          width={px(510)}
          footer={null}
          visible={true}
          centered={true}
          onCancel={() => setVisible(false)}
        >
          <AddCardForm onComplete={() => { load({}); setVisible(false); }} />
        </Modal>
      )}
    </div>
  );
}




export const AddCardForm: FC<{ onComplete: () => void }> = ({ onComplete }) => {
  const { translate } = useTranslator();
  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);

  const submitAddCard = async (data: any) => {
    setLoading(true);
    try {
      await new Ajax<any>('/creator/saveCreatorBankCard', data).post();
      onComplete();
    } catch (e: any) {
      console.log("errrorr $e", e)
      showError(e.message || e);
    } finally {
      setLoading(false);
    }
  }

  return (
    <Form
      name="basic"
      className="stripe-form account-form"
      initialValues={{ remember: true }}
      autoComplete="off"
      onFinish={(v) => {
        console.log("ccc", v)
        submitAddCard(v);
      }}
    >
      <div className="line">
        <div className="col">
          <label>{translate("withdraw.10")}</label>
          <Form.Item
            name="firstname"
            rules={[{ required: true, message: translate('withdraw.16') }]}
          >
            <Input className="name-input" value={name} onChange={(evt) => setName(evt.target.value)} />
          </Form.Item>
        </div>
        <div className="col">
          <label>{translate("withdraw.12")}</label>
          <Form.Item
            name="lastname"
            rules={[{ required: true, message: translate('withdraw.17') }]}
          >
            <Input className="name-input" value={name} onChange={(evt) => setName(evt.target.value)} />
          </Form.Item>
        </div>
      </div>
      <label>{translate('withdraw.13')}</label>
      <Form.Item
        name="bankname"
        rules={[{ required: true, message: translate('withdraw.18') }]}
      >
        <Input className="name-input" value={name} onChange={(evt) => setName(evt.target.value)} />
      </Form.Item>
      <label>{translate("withdraw.14")}</label>
      <Form.Item
        name="cardnumber"
        rules={[{ required: true, message: translate('withdraw.19') }]}
      >
        <NumericInput className="name-input" />
      </Form.Item>
      <label>{translate("withdraw.15")}</label>
      <Form.Item
        name="bsb"
        rules={[{ required: true, message: translate('withdraw.20') }]}
      >
        <NumericInput className="name-input" />
      </Form.Item >

      <div className="oCenter">
        <Button htmlType="submit" className="stripeSubmit" loading={loading}>
          {translate("payment.14")}
        </Button>
      </div>
      <div className="extra-info">
        {translate("payment.7")} <a target="_blank" href="https://aolinews.oss-accelerate.aliyuncs.com/pdf/subscription.pdf">{translate("payment.8")}</a>
      </div>
    </Form >
  );
}