import Index from "../pages/Index";
import { NewsPage } from "../pages/News/News";
import { PublishNewsPage } from "../pages/Publish/PublishNews";
import { PublishVideoPage } from "../pages/Publish/PublishVideo";
import { ResearchPage } from "../pages/Research/Research";
import { ResearchDetail } from "../pages/Research/ResearchDetail";
import { Subscribe } from "../pages/Subscribe/Subscribe";
import { ECalendarPage } from "../pages/Market/ECalendar/ECalendar";
import { MarketIndexPage } from "../pages/Market/MarketIndex/MarketIndex";
import { PatternsPage } from "../pages/Market/Patterns/Patterns";
import { IpoCalendarPage } from "../pages/Market/IpoCalendar/IpoCalendar";
import { WatchListPage } from "../pages/Market/WatchList/WatchList";
import { StatisticPage } from "../pages/Market/Stock/Statistic/Statistic";
import { NewsDetailPage } from "../pages/News/NewsDetail";
import { RecommendPage } from "../pages/Market/Stock/Statistic/Recommend";
import { VideosPlayPage } from "../pages/Videos/VideoPlayList";
import { PatternDetail } from "../pages/Market/Patterns/PatternsDetail";
import { CompanyPage } from "../pages/Market/Stock/Statistic/CompanyInfo";
import { MarketPage } from "../pages/Market/MarketIndex/MarketPage";
import { NewsIndexPage } from "../pages/News/NewsIndex";
import { StockPage } from "../pages/Market/Stock/StockPage";
import { StockIndexPage } from "../pages/Market/Stock/StockIndex";
import { FullScreenCharts } from "../pages/Market/Stock/Charts/FullScreenChart";
import { BuyGoldPage } from "../pages/BuyGold/BuyGold";
// import { IndexPage } from "../pages/Index/Index";
import { AnalystPage } from "../pages/Market/Stock/Statistic/AnalystDetail";
import { OwnerShipPage } from "../pages/Market/Stock/Statistic/OwnerShipDetail";
import { SalesPage } from "../pages/Market/Stock/Statistic/Sales";
import { OpportunitiesPage } from "../pages/Market/Opportunities/OpportunitiesPage";
import { CreatorCenterMyChannelVideos } from "../pages/UserCenter/MyChannelVideos";
import { CreatorCenterPlayList } from "../pages/UserCenter/PlayList";
import { AllCollectionsArea, CreatorCenterPage, CreatorRecommendPage, CreatorVideoSearchPage, LabelVideoList } from "../pages/CreatorCenter/CreatorCenter";
import { CreatorCollectionDetailVideoList } from "../pages/CreatorCenter/CreatorVideoList";
import { UserCenter } from "../pages/CreatorUser/UserCenter";
import { UserProfile } from "../pages/CreatorUser/UserProfile";
import { UserVideoList } from "../pages/CreatorUser/UserVideoList";
import { UserCollectionList } from "../pages/CreatorUser/UserCollectionList";
import { UserSubList } from "../pages/CreatorUser/UserSubList";
import { Followers, Following } from "../pages/UserCenter/Followers";
import { MyCenterPage } from "../pages/UserCenter/MyCenter";
import { Payment } from "../pages/UserCenter/Payment";
import { PayOrders } from "../pages/UserCenter/PayOrders";
import { WatchLog } from "../pages/CreatorCenter/WatchLog";
import { WithDrawPage } from "../pages/UserCenter/WithDraw";
import { UserIndexPage } from "../pages/UserCenter/UserIndex";
import { NotificationPage } from "../pages/UserCenter/Notification";
import { CommunityDetailPage, CommunityPage, MyCommunityPage } from "../pages/UserCenter/MyCommunity";

export const routesConfig = [
    // {
    //     path: "/",
    //     exact: true,
    //     component: IndexPage,
    // },
    {
        path: "/subscribe",
        hideRecord: true,
        component: Subscribe,
    },
    {
        path: "/stockChart",
        component: FullScreenCharts,
    },
    {
        path: "/main",
        component: Index,
        routes: [
            {
                path: "/main/index",
                component: CreatorCenterPage,
                routes: [
                    {
                        path: "/main/index/recommend",
                        component: CreatorRecommendPage
                    },
                    {
                        path: "/main/index/collection",
                        component: AllCollectionsArea
                    }, {
                        path: "/main/index/views/:id",
                        component: LabelVideoList
                    },
                ]
            },
            {
                path: "/main/news",
                component: NewsIndexPage,
                routes: [
                    {
                        path: "/main/news/index",
                        component: NewsPage
                    },
                    {
                        path: "/main/news/research/:type",
                        component: ResearchPage,
                        rule: 'researchReport'
                    },

                ]
            },


            {
                path: "/main/newsDetail",
                component: NewsDetailPage
            },
            {
                path: "/main/researchDetail/:type/:id",
                component: ResearchDetail,
            },
            // {
            //     path: "/main/gold",
            //     component: MarketIndexPage,
            //     routes: [
            //         {
            //             path: "/main/gold/index",
            //             component: BuyGoldPage
            //         },]
            // },
            {
                path: "/main/gold",
                component: BuyGoldPage,
            },
            {
                path: "/main/market",
                component: MarketIndexPage,
                routes: [
                    {
                        path: "/main/market/index",
                        component: MarketPage
                    },
                    {
                        path: "/main/market/eCalendar",
                        component: ECalendarPage,
                    },
                    {
                        path: "/main/market/watchList",
                        component: WatchListPage,
                    },

                    {
                        path: "/main/market/IpoCalendar",
                        component: IpoCalendarPage,
                    },

                    {
                        path: "/main/market/latestPatterns",
                        component: PatternsPage,
                    },
                    {
                        path: "/main/market/patternDetail/:strategy",
                        component: PatternDetail,
                    },
                    {
                        path: '/main/market/opportunities',
                        component: OpportunitiesPage
                    }
                ]
            },


            {
                path: "/main/stock",
                component: StockIndexPage,
                routes: [
                    {
                        path: "/main/stock/statistic/:symbol",
                        component: StatisticPage,
                    },
                    {
                        path: "/main/stock/detail/:symbol/:strategy",
                        component: StockPage,
                    },
                    {
                        path: "/main/stock/recommend/:symbol",
                        component: RecommendPage,
                    },
                    {
                        path: "/main/stock/sales/:symbol",
                        component: SalesPage,
                    },
                ]
            },

            {
                path: "/main/stockCompany",
                component: CompanyPage,
            },
            {
                path: "/main/analyst",
                component: AnalystPage,
            },
            {
                path: "/main/ownerShip",
                component: OwnerShipPage,
            },

            {
                path: "/main/videoPlay/:cid/:id",
                component: VideosPlayPage,
            },

            {
                path: "/main/publishVideo",
                component: PublishVideoPage,
            },
            {
                path: "/main/publishNews",
                component: PublishNewsPage,
            },
            // {
            //     path: "/main/creatorCenter",
            //     component: CreatorCenterPage,
            // },
            {
                path: "/main/userCenter/:id/myChannel/videos",
                component: CreatorCenterMyChannelVideos,
            },
            {
                path: "/main/userCenter/:id/myChannel/playlist",
                component: CreatorCenterPlayList,
            },
            {
                path: "/main/userCenter/:id/myChannel/community",
                component: MyCommunityPage,
            },
            {
                path: "/main/communityDetail/:userid/:id",
                component: CommunityDetailPage,
            },
            {
                path: "/main/myCenter",
                component: UserIndexPage,
                routes: [
                    {
                        path: "/main/myCenter/center",
                        component: MyCenterPage,
                    },
                    {
                        path: "/main/myCenter/notification",
                        component: NotificationPage,
                    },
                    {
                        path: "/main/myCenter/followers",
                        component: Followers,
                    },
                    {
                        path: "/main/myCenter/following",
                        component: Following,
                    },
                    {
                        path: "/main/myCenter/payment",
                        component: Payment,
                    }, {
                        path: "/main/myCenter/withdraw",
                        component: WithDrawPage,
                    }, {
                        path: "/main/myCenter/payorders/:type",
                        component: PayOrders,
                    },
                ]
            },
            {
                path: "/main/userCenter/:id/myChannel/playlistDetails",
                component: CreatorCollectionDetailVideoList
            },
           
            {
                path: '/main/videoSearch',
                component: CreatorVideoSearchPage
            },
            {
                path: '/main/watchLog',
                component: WatchLog
            },

            {
                component: UserCenter,
                path: "/main/userCenter/:id",
                routes: [
                    {
                        path: '/main/userCenter/:id/profile',
                        component: UserProfile
                    },
                    {
                        path: '/main/userCenter/:id/videoList',
                        component: UserVideoList
                    },
                    {
                        path: '/main/userCenter/:id/collList',
                        component: UserCollectionList
                    },
                    {
                        path: '/main/userCenter/:id/community',
                        component: CommunityPage
                    },

                    {
                        path: '/main/userCenter/:id/subList',
                        component: UserSubList
                    }
                ]
            }

        ]
    },
];



