import { useEffect } from "react";
import { useParams } from "react-router";
import { CreatorSubscribeListService } from "../../services/CreatorService";
import { useRequest } from "../../utils/hooks";
import { ViewBlock } from "../CreatorCenter/CreatorCenter";
import { CreatorList } from "../CreatorCenter/CreatorList";

import './UserSubList.scss';

const getSubUsers = ({ id = '' }) => new CreatorSubscribeListService({ userid: id, psize: 999 }).send()

export function UserSubList() {
  const { id } = useParams<any>();
  const { data = [], loading, load } = useRequest(getSubUsers, { autoLoad: false });
  useEffect(() => { load({ id }) }, [id]);
  return (
    <div className="user-sub-list">
      <ViewBlock title="订阅列表" loading={loading} noData={!data.length}>
        <CreatorList list={data} />
      </ViewBlock>
    </div>
  );
}