import { Ajax } from '../utils/http';

export class VideoLabelService {
    public async send(): Promise<LabelItem[]> {
        let response: LabelItem[];
        try {
            response = await new Ajax<LabelItem[]>('/creator/videoLabelList').post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}


/*Data*/
export interface LabelItem {
    id: string;
    name: string;
    enname:string;
  }
  
  