import './Patterns.scss';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Loading } from '../../../components/Loading';
import { klineStore } from '../../../stores/KlineStore';
import { StrategiesMap } from '../../../services/StatisticService';
import { SymbolItem } from '../../../services/KlineDetailService';
import { StockLogo } from '../../../components/StockLogo';
import StatisticStore from '../../../stores/StatisticStore';
import { mainStore } from '../../../stores/MainStore';


export const PatternDetail = observer((props: any) => {
  let strategy: string = props.match.params.strategy;
  useEffect(() => {
    klineStore.getDetailData(strategy);
  }, []);
  var strategyMap = StrategiesMap[klineStore.detail?.pattern_name.en || ''] || {};
  return (
    <div className="klineDetailWrapper">
      <div className="klineDetailContent" >
        {klineStore.detailloading ? <Loading /> : <div className="content">
          <div className="klineItem ">
            <div className="tit-pic ">
              {strategy && <img src={`https://aolinews.oss-accelerate.aliyuncs.com/patterns/${klineStore.detail?.pattern_name.en?? ''}.png`} />}
            </div>
            <div className="content" >
              <div className="title">{mainStore.isEn ? klineStore.detail?.pattern_name.en : klineStore.detail?.pattern_name.ch}</div>
              <div className="info"> {mainStore.isEn ? klineStore.detail?.description.en : klineStore.detail?.description.ch}</div>
            </div>
          </div>
          <div className="list">
            {klineStore.detail?.symbols.map((s: SymbolItem, i) => {
              return <div className="info" key={i} onClick={() => {
                window.scrollTo(0, 0);
                StatisticStore.symbol = s.symbol;
                StatisticStore.strategy = klineStore.detail?.pattern_name.en || '';
                props.history.push(`/main/stock/detail/${s.symbol}/${klineStore.detail?.pattern_name.en}`);
              }}>
                <StockLogo symbol={s.symbol} />
                <div className="followInfo">
                  <div className="titleInfo">
                    <div className="title">{s.symbol.replaceAll('_','  ')}</div>
                    <div className="subtitle">{s.name}</div>
                  </div>
                  <div className="change">{s.latest_change}</div>
                  <div className="data" style={{ color: s.latest_return < 0 ? '#EC4D3D' : '#4CB94D' }}>{(s.latest_return * 100).toFixed(2)}%</div>
                </div>
              </div>
            })}
          </div>
        </div>}

      </div>
    </div>
  )
});