import { Ajax } from '../utils/http';

export class CreatorNewsService {
    private page: number;
    constructor(_page: number) {
        this.page = _page;
    }

    public async send(): Promise<Array<CreatorNewsItem>> {
        let response: Array<CreatorNewsItem>;
        let data = {
            p: this.page,
            psize: 12
        }
        try {
            response = await new Ajax<Array<CreatorNewsItem>>('/creator/getArticleDraftList', data).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

