
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import { FC, useEffect, useState } from "react";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { mainStore } from "../../stores/MainStore";
import { useRequest, useRequestLoadMore, useScrollToBottomEvent, useTranslator } from "../../utils/hooks";
import { Ajax } from "../../utils/http";
import { PaymentSelector } from "../Stripe/Stripe";
import { Modal } from "./Common";
import { AddCardForm, getAllPayments } from "./Payment";

import './PayOrders.scss';
import { stripeStore } from "../../stores/StripeStore";
import { IconPlayList } from "./Icons";
import { userStore } from "../../stores/UserStore";
import { observer } from "mobx-react";

interface OrderData {
  id: string;
  stripeid?: string;
  orderno?: string;
  ordertype?: string;
  tempid?: string;
  paystatus?: string;
  totalmoney: string;
  createtime: string;
  paystatusStr?: string;
  ordertypeStr: string;
  timeStr?: string;
  info?: any;
}

async function getOrders({ ordertype = 1, pageNum = 1, pageSize = 10 }) {
  if (ordertype == 7) {
    let res = await new Ajax<OrderData[]>('/creator/withdrawApplyList', { p: pageNum, psize: pageSize }).post();
    return res.map((r: any) => {
      let remark = r.remarks && r.status == "2" ? `(${r.remarks})` : '';
      return {
        id: r.id,
        createtime: r.createtime,
        ordertypeStr: '提现',
        enordertypeStr: 'Withdraw',
        totalmoney: r.money,
        paystatusStr: (r.statustxt || ''),
        enpaystatusStr: (r.enstatustxt || ''),
      }
    });
  } else if (ordertype == 8) {
    let res = await new Ajax<OrderData[]>('/creator/commissionList', { p: pageNum, psize: pageSize }).post();
    res = res.filter((r) => r.info && !r.info.bankcardid);
    return res.map((r: any) => {
      return {
        id: r.id,
        orderno: r.orderid,
        createtime: r.createtime,
        ordertype: r.info.trycount ? '4' : '3',
        ordertypeStr: `${r.info.trycount ? '合集' : '视频'}收益`,
        enordertypeStr: `${r.info.trycount ? 'Playlist' : 'Video'} Income`,
        totalmoney: r.money,
        paystatusStr: (r.statustxt || ''),
        enpaystatusStr: (r.enstatustxt || ''),
        info: r.info
      }
    });
  }
  return new Ajax<OrderData[]>('/user/orderList', { ordertype, p: pageNum, psize: pageSize }).post();
}


const VideoInfo = ({ info }: any) => {
  const { translate } = useTranslator();
  return (
    <div className="video-info">
      <div className="thumb">
        <img src={info.thumb ?? info.cover} />
        {typeof info.videocount === 'number' && <div className="thumb-info">
          <span>{info.videocount} {translate('creator.playlist.4')}</span>
          {IconPlayList}
        </div>}
      </div>
      <div className="author">
        <img src={info.avatar ?? 'https://aolinews.oss-accelerate.aliyuncs.com/avatar/20220419142328.jpg'} />
      </div>
      <div className="item-info">
        <div className="title">{info.title ?? info.name}</div>
        <div className="author-name">{info.author ?? info.username}</div>
        <div className="intro">
          <div className="gray">{translate('creator.playlist.2')} {info.createtime}</div>
        </div>
        <div className="desc gray">
          {info.abstract ?? info.describe}
        </div>
      </div>
    </div>
  );
}

const OtherInfo = ({ info, type }: any) => {
  const { translate } = useTranslator();
  if (type === '6') {
    return (
      <div className="other-info">
        <div className="desc">{translate('gold.form.a')}: {info.pricerange}</div>
      </div>
    );
  } else if (type === '1' || type === '2') {
    return (
      <div className="other-info">
        <h2>{info.name}</h2>
        <div className="desc">{info.desc}</div>
      </div>
    )
  } else {
    return <div />
  }
}

const PaymentSelectorArea: FC<{ getSercet?: Function; onAdd: () => void; onComplete: () => void }> = ({ getSercet, onAdd, onComplete }) => {
  const { data = {}, loading } = useRequest(getAllPayments);
  const { list: allPayments, def } = data as any;
  const defPaymentId = def?.invoice_settings?.default_payment_method ?? '';
  const { translate } = useTranslator();
  return (
    <div>
      {loading ? <Loading size="mini" /> : (
        <PaymentSelector
          getSercet={getSercet}
          payments={allPayments}
          defaultPayment={defPaymentId}
          success={onComplete}
          hideExtraInfo={true}
          onAdd={onAdd}
          submitBtnTxt={translate('payOrders.9')}
        />
      )}
    </div>
  );
}

const PayOrderDetail: FC<{ data: any, typeTxt: string; orderId: string; onClose: () => void; onCompletePay: () => void; }> = ({ data, typeTxt, orderId, onClose, onCompletePay }) => {
  // const { data, loading, load } = useRequest(getOrderDetail, { autoLoad: false });
  const { translate } = useTranslator();
  const [showAddCard, setShowAddCard] = useState(false);
  const [stripePromise, setStripePromise] = useState<any>();
  useEffect(() => {
    setStripePromise(loadStripe(GlobalConfig.stripeKey));
  }, []);

  return (
    <Modal
      title={showAddCard ? (
        <a className="back2order-btn" onClick={() => setShowAddCard(false)}>{translate('payOrders.8')}</a>
      ) : (
        <div className="order-detail-title">
          {translate('payOrders.6')}
          {data?.orderno && <span className="order-no">#{data?.orderno}</span>}
        </div>
      )}
      visible={true}
      closeIcon={showAddCard}
      onCancel={onClose}>

      {data.info ? (
        <div className="pay-order-detail">
          <div className="product-type">{translate('payOrders.7')}: <span className="strong">{typeTxt}</span></div>
          {data.ordertype === '3' || data.ordertype === '4' ? (
            <VideoInfo info={data.info} />
          ) : (
            <OtherInfo info={data.info} type={data.ordertype} />
          )}
          <div className="order-time">
            {translate('payOrders.12')} {data.createtime}
          </div>
          <div className="price-area">
            <label>{translate('payOrders.11')}</label>
            <div>${data.totalmoney}</div>
          </div>
          {/* {data.paystatus === '0' && (
                <Elements stripe={stripePromise} options={{ locale: mainStore.isEn ? 'en' : 'zh' }}>
                  <PaymentSelectorArea getSercet={data.ordertype === '6' ? goldStore.purchase.bind(goldStore) : undefined} onAdd={() => setShowAddCard(true)} onComplete={onCompletePay} />
                </Elements>
              )} */}
        </div>
      ) : (
        <div className="no-order-data">
          订单已失效
        </div>
      )}
    </Modal>
  );
};

export const PayOrders: FC<{}> = observer((props: any) => {
  let { type } = props.match.params;
  const { data = [], reloading, reload, moreLoad } = useRequestLoadMore(getOrders);
  const { translate } = useTranslator();
  const [orderType, setOrderType] = useState(parseInt(type || '1'));
  const [curOrderId, setCurOrderId] = useState<string>();
  useEffect(() => reload({ ordertype: orderType }), [orderType]);
  useScrollToBottomEvent(moreLoad);
  return (
    <div>
      <div className="pay-orders-page">
        <div className="pay-orders-nav">
          {[...[1, 2, 3, 4, 6], ...(userStore.userInfo?.identity == 'creator' ? [7, 8] : [])].map((type) => (
            <a className={type === orderType ? 'active' : ''} onClick={() => setOrderType(type)}>
              {translate(`payOrders.nav.${type}`)}
            </a>
          ))}
        </div>
        {reloading ? <Loading size="mini" /> : data.length <= 0 ? <NoData /> : (
          <table>
            <thead>
              <tr>
                {orderType < 7 && <th>{translate('payOrders.1')}</th>}
                <th>{translate('payOrders.2')}</th>
                <th>{translate('payOrders.3')}</th>
                <th>{translate('payOrders.4')}</th>
                <th>{translate('payOrders.5')}</th>
              </tr>
            </thead>
            <tbody>
              {data.map((it: any) => (
                <tr key={it.id} onClick={() => {
                  if (orderType == 7) {
                    return;
                  }
                  setCurOrderId(it.id)
                }}>
                  {orderType < 7 && <td>{it.orderno}</td>}
                  <td>{it.createtime.slice(0, 16)}</td>
                  <td>{mainStore.isEn ? it.enordertypeStr : it.ordertypeStr}</td>
                  <td>{orderType < 8 ? '-' : '+'}{it.totalmoney}</td>
                  <td style={{ color: it.paystatus == "0" ? "rgb(236, 77, 61)" : "rgb(76, 185, 77)" }}>{mainStore.isEn ? (it.paystatus == "0" ? "Failed" : it.enpaystatusStr) : (it.paystatus == "0" ? "支付失败" : it.paystatusStr)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        {curOrderId && <PayOrderDetail
          data={data.find((d) => d.id == curOrderId)}
          typeTxt={translate(`payOrders.nav.${orderType}`)}
          orderId={curOrderId}
          onClose={() => setCurOrderId(undefined)}
          onCompletePay={() => {
            setCurOrderId(undefined);
            reload({ ordertype: orderType });
          }}
        />}
      </div>
    </div>
  );
})