import { makeAutoObservable, } from "mobx";
import moment from "moment";
import { showError, showLoading, showSuccess } from "../components/Message";
import ChartsConfig from "../config/ChartsConfig";
import { ResearchFollowService } from "../services/ResearchDetailService";
import { ResearchCollectService } from "../services/ResearchService";
import { Assets, CashFlows, Incomes, LatestRecommendations, StaticticService, StatisticResponse, StatisticType, Strategies, StrategyLine, Summary } from "../services/StatisticService";
import { StockDescriptionData, StockDescriptionService } from "../services/StockDescriptionService";
import { StockFollowItem, StockFollowService } from "../services/StockFollowService";
import { MarketTitle, StockMarketTitleService } from "../services/StockMarketTitleService";
import { StockNewsItem, StockNewsService } from "../services/StockNewsService";
import { OwnershipData, OwnerShipTrade, StockOwnerShipService, StockTraderService } from "../services/StockOwnerShipService";
import { Above, Below, QuotaSummary, QuotaType, StockQuotaResponse, StockQuotaService } from "../services/StockQuotaService";
import { AnalystData, RatedData, Recommendation, StockAnalystService, StockRateService } from "../services/StockRateService";
import { SalesData, StockSaleService } from "../services/StockSaleService";
import { StockSymbol, WatchListService, WatchMarket, WatchMarketService, WatchSymbolData } from "../services/WatchlistService";
import { LocalStorageService } from "../utils/storage";
import ChartStore from "./ChartStore";


class StatisticStore {
    chart: any;
    ownerShipChart: any;
    summary?: Summary;
    incomes?: Incomes;
    assets?: Assets;
    cashFlows?: CashFlows;
    rateData?: RatedData;
    strategies: Array<Strategies> = [];
    strategyLines: Array<StrategyLine> = [];
    symbols: StockSymbol[] = [];
    private _symbols: StockSymbol[] = [];
    followedSymbols: string[] = [];
    followedItems: StockFollowItem[] = [];
    type: StatisticType = StatisticType.incomes;
    quotaType: QuotaType = QuotaType.above;
    noData: boolean = false;
    loading: boolean = false;
    marketLoading: boolean = false;
    followLoading: boolean = false;
    getfollowLoading: boolean = false;


    loadingNews: boolean = false;
    loadingMoreNews: boolean = false;
    noMore: boolean = false;
    rateLoading: boolean = false;
    analystLoading: boolean = false;
    tradeLoading: boolean = false;
    salesLoading: boolean = false;
    analyst: string = '';
    analysts: AnalystData[] = [];
    sales: SalesData[] = [];
    ownerShipName: string = '';
    ownerShipTrade: OwnerShipTrade[] = [];

    descriptionLoading: boolean = false;
    marketsAu?: WatchMarket;
    marketsUs?: WatchMarket;
    symbol: string = '';
    strategy: string = '';
    ownerShip?: OwnershipData;
    marketTitle?: MarketTitle;
    stockNews: StockNewsItem[] = [];
    statisticData?: StatisticResponse;
    stockDescription?: StockDescriptionData;
    buyRate: number = 0;
    holdRate: number = 0;
    sellRate: number = 0;
    newsStart: moment.Moment = moment();
    newsEnd: moment.Moment = moment();
    constructor() {
        makeAutoObservable(this);
    }

    public async getData(symbol: string) {
        this.symbol = symbol;
        this.loading = true;
        await Promise.all([this.getStatisData(), this.getMarketTitle(), this.getOnwerShip(), this.getStockNews()]);
        this.loading = false;
        this.paintChart();
    }

    public async getMarketTitle() {
        var res: MarketTitle;
        res = await new StockMarketTitleService(this.symbol).send();
        this.marketTitle = res;
    }



    public async getStockNews(loadmore: boolean = false) {
        if (this.loadingMoreNews) {
            return;
        }
        if (loadmore) {
            if (this.noMore) {
                return;
            }
            this.loadingMoreNews = true;
            this.newsEnd = this.newsStart.clone();
            this.newsStart = this.newsStart.clone().subtract(1, "months");
        } else {
            this.loadingNews=true;
            this.newsStart = moment().subtract(1, "months");
            this.newsEnd = moment().add(1, 'days');
        }
        var response: StockNewsItem[];
        try {
            response = await new StockNewsService(this.symbol, this.newsStart.format("YYYY-MM-DD"), this.newsEnd.format("YYYY-MM-DD")).send();
        } catch (e: any) {
            this.loadingMoreNews = false;
            this.loadingNews=false;
            return;
        }
        this.loadingMoreNews = false;
            this.loadingNews=false;
            if (loadmore) {
            this.noMore = response.length === 0;
            this.stockNews.push(...response);
        } else {
            this.stockNews = response;
        }
    }

    public async getStatisData() {
        var statisticData: StatisticResponse;
        statisticData = await new StaticticService(this.symbol).send();
        this.statisticData = statisticData;
    }

    public async getCompanyInfo() {
        var res: StockDescriptionData;
        this.descriptionLoading = true;
        res = await new StockDescriptionService(this.symbol).send();
        this.descriptionLoading = false;
        this.stockDescription = res;
    }



    public async getOnwerShip() {
        var res: OwnershipData;
        res = await new StockOwnerShipService(this.symbol).send();
        this.ownerShip = res;
    }

    public async getRecommend(symbol: string) {
        var res: RatedData;
        this.symbol = symbol;
        this.rateLoading = true;
        res = await new StockRateService(this.symbol).send();
        this.rateLoading = false;
        this.rateData = res;
        var Highcharts: any = (window as any).Highcharts;
        this.chart = Highcharts.stockChart('recommendChart', ChartsConfig.setRecommendChart(
            [
                { dataLabels: { enabled: false }, y: res.rating_summary.buy },
                { dataLabels: { enabled: false }, y: res.rating_summary.hold },
                { dataLabels: { enabled: false }, y: res.rating_summary.sell },
            ]
        ));
        let buy = res.rating_summary.buy;
        let hold = res.rating_summary.hold;
        let sell = res.rating_summary.sell;
        this.buyRate = Math.floor((buy * 100) / (buy + hold + sell));
        this.sellRate = Math.floor((sell * 100) / (buy + hold + sell));
        this.holdRate = 100 - this.buyRate - this.sellRate;
    }

    public async getAnalystDetail() {
        var res: AnalystData[];
        this.analystLoading = true;
        res = await new StockAnalystService(this.analyst).send();
        this.analystLoading = false;
        this.analysts = res || [];

    }


    public async getSalesDetail() {
        var res: SalesData[];
        this.salesLoading = true;
        res = await new StockSaleService(this.symbol).send();
        await this.getOnwerShip();
        this.salesLoading = false;
        if (Object.keys(this.ownerShip?.insider_transactions_summary || {}).length) {
            this.paintOwnerShipChart();
        }
        this.sales = res;
    }



    public async getOwnerShipDetail() {
        var res: OwnerShipTrade[];
        this.tradeLoading = true;
        res = await new StockTraderService(this.ownerShipName).send();
        this.tradeLoading = false;
        this.ownerShipTrade = res;

    }

    public async follow() {
        if (this.followLoading) {
            return;
        }
        this.followLoading = true;

        this.followLoading = false;
        var _followed: string[] = this.followedSymbols;
        var symbol = this.symbol.toLowerCase();
        var symbolIdx = _followed.indexOf(symbol);
        if (symbolIdx == -1) {
            _followed.push(symbol);
        } else {
            _followed.splice(symbolIdx, 1);
        }
        this.followedSymbols = _followed;
        try {
            await new ResearchFollowService(this.symbol, symbolIdx == -1 ? 1 : 0, 3).send();
            await this.getFollowed(_followed.join(","));
        } catch (e: any) {
            this.followLoading = false;
            showError(e);
            return;
        }
    }

    public async getFollowed(_symbols?: string) {
        var list: StockFollowItem[];
        this.getfollowLoading = true;
        try {
            var symbols: string;
            if (!_symbols) {
                var ids = await new ResearchCollectService(1, 3).send();
                var _list = (ids.list || []).map((l: any) => l.objectid);
                symbols = _list.join(',');
            } else {
                symbols = _symbols;
            }
            list = await new StockFollowService(symbols).send();
        } catch (e) {
            console.log('getFollowed error', e);
            this.getfollowLoading = false;

            return;
        }
        // list = list.reverse();
        this.getfollowLoading = false;

        this.followedItems = list;
        this.followedSymbols = list.map((l: StockFollowItem) => l.symbol.toLowerCase());
    }


    public async getWatchMarketlist(country: string, hideloading?: boolean) {
        var response: WatchMarket;
        if (!hideloading)
            this.marketLoading = true;
        try {
            response = await new WatchMarketService(country).send();
        } catch (e) {
            this.marketLoading = false;
            return;
        }
        this.marketLoading = false;
        const market_movements = response.market_movements;
        let markets: WatchMarket = {
            ...response,
            market_movements: market_movements.map(o => {
                const [name] = Object.keys(o).filter(k => k !== 'symbol');
                const [date, value, rate] = o[name];
                return { date, value, rate, name, symbol: o.symbol };
            })
        };
        if (country == 'US') {
            this.marketsUs = markets;
        } else {

            this.marketsAu = markets;
        }
    }



    public async getWatchSymbolist() {
        var response: WatchSymbolData;
        var _local = LocalStorageService.get('watchlist');
        if (_local) {
            this.symbols = JSON.parse(_local);
        }
        try {
            response = await new WatchListService().send();
        } catch (e) {
            console.log("getWatchlist error ", e);
            return;
        }
        this.symbols = response.symbol_and_name;
        LocalStorageService.set('watchlist', JSON.stringify(response.symbol_and_name));
    }

    public onSearchInputChange(keyword: string) {
        var _symbols: StockSymbol[] = JSON.parse(JSON.stringify(this._symbols));
        var _keyword: string = keyword.toLowerCase();
        if (!_keyword) {
            this.symbols = []
        } else {
            this.symbols = _symbols.filter((s) => {
                var isSymbolContained = s.symbol.toLowerCase().indexOf(_keyword) > -1;
                var isNameContained = s.name.toLowerCase().indexOf(_keyword) > -1;
                return isSymbolContained || isNameContained;
            });
        }
    }


    public async paintChart() {
        this.noData = false;
        if (this.statisticData) {
            var _strategyLineMap = {};
            var _strategyLines: StrategyLine[] = [];
            this.incomes = this.statisticData.finances.incomes;
            this.assets = this.statisticData.finances.assets;
            this.cashFlows = this.statisticData.finances.cash_flows;
            this.summary = this.statisticData.summary;
            this.strategies = this.statisticData.strategies || [];
            this.statisticData.strategies.forEach((s) => {
                _strategyLineMap[s.strategy_name] = _strategyLineMap[s.strategy_name] || [];
                _strategyLineMap[s.strategy_name].push(s);
            })
            for (var key in _strategyLineMap) {
                _strategyLines.push({ strategy_name: key, strategies: _strategyLineMap[key] });
            }
            this.strategyLines = _strategyLines;
        } else if (!this.chart) {
            return;
        }
        setTimeout(() => {
            this.handleTabChange(StatisticType.incomes, true, 0);
        }, 0);
    }

    public async paintOwnerShipChart() {
        var Highcharts: any = (window as any).Highcharts;
        var line1: Array<Array<number>> = [], line2: Array<Array<number>> = [];
        var buy: Object = this.ownerShip?.insider_transactions_summary?.buy_qty || {};
        var sell: Object = this.ownerShip?.insider_transactions_summary?.sell_qty || {};
        for (var key in buy) {
            line1.push([Date.parse(key), buy[key]]);
        }
        for (var key in sell) {
            line2.push([Date.parse(key), sell[key]]);
        }
        this.chart = Highcharts.stockChart('ownerShipChart', ChartsConfig.setOwnerShipChart(line1, line2));
    }

    handleTabChange(type: StatisticType, init: boolean = false, index: number) {
        var Highcharts: any = (window as any).Highcharts;
        var line1: Array<Array<number>> = [], line2: Array<Array<number>> = [];
        this.type = type;
        switch (this.type) {
            case StatisticType.incomes:
                var datesIncomes: Array<string> = this.incomes?.financial_years || [];
                var revenues: Array<number> = this.incomes?.total_revenues || [];
                var incomes: Array<number> = this.incomes?.net_incomes || [];
                for (let i = 0; i < datesIncomes.length; i++) {
                    line1.push([Date.parse(datesIncomes[i]), revenues[i]]);
                    line2.push([Date.parse(datesIncomes[i]), incomes[i]]);
                }
                break;
            case StatisticType.assets:
                var datesAssets: Array<string> = this.assets?.financial_years || [];
                var revenuesAssets: Array<number> = this.assets?.total_assets || [];
                var liabs: Array<number> = this.assets?.total_liabs || [];
                for (let i = 0; i < datesAssets.length; i++) {
                    line1.push([Date.parse(datesAssets[i]), revenuesAssets[i]]);
                    line2.push([Date.parse(datesAssets[i]), liabs[i]]);
                }
                break;
            case StatisticType.cashFlows:
                var datesCashFlows: Array<string> = this.cashFlows?.financial_years || [];
                var cashes: Array<number> = this.cashFlows?.cashes || [];
                var changeInCashes: Array<number> = this.cashFlows?.change_in_cashes || [];
                for (let i = 0; i < datesCashFlows.length; i++) {
                    line1.push([Date.parse(datesCashFlows[i]), cashes[i]]);
                    line2.push([Date.parse(datesCashFlows[i]), changeInCashes[i]]);
                }
                break;
        }
        line1.sort((a, b) => a[0] - b[0]);
        line2.sort((a, b) => a[0] - b[0]);
        if (this.chart && !init) {
            this.chart.series[0].update({ data: line1 });
            this.chart.series[1].update({ data: line2 });
        } else {
            if (ChartStore.stockTab === 0) {
                this.chart = Highcharts.stockChart('statisticChart', ChartsConfig.setStatisticChart(line1, line2));
                this.chart.isSubChart = true;
            }
        }
    }
}

export default new StatisticStore();