import { useEffect, useState } from 'react';
import imgTop from '../images/main/toTop.png';
import VideoThumbnail from 'react-video-thumbnail';
import { Spin } from 'antd';
import { settingStore } from '../stores/SettingStore';
import { showError } from './Message';
import { mainStore } from '../stores/MainStore';

export interface VideoImageProps {
  url: string,
  time: number,
  onClick: Function,
  mask: any
}

function VideoImage(props: VideoImageProps) {
  const { url, time, mask } = props;
  const [imgUrl, setImgUrl] = useState('');
  const [loading, showLoading] = useState(true);
  return (
    <div className='videoThumbnail' onClick={() => {
      if (imgUrl) {
        props.onClick(imgUrl);
      }
    }}>
      <VideoThumbnail
        videoUrl={url}
        thumbnailHandler={(thumbnail: any) => {
          settingStore.OssUpload(thumbnail, (url: string | null) => {
            if (url) {
              showLoading(false);
              setImgUrl(url)
            } else {
              showError(mainStore.isEn ? 'Load image error' : '加载失败')
            }
          }, true);
        }
        }
        snapshotAtTime={time}
      >
      </VideoThumbnail>
      {loading && <Spin></Spin>}
      {mask}
    </div>

  );
}

export default VideoImage;
