import { observer } from 'mobx-react';
import StatisticStore from '../../../../stores/StatisticStore';
import { useEffect } from 'react';
import NoData from '../../../../components/NoData';
import './Statistic.scss';
import { Loading_icon } from '../../../../images/Loading';
import { StockLogo } from '../../../../components/StockLogo';
import { indexStore } from '../../../../stores/IndexStore';
import GlobalConfig from '../../../../config/GlobalConfig';
import moment from 'moment';
import { Icon_Back } from '../../../../images/back';
import { OwnerShipTrade } from '../../../../services/StockOwnerShipService';
import  { translate } from '../../../../components/Text';
import statisticStore from "../../../../stores/StatisticStore";

export const OwnerShipPage = observer((props:any) => {
  useEffect(() => {
    StatisticStore.getOwnerShipDetail();
    }, []);
    return <div className="ownerShipWrapper">
        <div className="top">
            <div className="back" onClick={() => {
                GlobalConfig.history.goBack()
            }}>
                <img src={Icon_Back} alt="" />
                <div className="text">{translate('common.r')}</div>
            </div>

            <div className="line"></div>
            <div className="text">{StatisticStore.ownerShipName}</div>
        </div>
        {StatisticStore.tradeLoading ? <div className="loading">
            <img src={Loading_icon} alt="" />
        </div> : StatisticStore.ownerShipTrade.length == 0 ?
            <div style={{ height: '80px', margin: "0 auto" }}>
                <NoData />
            </div>
            :
            <div className="list">
                {StatisticStore.ownerShipTrade.map((f: OwnerShipTrade, i) => {
                    return <div className="info" key={i} onClick={() => {
                        window.scrollTo(0, 0);
                        indexStore.link = `/statistic/${f.symbol}`;
                        StatisticStore.symbol = f.symbol;
                        GlobalConfig.history.push(`/main/stock/statistic/${f.symbol}`);
                    }}>
                        <StockLogo symbol={f.symbol} />
                        <div className="followInfo">
                            <div className="titleInfo">
                                <div className="title">{f.symbol}</div>
                            </div>
                            <div className="change">
                                <div className="title" style={{fontWeight:"normal",fontSize:"14px"}}>{translate('markets.owner_ship.f')}</div>
                                <div>{f.change}</div>
                                </div>
                            <div className="change">
                                <div className="title" style={{fontWeight:"normal",fontSize:"14px"}}>{translate('markets.owner_ship.g')}</div>
                                <div>{f.share}</div>
                                </div>
                            <div className="data">{moment(f.transactionDate).format( 'YYYY-MM-DD')}</div>
                        </div>
                    </div>
                })}
            </div>
        }
    </div>

});