import { Form, Input, Button, Result } from 'antd';
import { observer } from 'mobx-react';
import { translate } from '../../components/Text';
import { publishStore } from '../../stores/PublishStore';
import { userStore } from '../../stores/UserStore';
import './Apply.scss';
const FormItem = Form.Item;
const { TextArea } = Input;


export const ApplyPage = observer(() => {

  const doSubmit = (values: any) => {
    publishStore.apply(values);
  };
  return (
    <div className="creator-apply-page">
      <h1>{translate("creator.center.4")}</h1>
      {(userStore.userInfo?.applyCreatorStatus == 0 || publishStore.reviewing) &&
        <Result
          className='result'
          status="success"
          title={translate("creator.center.5")}
          subTitle={translate("creator.center.6")}

        />
      }
      {(userStore.userInfo?.applyCreatorStatus == -1 || !userStore.userInfo?.applyCreatorStatus) && !publishStore.reviewing && <Form className="form" labelAlign="left" onFinish={doSubmit}>
        <FormItem label={translate("creator.apply.1")} name="name" required>
          <Input placeholder={translate("creator.apply.5")} />
        </FormItem>
        <FormItem label={translate("creator.apply.2")} name="email" required>
          <Input placeholder={translate("creator.apply.6")} />
        </FormItem>
        <FormItem label={translate("creator.apply.3")} name="phone" required>
          <Input placeholder={translate("creator.apply.7")} />
        </FormItem>
        <FormItem label={translate("creator.apply.4")} name="describe" required>
          <TextArea placeholder={translate("creator.apply.8") + '\n' + translate("creator.apply.9")} />
        </FormItem>
        <FormItem label=" ">
          <Button htmlType="submit" loading={publishStore.loading} className="submit-btn">{translate("creator.apply.10")}</Button>
        </FormItem>
      </Form>}
    </div>
  )
});