import { makeAutoObservable, } from "mobx";
import { ForgetPasswordService, LoginService, PhoneForgetPasswordService, PhoneLoginService, RegisterService, SendEmialService, SendPhoneService, UserInfo } from "../services/LoginService";
import { showError, showLoading, showSuccess } from "../components/Message";
import { userStore } from "./UserStore";
import eventBus from "../utils/eventBus";
import { mainStore } from "./MainStore";

enum LoginModel {
  login,
  register,
  registerPop,
  forgetPassword
}

enum FindType {
  email,
  phone,
}

enum LoginType {
  email,
  phone,
}

enum RegisterType {
  email,
  phone,
}

class LoginStore {

  public LoginModel: LoginModel = LoginModel.login;
  public loginType: LoginType = LoginType.email;
  public registerType: RegisterType = RegisterType.email;
  public findType: FindType = FindType.email;

  public fromIndex: boolean = false;
  public loginLoading: boolean = false;

  public registerLoading: boolean = false;

  public findLoading: boolean = false;

  public sending: boolean = false;

  public sendBtnText: string = 'mine.find.d';

  public countDwon: number = 60;

  private timer: any;

  constructor() {
    makeAutoObservable(this);
  }

  public async login(data: any, success: Function) {
    if (this.loginLoading) {
      return;
    }
    this.loginLoading = true;
    showLoading(!mainStore.isEn ? '登录中...' : 'Submitting...');
    let response: UserInfo;
    try {
      if (this.loginType == LoginType.email) {
        response = await new LoginService(data.email, data.password, 999).send();
      } else {
        var phoneNum = data.phone.indexOf('0') == 0 ? data.phone : `0${data.phone}`;
        response = await new PhoneLoginService(phoneNum, data.code, data.areacode).send();
      }
    } catch (e: any) {
      this.loginLoading = false;
      console.log("login error ", e)
      showError(e);
      return;
    }
    userStore.updateUser(response);
    userStore.init();
    this.loginLoading = false;
    showSuccess(!mainStore.isEn ? '登录成功！' : 'Sign in successfully');
    eventBus.emit('login');
    success && success();
  }

  public async register(data: any, success?: Function) {
    if (this.registerLoading) {
      return;
    }
    this.registerLoading = true;
    showLoading(!mainStore.isEn ? '注册中...' : 'Submitting...');
    if (data.phone) {
      var phoneNum = data.phone.indexOf('0') == 0 ? data.phone : `0${data.phone}`;
      data.phone = phoneNum;
      data.url=window.location.href;
    }
    let reponse: UserInfo;
    try {
      reponse = await new RegisterService(data).send();
    } catch (e: any) {
      this.registerLoading = false;
      showError(e);
      return;
    }
    this.registerLoading = false;
    userStore.updateUser(reponse);
    userStore.init();
    showSuccess(!mainStore.isEn ? '注册成功！' : 'Sign up successfully');
    success && success();
  }


  public async sendEmial(_emial: string, _type: number) {
    if (this.sending || (this.countDwon < 60 && this.countDwon > 0)|| !_emial) {
      return;
    }
    this.sending = true;

    this.sendBtnText = 'mine.find.f';
    try {
      await new SendEmialService(_emial, _type).send();
    } catch (e: any) {
      this.sending = false;
      this.sendBtnText = 'mine.find.d';
      showError(e);
      return;
    }
    this.sending = false;
    showSuccess(mainStore.isEn ? 'Code sent successfully' : '发送成功！');
    this.timer = setInterval(() => {
      if (this.countDwon === 0) {
        this.countDwon = 60;
        this.sendBtnText = 'mine.find.d';
        clearInterval(this.timer);
        return;
      }
      this.countDwon -= 1;
      this.sendBtnText = `${this.countDwon}s`;
    }, 1000);
  }

  public async sendPhone(_phone: string, _type: number, _areacode: string, _imagecodeid: string, _imagecode: string) {
    if (this.sending || (this.countDwon < 60 && this.countDwon > 0) || !_phone) {
      return;
    }
    this.sending = true;
    var phoneNum = _phone.indexOf('0') == 0 ? _phone : `0${_phone}`;

    this.sendBtnText = 'mine.find.f';
    try {
      await new SendPhoneService(phoneNum, _type, _areacode, _imagecodeid, _imagecode).send();
    } catch (e: any) {
      this.sending = false;
      this.sendBtnText = 'mine.find.d';
      showError(e);
      return;
    }
    this.sending = false;
    showSuccess(mainStore.isEn ? 'Code sent successfully' : '发送成功！');
    this.timer = setInterval(() => {
      if (this.countDwon === 0) {
        this.countDwon = 60;
        this.sendBtnText = 'mine.find.d';
        clearInterval(this.timer);
        return;
      }
      this.countDwon -= 1;
      this.sendBtnText = `${this.countDwon}s`;
    }, 1000);
  }

  public async findPassword(data: any) {
    if (this.findLoading) {
      return;
    }
    this.findLoading = true;
    showLoading(mainStore.isEn ? 'Submitting...' : '提交中...');
    try {
      if (this.findType == FindType.email) {
        await new ForgetPasswordService(data.email, data.captcha, data.password).send();
      } else {
        new PhoneForgetPasswordService(data.phone, data.captcha, data.password).send();
      }
    } catch (e: any) {
      this.findLoading = false;
      showError(e);
      return;
    }
    this.findLoading = false;
    showSuccess(mainStore.isEn ? 'Password set successfully' : '密码修改成功！');
    this.LoginModel = LoginModel.login;
  }


  setLoginModel(LoginModel: LoginModel) {
    this.LoginModel = LoginModel;
  }

}

const loginStore = new LoginStore();

export { loginStore, LoginModel, LoginType, RegisterType, FindType };