import { autorun, runInAction } from "mobx";
import { useCallback, useEffect, useRef, useState } from "react";
import { useLocation } from "react-router";
import { getTranslator } from "../components/Text";
import { UserInfo } from "../services/LoginService";
import { indexStore, SearchType } from "../stores/IndexStore";
import { mainStore } from "../stores/MainStore";
import { userStore } from "../stores/UserStore";
import { IVideoSearchContext, videoSearchStore } from "../stores/VideoSearchStore";


export interface RequestOptions {
  /**
   * 是否在初始化时自动加载， 默认为true
   */
  autoLoad?: boolean;
}

/**
 * hook: 使用网络请求
 * @param request 相应的请求方法
 * @param options 设置
 * @returns 
 */
export function useRequest<P = any, R = any>(
  request: (params: P) => Promise<R>,
  options: RequestOptions = {}
) {
  const { autoLoad = true } = options;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(undefined as undefined | R);
  const [error, setError] = useState(undefined as undefined | Error);

  const loadWithPromise = useCallback(async (params: P) => {
    setLoading(true);
    setError(undefined);
    try {
      const res = await request(params);
      setData(res);
      setLoading(false);
      return res;
    } catch (err) {
      const e = err instanceof Error ? err : new Error(`${err}`);
      setError(e);
      setLoading(false);
      throw e;
    }
  }, [request]);

  const load = useCallback((params: P) => {
    loadWithPromise(params);
  }, [loadWithPromise])

  useEffect(() => {
    if (autoLoad) {
      load({} as P);
    }
  }, []);

  return {
    loading,
    data,
    error,
    load,
    loadWithPromise
  };
}

export interface RequestLoadMoreOptions extends RequestOptions {
  /**
   * 每页数据量
   */
  pageSize?: number;
}

/**
 * hook: 使用下拉加载逻辑
 * @param request 相应的分页请求方法
 * @param options 设置
 * @returns 
 */
export function useRequestLoadMore<P = any, R = any>(
  request: (params: P & { pageNum: number; pageSize: number }) => Promise<R[]>,
  options: RequestLoadMoreOptions = {}
) {
  const { pageSize = 20, ...restOptions } = options;
  const {
    loadWithPromise,
    loading,
    ...rest
  } = useRequest(request, { ...restOptions, autoLoad: false });
  const [allData, setAllData] = useState([] as R[]);
  const [pageNum, setPageNum] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [mode, setMode] = useState("");
  const [curParams, setCurParams] = useState<P>();

  const afterLoad = (data: R[], isRefresh?: boolean) => {
    setAllData(isRefresh ? data : allData.concat(data));
    if (data.length > 0) {
      setPageNum(pageNum + 1);
    } else {
      setHasMore(false);
    }
  }

  const reload = useCallback((params: P, skipLoading?: boolean) => {
    if (loading && !skipLoading) return;
    setMode("reload");
    setPageNum(0);
    setHasMore(true);
    setAllData([]);
    setCurParams(params);
    loadWithPromise({ pageNum: 1, pageSize, ...(params as any) }).then((data) => afterLoad(data, true)
    )
  }, [loading, pageSize, loadWithPromise]);

  const moreLoad = useCallback(() => {
    if (loading || !hasMore) return;
    setMode("moreLoad");
    loadWithPromise({ pageNum: pageNum + 1, pageSize, ...(curParams as any) }).then(afterLoad);
  }, [loading, pageSize, curParams, loadWithPromise, pageNum, hasMore]);

  return {
    ...rest,
    reload,
    moreLoad,
    data: allData,
    reloading: loading && mode === "reload",
    moreLoading: loading && mode === "moreLoad",
  };
}

/**
 * hook: 侦听全局滚动条触底事件
 * @param listener 
 * @param minHeight 
 */
export function useScrollToBottomEvent(listener: () => void, minHeight = 100) {
  useEffect(() => {
    var middle: any = document.getElementsByClassName('ant-layout-content')[0];
    let scrollFuc = (event: any) => {
      if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - minHeight)) {
        listener();
      }
    }
    middle.addEventListener("scroll", scrollFuc);
    return () => {
      middle.removeEventListener("scroll", scrollFuc);
    }
  }, [minHeight, listener]);
}

export function useQuery() {
  const url = new URLSearchParams(useLocation().search);
  const records = {} as Record<string, string>;
  url.forEach((v, k) => { records[k] = v });
  return records;
}



export function useVideoSearch(options: IVideoSearchContext | ((value: string) => void)) {
  useEffect(() => {
    const ctx = typeof options === 'function' ? { searchSubmit: options } : options;
    let key = '';
    runInAction(() => {
      key = videoSearchStore.addContext(ctx);
      indexStore.searchType = SearchType.creator;
    });
    return () => {
      runInAction(() => {
        videoSearchStore.removeContext(key);
        if (!videoSearchStore.visible()) {
          indexStore.searchType = undefined;
        }
      });
    }
  }, [options]);
}

export function useCurrentUser() {
  const [user, setUser] = useState<UserInfo>();
  useEffect(() => {
    const disposer = autorun(() => {
      setUser(userStore.userInfo);
    });
    return disposer;
  }, []);
  return user;
}



export function useCurrentUserCheck(userId: string) {
  return userId === useCurrentUser()?.id;
}




export function useTranslator() {
  const [isEn, setEn] = useState(false);
  useEffect(() => {
    return autorun(() => {
      setEn(mainStore.isEn);
    });
  });
  return { isEn, translate: getTranslator(isEn) };
}

export function useResizeEvent(cb: (width: number, height: number) => void) {
  const cbRef = useRef(cb);
  cbRef.current = cb;
  useEffect(() => {
    const listener = () => {
      cbRef.current(window.innerWidth, window.innerHeight);
    };
    window.addEventListener('resize', listener);
    return () => window.removeEventListener('resize', listener);
  }, []);
}