import { Ajax } from '../utils/http';

export class StripeOrderService {
    private amount?: string;
    private objectid?: string;
    private ordertype?: number;
    private paymentMethod?: string;
    constructor(_amount?: string, _ordertype?: number, _objectid?: string,_paymentMethod?:string) {
        this.amount = _amount;
        this.ordertype = _ordertype;
        this.objectid = _objectid;
        this.paymentMethod = _paymentMethod;
    }
    public async send(): Promise<StripeOrder> {
        let response: StripeOrder;
        let data = {
            "amount": this.amount,
            "currency": "aud",
            "payment_method":this.paymentMethod,
            "payment_method_types": ["card"],
            "metadata": {
                "objectid": this.objectid,
                "ordertype": this.ordertype
            }
        }

        try {
            response = await new Ajax<StripeOrder>('/order/createPaymentIntent', {data:JSON.stringify(data)}).post();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

