import { Button, Form, Input, Popconfirm } from 'antd';
import { useEffect, useState } from 'react';
import { Img } from '../../components/Image';
import { showError, showLoading, showSuccess } from '../../components/Message';
import { translate } from '../../components/Text';
import { CommentDeletelService, CommentDetailService, CommentItem, CommentListService, CommentReplyService } from '../../services/CommentService';
import { userStore } from '../../stores/UserStore';
import { IconDelete, IconDown, IconUp } from '../UserCenter/Icons';
import './Comment.scss';
import { CommentSubItemPage } from './CommentSubItem';
import IconLike from '../../images/main/like.png';
import { commentStore } from '../../stores/CommentStore';
import { LocalStorageService } from '../../utils/storage';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { CommentLikeService } from '../../services/VideoService';

export interface CommentItemProps {
    key: any
    isSelf: boolean
    item: CommentItem
    success: Function
    onDelete: Function
}

export const deleteVideo = (id?: string, cb?: Function) => new CommentDeletelService(id || '').send().then(() => {
    cb && cb();
}, (e) => {
    showError(e);
});


export const CommentItemPage = (props: CommentItemProps) => {
    const [fold, setFold] = useState(false);
    const [reply, setReply] = useState(false);
    const [loading, seLoading] = useState(false);
    const [replying, setReplying] = useState(false);

    const [replyList, setReplyList] = useState<CommentItem[]>([]);
    useEffect(() => {
        if (parseInt(props.item.commentcount || '0')) {
            getReplyList();
        }
    }, [props])
    var getReplyList = async () => {
        if (loading) {
            return;
        }
        seLoading(true);
        let response: CommentItem[];
        try {
            response = await new CommentDetailService(props.item?.id || '').send();
        } catch (e) {
            showError(`${e}`);
            seLoading(false);
            return;
        }
        response = response.filter((r) => r.id != props.item.id);
        seLoading(false);
        setReplyList(response);
    }



    var replyComment = async (replyText?: string, beuserid?: string) => {
        if (!replyText || replying) {
            return;
        }
        setReplying(true);
        let replyId: string;
        try {
            if (!replyList.length) {
                await getReplyList();
            }
            replyId = await new CommentReplyService(parseInt(props.item?.objectid || ''), props.item?.objecttype || '1', replyText, props.item.id || '', props.item.id || '').send();
        } catch (e) {
            setReplying(false);
            showError(`${e}`);
            return;
        }
        setReplying(false);
        var Dates = new Date();
        var Y = Dates.getFullYear();
        var M = Dates.getMonth() + 1;
        var D = Dates.getDate();
        var H = Dates.getHours();
        var Mu = Dates.getMinutes();
        var times = Y + (M < 10 ? "-0" : "-") + M + (D < 10 ? "-0" : "-") + D + ' ' + (H < 10 ? "0" : "") + H + (Mu < 10 ? ":0" : ":") + Mu;
        let _resultList: any = [{
            id: replyId,
            pid: props.item.id,
            userid: userStore.userInfo?.id,
            username: userStore.userInfo?.username,
            userimg: userStore.userInfo?.avatar,
            content: replyText,
            createtime: times,
            likecount: '0',
            islike: 0
        }]
        _resultList.push(...replyList);
        replyText = '';
        setReplyList(_resultList);
        setFold(true);
        props.item.commentcount = `${(parseInt(props.item.commentcount || '') + 1)}`;
        setReply(false)
    }
    return (
        <div className="ttp-comment-item" key={props.key}>
            <div className="ttp-avatar auth-0 auth">
                <img src={props.item.userimg} alt="" />
            </div>
            <div className="comment-info">
                <div className="user-name">
                    <div className="left-content">
                        <span className="name">{props.item.username}</span>
                    </div>
                    {props.isSelf && <Popconfirm title={translate('creator.myVideos.4')} onConfirm={() => {
                        console.log("props.item", props.item)
                        return deleteVideo(props.item.id, () => {
                            props.onDelete && props.onDelete();
                        });
                    }}>
                        <a className="btn">{IconDelete}</a>
                    </Popconfirm>}
                </div>
                <div className="body">
                    <div style={{ whiteSpace: "pre-line" }} className="content">{props.item.content}</div>
                </div>
                <div className="footer">
                    <div className="left-action">
                        <span className="time">{props.item?.createtime?.substring(5, 16)}</span>
                        <div
                            className="like"
                            onClick={() => {
                                userStore.signInCheck(() => {
                                    let list = JSON.parse(JSON.stringify(commentStore.commentList));
                                    for (var i = 0; i < list.length; i++) {
                                        if (props.item.id == list[i].id) {
                                            list[i].likecount = `${parseInt(list[i].likecount) + (list[i].islike == 0 ? 1 : -1)}`;
                                            list[i].islike = list[i].islike == 0 ? 1 : 0;
                                            break;
                                        }
                                    }
                                    commentStore.commentList = list;
                                    new CommentLikeService(props.item.id, props.item.islike == 0 ? 1 : 0).send();
                                })
                            }}
                        >
                            <Img
                                className="left"
                                src={IconLike}
                                alt=""
                            />
                            <div className="likeCount left">{props.item.likecount}</div>
                        </div>
                        {userStore.userInfo?.id != props.item.userid && <div className="reply-btn" onClick={() => {
                            userStore.signInCheck(() => setReply(!reply));
                        }}>{translate(`home.video.${reply ? 'h' : 'g'}`)}</div>}
                    </div>
                </div>
                {parseInt(props.item.commentcount || '0') > 0 && <button className="check-more-reply" onClick={() => {
                    setFold(!fold);
                    if (!replyList.length) {
                        getReplyList();
                    }
                }}>{fold ? IconUp : IconDown} {translate(`home.video.${fold ? props.item.commentcount == '1' ? 'k1' : 'j' : props.item.commentcount == '1' ? 'k' : 'i'}`, { value: props.item.commentcount })}
                </button>}
                {reply &&
                    <div className='subReply'>
                        <Form
                            name="normal_login"
                            className="login-form"
                            onFinish={(v) => {
                                replyComment(v.replyText);
                            }}>
                            <div className="ttp-comment-input reply-wrapper">
                                <Form.Item
                                    name="replyText"
                                >
                                    <Input.TextArea className="comment-textarea" placeholder={translate('home.video.c')} />
                                </Form.Item>
                            </div>
                            <div className="action">
                                <Button className="submit-btn disable" htmlType="submit" loading={replying} >{translate('home.video.d')}</Button>
                            </div>
                        </Form>
                    </div>}


                {fold && <div className="reply-list">
                    {replyList.map((l, i) => {
                        return <CommentSubItemPage key={l.id} onLike={() => {
                            userStore.signInCheck(() => {
                                new CommentLikeService(l.id, l.islike == 0 ? 1 : 0).send();
                                let list = JSON.parse(JSON.stringify(replyList));
                                list[i].likecount = `${parseInt(list[i].likecount) + (list[i].islike == 0 ? 1 : -1)}`;
                                list[i].islike = list[i].islike == 0 ? 1 : 0;
                                setReplyList(list)
                            });
                        }} isSelf={props.isSelf} item={l} success={(item: any) => {
                            let _list = JSON.parse(JSON.stringify(replyList));
                            _list.splice(i + 1, 0, item);
                            setReplyList(_list);
                        }} pid={props.item.id} />
                    })}
                </div>}
            </div>
        </div>
    );
}