import { ResearchItem } from '../../services/ResearchService';
import { EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import '../Main/Main.scss';
import { verifyRule } from '../../utils/globalFuncs';
import { indexStore } from '../../stores/IndexStore';
import { Icon_collect } from '../../images/market/collect';
import { Icon_collect_on } from '../../images/market/collectOn';
import { Icon_view } from '../../images/market/view';
import { mainStore } from '../../stores/MainStore';
import { observer } from 'mobx-react';
import GlobalConfig from '../../config/GlobalConfig';
import { LocalStorageService } from '../../utils/storage';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { userStore } from '../../stores/UserStore';

interface ReseatchItemProps {
    id: any;
    type: any;
    item: ResearchItem;
}

const ResearchItemPage = observer((props: ReseatchItemProps) => {
    var item: ResearchItem = props.item;
    return (
        <div key={props.id} className="researchItem" onClick={() => {
            let isLogin = LocalStorageService.get("_userInfo");
            if (isLogin) {
                GlobalConfig.history.push(`/main/researchDetail/${props.type}/${item.id}`);
            } else {
                loginStore.LoginModel = LoginModel.login;
                userStore.loginModalVisible = true;
            }
        }}>
            <div className="content">
                <div className="top">
                    <img className="thumb" src={item.thumb} alt="" />
                    <div className="info">
                        <div className="title">{(mainStore.isEn ? item.entitle : item.title) || item.title}</div>
                        <p className="abstract">{(mainStore.isEn ? item.encontent : item.content) || item.content}</p>
                        <div className="views">
                            <img className='collect' src={item.isfollow === 1 ? Icon_collect_on : Icon_collect} alt="" />
                            <img className='view' src={Icon_view} alt="" />
                            <div className="viewCount">{item.viewcount}</div>
                            <div className="time">
                                <span>{item.createtime.slice(5, 10).replace("-", '/')}</span> <span>{item.createtime.slice(11, 16)}</span>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    );
})

export default ResearchItemPage;

