import { observer } from 'mobx-react';
import StatisticStore from '../../../stores/StatisticStore';
import { useEffect } from 'react';
import { StockFollowItem } from '../../../services/StockFollowService';
import { Loading } from '../../../components/Loading';
import NoData from '../../../components/NoData';
import './WatchList.scss';
import { Loading_icon } from '../../../images/Loading';
import { StockLogo } from '../../../components/StockLogo';
import { indexStore } from '../../../stores/IndexStore';
import GlobalConfig from '../../../config/GlobalConfig';
import Translate from '../../../components/Text';

export const WatchListPage = observer(() => {
    useEffect(() => {
        StatisticStore.getFollowed();
    }, []);
    return <div className="watchListWrapper">
        {StatisticStore.getfollowLoading ? <div className="loading">
            <img src={Loading_icon} alt="" />
        </div> : StatisticStore.followedItems.length == 0 ?
            <div style={{ height: '80px', margin: "0 auto" }}>
                <NoData />
            </div>
            :
            <div className="list">
                {StatisticStore.followedItems.map((f: StockFollowItem, i) => {
                    return <div className="info" key={i} onClick={() => {
                        window.scrollTo(0, 0);
                        indexStore.link = `/statistic/${f.symbol}`;
                        StatisticStore.symbol = f.symbol;
                        GlobalConfig.history.push(`/main/stock/statistic/${f.symbol}`);
                    }}>
                        <StockLogo symbol={f.symbol} />
                        <div className="followInfo">
                            <div className="titleInfo">
                                <div className="title">{f.symbol.replaceAll('_','  ')}</div>
                                <div className="subtitle">{f.symbol_name}</div>
                            </div>
                            <div className="change">{f.daily_change}</div>
                            <div className="data" style={{ color: f.daily_return < 0 ? '#EC4D3D' : '#4CB94D' }}>{(f.daily_return * 100).toFixed(2)}%</div>
                        </div>
                    </div>
                })}
            </div>
        }
    </div>

});