import { Modal, ModalProps, Spin, Upload } from "antd";
import { FC, useEffect, useRef, useState } from "react";
// import AvatarEditor from 'react-avatar-editor'
import Cropper from 'react-easy-crop'
import { showError } from "./Message";

import './ImageUploader.scss';
import { useRequest } from "../utils/hooks";
import { settingStore } from "../stores/SettingStore";
import { mainStore } from "../stores/MainStore";
import { translate } from "./Text";

export interface ImageUploaderProps {
  width: number;
  height: number;
  value?: string;
  className?: string;
  displayScale?: number;
  displayContent?: any;
  disable?:boolean;
  onChange?: (url: string) => void;
}

export const ImageUploader: FC<ImageUploaderProps> = ({ children, className, width, height, displayScale = 1, displayContent,  value = '', onChange = () => {},disable }) => {

  const { loadWithPromise: upload, loading: uploading } = useRequest((finalFile) => new Promise<string>((resolve, reject) => {
    settingStore.OssUpload(finalFile, (url: string | null) => url ? resolve(url) : reject('上传失败'));
  }), { autoLoad: false });

  const { inputImageFile, tmpImageInfo, overCutImageFile, submitCutImageFile } = useImageEditorState(width, height, (err) => {
    showError(err.message);
  });

  const beforeUpload = (file: File) => {
    inputImageFile(file).then(upload).then(onChange);
    return false;
  }

  const styles = {
    width: width * displayScale,
    height: height * displayScale
  };
  return (
    <>
      <Spin spinning={uploading}>
        <Upload multiple={false}
          accept=".png,.jpg,.jpeg,.svg"
          className={className}
          itemRender={() => <div />}
          disabled={disable}
          beforeUpload={beforeUpload}
        >
          {children ?? (value && value !='0' ? (
            <img className="image-uploader-preview" style={styles} src={value} /> 
          ) : (
            <div className="image-uploader-placeholder" style={styles}>{displayContent ?? (mainStore.isEn?'Upload Thumbnail': '请上传图片')}</div>
          ))}
        </Upload>
      </Spin>
      {tmpImageInfo && (
        <ImageEditorModal
          width={tmpImageInfo.width}
          height={tmpImageInfo.width * (height/width)}
          imageInfo={tmpImageInfo}
          onCancel={overCutImageFile}
          onSubmit={submitCutImageFile}
        />
      )}
    </>
  );
}

interface ImageInfo {
  originFile: File;
  image: HTMLImageElement;
  dataUrl: string;
  width: number;
  height: number;
}

function useImageEditorState(imgMinWidth: number, imgMinHeight: number, onError: (err: Error) => void) {
  const [tmpImageInfo, setTmpImageInfo] = useState<ImageInfo>();
  const [ctrl, setCtrl] = useState<{ resolve: Function, reject: Function }>();
  
  const inputImageFile = (file: File): Promise<File> => {
    return new Promise((resolve, reject) => {
      const dataUrl = URL.createObjectURL(file);
      const img = new Image();
      img.src = dataUrl;
      img.onload = () => {
        // if (img.width < imgMinWidth || img.height < imgMinHeight) {
        //   const err = new Error(`图片尺寸最小为 ${imgMinWidth} X ${imgMinHeight}`);
        //   onError(err);
        //   reject(err);
        //   return;
        // }
        setTmpImageInfo({ originFile: file, image: img, dataUrl, width: img.width, height: img.height });
        setCtrl({ resolve, reject });
      };
    });
  }

  const clear = () => {
    setCtrl(undefined);
    setTmpImageInfo(undefined);
  }

  const submitCutImageFile = (file: File) => {
    ctrl?.resolve(file);
    clear();
  }

  const overCutImageFile = () => {
    ctrl?.reject(new Error('停止编辑图片'));
    clear();
  }
  return {
    inputImageFile,
    submitCutImageFile,
    overCutImageFile,
    tmpImageInfo
  };
}

interface ImageEditorModalProps extends ModalProps {
  imageInfo: ImageInfo;
  width: number;
  height: number;
  onSubmit: (file: File) => void;
}

function ImageEditorModal({ imageInfo, width, height, onSubmit, ...rest }: ImageEditorModalProps) {
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [v, setV] = useState(false);
  const [config, setConfig] = useState<any>({});
  const { loadWithPromise: submit, loading } = useRequest(cutImage, { autoLoad: false });

  const curCropAreaRef = useRef<any>({});

  const onCropComplete = (_: any, croppedAreaPixels: any) => {
    curCropAreaRef.current = croppedAreaPixels;
  };

  const doSubmit = () => {
    const { x, y, width: swidth, height: sheight } = curCropAreaRef.current;
    // debugger
    submit({
      image: imageInfo.image,
      sx: x, sy: y,
      swidth, sheight,
      width, height,
      type: 'image/png'
    }).then((data) => {
      const fileName = imageInfo.originFile.name;
      const isPng = /\.png$/.test(fileName);
      if (data) {
        onSubmit(new File([data], isPng ? fileName : `${fileName}.png`));
      }
    });
  };

  useEffect(() => {
    const [cropWidth, cropHeight] = frameSize(width, height, 400);
    const aspect = cropWidth / cropHeight;
    const editorWidth = cropWidth * 1.5;
    const editorHeight = cropHeight * 1.5;
    const ba = Math.max(imageInfo.width / editorWidth, imageInfo.height / editorHeight);
    const ta = Math.min(imageInfo.width / cropWidth, imageInfo.height / cropHeight);

    setConfig({
      cropSize: {
        width: cropWidth,
        height: cropHeight
      },
      minZoom: ba / ta,
      initArea: {
        x: 0, y: 0,
        width: imageInfo.width,
        height: imageInfo.height
      },
      aspect,
      editorWidth,
      editorHeight
    });
    setTimeout(() => setV(true), 500);
  }, []);

  return (
    <Modal {...rest} visible={true}
      title={translate('common.h')} width={config.editorWidth + 48}
      cancelText={translate('common.g')}
      okText={translate('common.f')}
      okButtonProps={{
        loading,
        onClick: doSubmit
      }}
    >
      <div className="image-uploader-editor" style={{ height: config.editorHeight, width: config.editorWidth }}>
        {v && <Cropper
          image={imageInfo?.dataUrl ?? ''}
          crop={crop}
          zoom={zoom}
          aspect={config.aspect}
          cropSize={config.cropSize}
          objectFit="contain"
          showGrid={false}
          minZoom={config.minZoom}
          onCropChange={setCrop}
          onCropComplete={onCropComplete}
          onZoomChange={setZoom}
          initialCroppedAreaPixels={config.initArea}
        />}
      </div>
    </Modal>
  );
}

/**
 * 框定尺寸范围
 * @param width 
 * @param height 
 * @param min 
 * @param max 
 */
function frameSize(width: number, height: number, max: number) {
  const h = height > width;
  const l = Math.min(max, h ? height : width);
  const a = l / (h ? height : width);
  return [
    h ? width * a : l,
    h ? l : height * a,
    a
  ];
}

function cutImage(cfg: { image: HTMLImageElement, sx: number, sy: number, swidth: number, sheight: number, width: number, height: number, type: string }) {
  return new Promise<Blob | null>((resolve, reject) => {
    const canvas = document.createElement('canvas');
    // document.body.appendChild(canvas);
    canvas.width = cfg.width;
    canvas.height = cfg.height;
    const cx = canvas.getContext('2d');
    if (cx) {
      cx.drawImage(cfg.image, cfg.sx, cfg.sy, cfg.swidth, cfg.sheight, 0, 0, cfg.width, cfg.height);
      canvas.toBlob(resolve, cfg.type, 1);
    } else {
      reject(new Error(''));
    }
  });
}

