import ContentLoader from "react-content-loader"
import { SkelletonLoader } from ".";
import './Skelleton.scss';

export const VideoPlayLoader = (props: any) => (
    <div className="videoPlayLoader" >
        <div className="leftContent">
            <SkelletonLoader className="videoBanner" >
                <rect />
            </SkelletonLoader>
            <ContentLoader
                className="videoInfo"
                speed={2}
                backgroundColor="#f3f3f3"
                foregroundColor="#ecebeb"
                {...props}
            >
                <rect className="title" />
                <rect className="watch" />
            </ContentLoader>
            <SkelletonLoader className="videoComment" >
                <rect className="count" />
                <circle className="avatar" />
                <rect className="comment" />
            </SkelletonLoader>
        </div>
        <div className="rightContent">
            <div className="list">
                {[1,2,3,4,5,6,7].map((i) => {
                    return <SkelletonLoader key={i} className="videoItem" >
                        <rect className="cover" />
                        <rect className="title" />
                        <rect className="title2" />
                        <rect className="watch" />
                        <rect className="date" />
                    </SkelletonLoader>;
                })
                }
            </div>
        </div>

    </div>

)

export const VideoContentLoader = (props: any) => (
    <ContentLoader
        className="video"
        speed={2}
        // viewBox="0 0 400 460"
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
    >
        <rect className="cover" x="0" y="0" rx="2" ry="2" width="400" height="400" />
        <circle className="avatar" cx="31" cy="151" r="15" />
        <rect className="title" x="58" y="188" rx="2" ry="2" width="140" height="10" />
        <rect className="name" x="58" y="154" rx="2" ry="2" width="140" height="10" />
    </ContentLoader>
)


